.subject-card-wrapper {
	&:focus {
		outline: none;
		// box-shadow: 0 0 0 2px $inputFocusBorder;
		border-radius: 4px;
	}
	.ember-popover {
		position: fixed !important;
	}

	.ember-popover-arrow {
		display: none;
	}

	.screen-reader-only {
		position: sticky;
	}

	.subject-card-content {
		width: 470px;
		min-height: 220px;
		border: none;
		z-index: 1001;
		padding: 0;
		box-shadow: 0px 2px 4px rgba(18, 52, 77, 0.06), 0px 2px 16px rgba(18, 52, 77, 0.16);
		border-radius: 4px;

		.subject-card-info {
			display: flex;
			padding: 18px 16px 12px;
			align-items: center;

			.subject-name-time-info {
				padding-left: 8px;

				.subject-time-info {
					color: $toggleSlider;
					font-weight: normal;
					margin: 0 !important;
				}

				p.subject-time-info {
					font-size: 12px;
				}
			}

			.requester-name {
				max-width: 380px;
				display: inline-flex;
			}
		}

		.loader-container {
			position: relative;
			left: 210px;
			svg {
				fill: $app-primary;
			}
		}

		.subject-card-description {
			margin: 0px 16px 16px 56px;
			height: 100px;
			// make subject bold by default
			> div.lt-line-clamp {
				font-weight: $text-weight-semi-bold;
			}
			.description-text {
				font-weight: $text-weight-normal;
				.nodesc {
					color: $disabled-text-color;
				}
			}
		}

		.quick-reply-note {
			width: 470px;
			height: 48px;
			border-top: 1px solid $border-light-color;
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
			background: $background-color-2;
			display: flex;
			align-items: center;
			justify-content: flex-end;

			svg {
				vertical-align: middle;
				path {
					fill: $primary-text-color-1;
				}
			}

			.pull-right {
				margin-top: 8px;
				float: right;
				background: $white;
				border-color: $border-light-color;
			}
		}
	}
}
