.list-wrap {

	.lt-body-wrap {

		div.lt-head {
			background: $section-background-blue;
			box-shadow: 0px 2px 3px $list-item-shadow;
			border: 1px solid $border-light-blue;
			border-radius: 4px;
			tr {
				th {
					padding: 10px 16px;
					font-weight: $text-weight-semi-bold;
					font-size: $title-small;
				}
			}
		}

		.lt-no-data {
			box-shadow: none;
			background: url(/a/assets/images/empty-states/no-search-results.svg) no-repeat center;
			td {
				border: none;
				border-left: none !important;
				border-right: none !important;
				padding-top: 250px;
			}
		}

		& > table {
			border-collapse: separate;
			border-spacing: 0 8px;

			tr:not(.lt-no-data) {
				box-shadow: 0px 1px 6px $list-item-shadow;
				vertical-align: top;
				background: $list-item-gradient;

				&:hover {
					background: $white;
				}

				td {
					padding: 16px;
					font-size: $title-small;
					border-top: 1px solid $border-light-color;
					border-bottom: 1px solid $border-light-color;

					&:first-child {
						border-left: 1px solid $border-light-color;
						border-top-left-radius: 4px;
						border-bottom-left-radius: 4px;
					}

					&:last-child {
						border-right: 1px solid $border-light-color;
						border-top-right-radius: 4px;
						border-bottom-right-radius: 4px;
					}
				}
			}
		}
	}

}
.page-sidebar{
	.ui-list-filter{
		.filter-row:nth-child(4){
			.title-small{
				text-transform: capitalize;
			}
		}
	}
}
