.confirmation-side-modal-overlay {
	position: absolute;
	background-color: $app-light-bg;
	width: 100%;
	height: 100%;
	z-index: 10;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;

	.confirmation-side-modal-content {
		width: 512px;
		min-height: 150px;
		margin: auto;
		margin-top: 30%;
		border-radius: 4px;
		padding: 32px;
		line-height: 1.5;
		border: 1px solid $border-dark-color;
	}

	.confirmation-side-modal-title {
		font-size: $text-large;
		font-weight: $text-weight-semi-bold;
		margin-bottom: 16px;
	}

	.confirmation-side-modal-actions {
		text-align: right;
		margin-top: 40px;
	}
}
