.ember-pill {
	background-color: $border-light-color;
	border: none;
	align-items: center;
	border-radius: 4px;
	word-break: break-word;
}

.time-picker-wrapper .ember-power-select-search-input {
	width: 65%;
}

.ember-basic-dropdown {
	position: relative;

	input:not([type='checkbox']):not([type='radio']) {
		&:focus-visible {
			border-color: $linkColor;
			box-shadow: 0 0 0 1px $inputFocusBorder;
		}
	}

	.ember-power-select-trigger-no-border {
		border: none !important;
	}

	.ember-power-select-trigger {
		display: flex;
		border: 1px solid $border-dark-color;
		padding-right: 0;
		min-height: 32px;
		min-width: 70px;
		line-height: 2;
		border-radius: 4px !important;
		font-weight: 500;
		position: relative;
		background-color: $app-white;
		overflow-x: hidden;
		text-overflow: ellipsis;
		user-select: none;
		-webkit-user-select: none;
		color: inherit;
		cursor: pointer;
		box-sizing: border-box;

		&:after {
			display: none;
		}

		&:hover {
			border: 1px solid $inputHoverBorder;
		}

		&--active {
			border: 1px solid $border-dark-color !important;
		}

		&:focus {
			border: 1px solid $inputFocusBorder !important;
			box-shadow: 0 0 0 1px $inputFocusBorder;
		}

		&[aria-expanded=true] {
			border: 1px solid $inputFocusBorder !important;
			box-shadow: 0 0 0 1px $inputFocusBorder;
		}

		&.ember-basic-dropdown-trigger--below{
			border: 1px solid $inputFocusBorder !important;
			box-shadow: 0 0 0 1px $inputFocusBorder;

			.ember-power-select-status-icon {
				transform: rotate(225deg);
				-webkit-transform: rotate(225deg);
			}
		}

		&[aria-disabled=true] {
			cursor: not-allowed;
			background-color: $inputDisabledBackground;
		}

		.ember-power-select-selected-item {
			color: $app-icon-color;
			overflow-x: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			display: inline-block;
			margin-left: 8px;
			&.has-clear {
				width: calc(100% - 45px);
			}
		}

		.ember-power-select-trigger-multiple-input {
			padding: 0;
			padding-left: 8px;
		}

		.ember-power-select-placeholder {
			font-weight: 500;
			color: rgba(18, 52, 77, 0.7); // $primary-text-color-1 at 70% alpha
			margin-left: 8px;
			display: block;
			overflow-x: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
		}

		.ember-power-select-status-icon {
			position: absolute !important;
			transform: rotate(45deg);
			-webkit-transform: rotate(45deg);
			border: solid $dropdown-icon-color;
			border-width: 0 2px 2px 0;
			padding: 2px;
			display: inline-block;
			width: 0;
			height: 0;
			top: 0;
			right: 8px;
		}

		&-multiple-input {
			padding-left: 8px;
			box-shadow: none !important;
			border: none !important;
			font-weight: $text-weight-normal !important;

			&[type="search"] {
				&::-ms-clear {  display: none; width : 0; height: 0; }
				&::-ms-reveal {  display: none; width : 0; height: 0; }
			}
		}

		&.ember-power-select-multiple-trigger{
			display: block;
			height: initial;
		}

		.ember-power-select-multiple-option{
			display: inline-flex;
			margin: 2px 4px;
			max-width: 90%;
			@extend .ember-pill;
			padding: 2px 0 2px 4px;
			color: $primary-text-color-1;

			.ember-power-select-multiple-remove-btn{
				margin: 2px 2px 0 4px;
				font-size: 18px;
				color: $app-form-label-color;
				font-weight: $text-weight-medium;
				width: 20px;
				height: 20px;
				text-align: center;
				background: none;
				border: none;
				padding: 0;
			}
		}
	}

	input[type="search"].ember-power-select-search-input-trigger {
		border: 0;
		box-shadow: none !important;
		height: 30px;
		padding: 4px 0 7px 8px;
		margin-right: 16px;
		font-family: inherit;
		z-index: 10;
		&::-webkit-search-cancel-button, &::-webkit-search-decoration {
			-webkit-appearance: none;
		}
	}

	.ember-power-select-clear-btn {
		background: none;
		border: none;
		top: 8px;
		margin: 0;
		padding: 0;
		height: 18px;
		width: 18px;
		position: absolute;
		right: 24px;
	}

	.ember-basic-dropdown-content {
		margin-top: 5px;
		position: absolute;
		box-sizing: border-box;
		border: none;
		border-radius: 4px;
		background-color: $app-white;
		box-shadow: 0 2px 4px 0 $dropdown-shadow-color;
		z-index: 9;

		&.ember-basic-dropdown-content--above {
			margin-top: -4px;
		}

		@extend .drop-down-fade-below;

		.ember-power-select-search {
			padding: 8px 12px 4px 12px;

			.search-container {
				position: absolute;
				top: 15px;
				left: 20px;
				svg {
					fill: $inputHoverBorder;
					height: 14px;
					width: 14px;
				}
			}

			.ember-power-select-search-input {
				width: 100%;
				padding-left: 24px;
				box-sizing: border-box;
				-webkit-appearance: none;
				height: 33px;
				border: 1px solid $app-form-label-color;
				border-radius: 4px;
				background-color: $app-white;
				box-shadow: inset 0 1px 2px 0 $dropdown-search-shadow-color;
			}
		}

		.ember-power-select-group-name{
			display: block;
			padding-left: 15px;
			margin-top: 5px;
		}

		.ember-power-select-options {
			padding: 2px 0 4px 0;
			list-style: none;
			margin: 0;
			user-select: none;
			-webkit-user-select: none;

			&[role="listbox"] {
				overflow-y: auto;
				-webkit-overflow-scrolling: touch;
				max-height: 232px;
			}
		}

		.ember-power-select-multiple-options {
			width: 90%;
		}

		.ember-power-select-option {
			border-radius: 4px;
			margin: 4px 12px;
			position: relative;
			font-weight: $text-weight-medium;
			cursor: pointer;
			padding: 6px 8px;
			box-sizing: border-box;
			word-break: break-word;

			// location dropdown option level
			&.level {
				&-1 {
					padding-left: 15px;
				}
				&-2 {
					padding-left: 30px;
				}
				&-3 {
					padding-left: 45px;
				}
				&-4 {
					padding-left: 60px;
				}
			}

			&[aria-current=true] {
				background-color: $dropdown-hover;
				color: $primary-text-color-1;
			}

			&[aria-selected=true] {
				font-weight: $text-weight-semi-bold;
				background-color: $dropdown-option-selected;
				padding-right: 24px;

				&::before {
					position: absolute;
					right: 10px;

					//tick icon - copied from freshdesk
					content: url("data:image/svg+xml,%3C%3Fxml version='1.0' encoding='UTF-8'%3F%3E%3Csvg width='12px' height='9px' viewBox='0 0 12 9' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3C!-- Generator: Sketch 52.4 (67378) - http://www.bohemiancoding.com/sketch --%3E%3Ctitle%3ECheck%3C/title%3E%3Cdesc%3ECreated with Sketch.%3C/desc%3E%3Cdefs%3E%3Cpath d='M4.17095654,7.28764233 C3.74936256,6.89579049 3.08481084,6.91447629 2.68663874,7.32937825 C2.28846663,7.7442802 2.30745383,8.39828338 2.72904782,8.79013522 L5.87904631,11.7179111 C6.3006403,12.109763 6.96519201,12.0910772 7.36336412,11.6761752 L13.3133613,5.47617918 C13.7115334,5.06127722 13.6925462,4.40727405 13.2709522,4.0154222 C12.8493582,3.62357036 12.1848065,3.64225616 11.7866344,4.05715812 L6.55759159,9.50590771 L4.17095654,7.28764233 Z' id='path-1'%3E%3C/path%3E%3C/defs%3E%3Cg id='TD-with-Freddy-4-Email' stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg id='ticket-detail--open-copy-2' transform='translate(-1054.000000, -997.000000)'%3E%3Cg id='Group-92' transform='translate(880.000000, 828.000000)'%3E%3Cg id='Group-26'%3E%3Cg id='Group-7' transform='translate(0.000000, 54.000000)'%3E%3Cg id='Group-2' transform='translate(16.000000, 104.000000)'%3E%3Cg id='Icon/new/Check' transform='translate(156.000000, 8.000000)'%3E%3Cmask id='mask-2' fill='white'%3E%3Cuse xlink:href='%23path-1'%3E%3C/use%3E%3C/mask%3E%3Cuse id='save' fill='%23000000' fill-rule='nonzero' xlink:href='%23path-1'%3E%3C/use%3E%3Cg id='Color/Blue-(Hype)' mask='url(%23mask-2)' fill='%232C5CC5' fill-rule='evenodd'%3E%3Crect id='Color---Jade' x='0' y='0' width='15.8024691' height='15.8024691'%3E%3C/rect%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/g%3E%3C/svg%3E");
				}
			}
		}
	}
}
