.ui-pills-wrapper {
	@include flexbox-simple(flex-start, center);
	flex-wrap: wrap;

	.ui-pills {
		@include inline-flex-center;
		border-radius: 4px;
		min-height: 20px;
		padding: 0 8px;
		margin: 0 8px 8px 0;
		font-weight: $text-weight-normal;
		background: $border-light-color;
		font-size: $text-normal;
		color: $primary-text-color-1;
		cursor: default;

		.pill-ellipsis {
			@extend .ellipsis;
			display: block;
			max-width: 65px;
		}

		&.more-pills {
			cursor: pointer;
			margin-right: 0;
		}
	}

	.hover-pills-wrapper {
		position: relative;
		display: inline-block;

		.hover-card {
			z-index: 1;
			top: 8px;
			left: -10px;
			white-space: nowrap;
			border-radius: 4px;
			position: absolute;
			color: $app-white;
			padding: 4px 12px;
			font-size: $text-normal;
			background: $app-grey;
			font-weight: $text-weight-medium;
			box-shadow: 0px 2px 10px $box-shadow-color-1;

			.remaining-pills {
				word-break: keep-all;
			}
		}
	}
}
