.office-calendar-modal {
	
	.meeting-time-field {
		flex-direction: column;
		align-items: flex-start;

		.time-label-field {
			padding-bottom: 4px;
			color: $inputHoverBorder;
			font-size: $text-normal;
			font-weight: $text-weight-semi-bold;
			line-height: 20px;
			word-break: break-word;
		}

		.time-picker-wrapper {
			margin-left: 0;
		}

		&:last-child {
			margin-left: 20px;
		}
	}

	.loader-container {
		margin-top: 150px;
		svg { fill: $inputFocusBorder; }
	}

	.best-times {
		margin-top: 25px;

		&-label {
			font-weight: $text-weight-semi-bold;
			font-size: $baseFontSize;
			margin-bottom: 15px;
		}

		&-box {
			flex-wrap: wrap;
		}

		&-box-item {
			width: 160px;
			background: $disabled-button-color;
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
			margin-bottom: 8px;
			font-family: inherit;

			&.selected {
				background: $section-background-blue !important;
				border-color: $inputFocusBorder !important;
			}

			&:hover {
				background: $border-light-color;
			}
		}
		
		.view-link {
			position: relative;
			top: 15px;
			right: 40px;
			float: right;
			font-size: $title-normal;
		}

		.empty-state {
			margin-top: 50px;

			#no-times-available {
				width: 400px;
				font-size: $title-normal;
				text-align: center;
				margin-top: 20px;
				color: $app-form-label-color;
			}
		}
	}
}