.app-dropdown-group {
	vertical-align: inherit;
	display: inline-block;

	&.open {
		border-color: $app-primary;

		.app-icon-btn--text {
			color: $app-icon-fillcolor-active;
		}

		.app-icon-btn .app-icon {
			fill: $app-icon-fillcolor-active;
		}

		.app-icon-btn {
			border-color: $app-primary;
			background-color: $app-primary;
		}

		.count {
			color: #fff;
		}
	}
}

.classic-active {
	.app-icon-btn {
		padding-bottom: 5px;
		margin-left: 5px;
	}

	&.open {
		.app-icon-btn {
			border: none;
			background-color: $app-secondary-btn-hover;
			box-shadow: none;
		}
	}
}

.app-dropdown {
	padding: 10px;
	max-width: 480px;
	border-radius: 4px;
	z-index: 1000;
	min-width: 160px;
	list-style: none;
	text-align: left;

	@extend .default-box-shadow;

	&__item {
		border-radius: 4px;
		color: $app-primary-black;
		position: relative;

		@extend .ellipsis;

		.selectedtick {
			right: 5px;
			top: 5px;
			fill: $app-primary;
			position: absolute;
		}

		&.disabled {
			color: $muted-secondary;
			text-decoration: none;
			cursor: not-allowed;
			text-shadow: none;

			&:hover,
			&:focus {
				color: $muted-secondary;
				text-decoration: none;
				background: none;
			}
		}

		a {
			display: block;
			clear: both;
			padding: 5px 10	px;
			line-height: 1.42857;
			color: $app-primary-black;
			white-space: nowrap;

			&:hover {
				text-decoration: none;
				background-color: $dropdown-hover;
			}
		}
	}
}

.app-dropdown-plain {
	padding: 20px;
	min-width: 200px;
}
