.fs-alert {
	border-radius: $radius;
	@include flexbox;
	margin: 12px 0 24px;
	color: $btnPrimaryActiveBackground;
	position: relative;
	
	&.alert-warning {
		background: $app-casablanca-light;
		border: 1px solid $alertPartialBorder;
	}
	
	&.alert-error {
		background: $alertWarningBackground;
		border: 1px solid $alertWarningBorder;
	}
	
	&.alert-success {
		background: $alertSuccessBackground;
		border: 1px solid $alertSuccessBorder;
	}
	
	&.alert-info {
		background: $alertInfoBackground;
		border: 1px solid $alertInfoBorder;
	}

	.alert-icon {
		margin-top: 8px;
		margin-bottom: 8px;
		height: 36px;
		
		svg {
			margin: 8px 12px 0 12px;
		}
		
		&.icon-separator-warning {
			border-right: 1px solid $alertPartialBorder;
		}
		
		&.icon-separator-error {
			border-right: 1px solid $alertWarningBorder;
		}
		
		&.icon-separator-success {
			border-right: 1px solid $alertSuccessBorder;
		}
		
		&.icon-separator-info {
			border-right: 1px solid $alertInfoBorder;
		}
	}
	.content {
		padding: 8px 16px;
	}
		
	.btn-close{
		cursor: pointer;
		position: absolute;
		right: -8px;
		top: 2px;
		svg {
			&.close-icon { 
				width: 24px; 
				height: 24px; 
				vertical-align: middle; 
			}
			path {
				fill: $app-black;
			}
		}
	}
}



