/* this files should be in sync with
*  public/src/ember/base/lib/_module-view-list.scss
*/

.view-create-modal, .view-update-modal {
	.dependent-select-radio-field {
		margin-bottom: 8px;
		.radio-field {
			display: flex;
			width: 100%;
			margin-bottom: 8px !important;
		}
	}
}

.view-actions-dropdown {
	margin-left: 8px;
	.ember-basic-dropdown-content {
		margin-top: 20px;
		/*right: 0;*/
		.basic-dropdown-item {
			display: flex;
			align-items: center;
			.ember-svg {
				margin-right: 8px;
			}
			.btn-no-bg {
				display: flex;
				align-items: center;
				width: 100%;
			}
		}
	}
}

.view-wrapper {
	display: inline-flex;
	align-items: center;
	color: $primary-text-color-1;

	.view-module {
		padding-right: 8px;
		font-weight: $text-weight-semi-bold;

		&:after {
			content: "-";
			padding-left: 8px;
		}
	}

	.view-selected {
		font-weight: $text-weight-medium;
		max-width: 300px;
		@extend .ellipsis;
	}

	.view-new {
		background-color: $app-casablanca-light;
		border: 1px solid $app-warning-state;
		padding: 6px;
		border-right: 0;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
		max-width: 300px;
		font-style: italic;
		font-weight: $text-weight-medium;
		box-sizing: border-box;
		height: 33px;
		@extend .ellipsis;
		:before {
			content: "*";
			padding-right: 8px;
		}
	}
}

.view-action-items {
	display: flex;
	align-items: center;
	margin-left: -2px;

	& > div {
		display: flex;
		align-items: center;
	}

	button {
		margin-right: 0;
	}

	.view-modified-action-items {
		background-color: $app-casablanca-light;
		border: 1px solid $app-warning-state;
		border-left: none;
		border-top-right-radius: 4px;
		border-bottom-right-radius: 4px;
		margin-left: -6px;
		max-height: 33px;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		svg path { fill: $primary-text-color-1; }
	}
}

.module-view-list {
	margin-left: 8px;
	#view-unstarred, #view-starred {
		margin-bottom: 12px;
		margin-left: 8px;
		font-weight: $text-weight-bold;
		text-transform: uppercase;
		font-size: $text-normal;
		display: inline-block;
		margin-top: 20px;
	}

	.view-starred, .view-unstarred {
		position: relative;
		padding-bottom: 10px;
	}

	.view-unstarred li:last-child{
		margin-bottom: 100px !important;
	}

	.view-starred {
		&:after {
			content: "";
			position: absolute;
			width: calc(100% + 24px);
			left: -12px;
			border-bottom: 1px solid $border-light-color;
			bottom: 0;
		}
	}

	.search-views {
		position: relative;

		input {
			width: 100%;
			padding-left: 32px !important;
		}

		svg {
			position: absolute;
			top: 8px;
			left: 12px;
			fill: $disabled-text-color;
		}
	}

	.no-views {
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 24px;
	}

	.view-item {
		display: flex;
		align-items: center;
		min-height: 32px;
		padding: 0 8px;
		cursor: pointer;
		margin-bottom: 8px;
		border-radius: 4px;
		transition: 0.3s ease;

		.star-view {
			opacity: 0;
			height: 18px;
			min-width: 18px;
			margin: 0;
			svg { fill: $primary-text-color-1; }
		}

		.starred-view {
			height: 18px;
			min-width: 18px;
			margin: 0;
		}

		a {
			color: $primary-text-color-1;
			max-width: 220px;
			width: 100%;
			padding: 6px 0;
			margin-right: auto;

			&:hover { color: $linkColor; }
			&.active {
				color: $linkColor;
				font-weight: $text-weight-medium;
			}
		}

		a.hidden-view + svg.view-active { left: 0 !important; }

		&:hover {
			background: $background-color-2;
			.star-view { opacity: 1 !important; }
			&.active .view-active { display: none !important; }
		}

		&:focus, &:focus-within {
			.star-view { opacity: 1 !important; }
		}

		&.active {
			background: $list-item-active;

			svg { fill: $linkColor; }

			.starred-view { opacity: 0; }

			.view-active {
				position: relative;
				left: 16px;
			}

			&:hover, &:focus, &:focus-within {
				.view-active { display: none; }
				.starred-view { opacity: 1 !important; }
			}
		}
	}

	.custom-view + .default-view {
		position: relative;
		margin-top: 30px;
		&:before {
			content: "";
			position: absolute;
			top: -20px;
			border-top: 1px solid $border-light-color;
			width: calc(100% + 24px);
			left: -12px;
		}
	}
}
