.audio-wrapper {
	display: inline-flex;
	align-items: center;
	border: 1px solid $border-light-blue;
	box-sizing: border-box;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
	border-radius: 8px;
	padding: 14px 16px;
	margin: 16px 0;
	background: $white;

	&.disabled-audio {
		cursor: not-allowed;
		color: $btnDisabledColor;
		.audio-icon svg path{
			fill: $disabled-text-color;
		}
		.audio-button{
			cursor: inherit;
		}
	}

	.audio-icon {
		margin-right: 13px;
		.audio-button{
			display: flex;
		}
	}

	.play-audio{
		font-weight: $text-weight-semi-bold;
		margin-right: 20px;
	}

	.phone-remaining-time {
		padding-left: 10px;
		margin-top: -2px;
	}

	.audio-playing{
		display: flex;
		align-items: center;
		div{
			font-weight: $text-weight-medium;
			font-size: $text-normal;
			width: 75px;
		}
	}
	
	//CSS to handle input range slider for cross browsers
	@mixin track($fill: 0) {
		box-sizing: border-box;
		border-radius: $sliderRadius;
		width: $sliderTrack-w;
		height: $sliderTrack-h;
		background: $sliderTrack;
		
		@if $fill == 1 {
			& {
				background: linear-gradient($sliderTrackFill, $sliderTrackFill)
					0/ var(--sx) 100% no-repeat $sliderTrack
			}
		}
	}
	
	@mixin fill() {
		height: $sliderTrack-h;
		background: $sliderTrackFill;
		border-top-left-radius: $sliderRadius;
		border-bottom-left-radius: $sliderRadius;
	}
	
	@mixin thumb() {
		box-sizing: border-box;
		border: none;
		width: $sliderThumb-d;
		height: $sliderThumb-d;
		border-radius: 50%;
		background: $app-primary;
		box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.19);
	}
	
	[type='range'] {
		&, &::-webkit-slider-thumb {
			-webkit-appearance: none;
		}
		
		--range: calc(var(--max) - var(--min));
		--ratio: calc((var(--val) - var(--min)) / var(--range));
		--sx: calc(.5 * #{$sliderThumb-d} + var(--ratio) * (100% - #{$sliderThumb-d}));
		margin-left: 14px;
		padding: 0;
		width: $sliderTrack-w; height: $sliderThumb-d;
		background: transparent;
		
		&::-webkit-slider-runnable-track {
			@include track(1)
		}
		&::-moz-range-track { @include track }
		&::-ms-track { @include track }
		
		&::-moz-range-progress { @include fill }
		&::-ms-fill-lower { @include fill }
		
		&::-webkit-slider-thumb {
			margin-top: .5*($sliderTrack-h - $sliderThumb-d);
			@include thumb
		}
		&::-moz-range-thumb { @include thumb }
		&::-ms-thumb {
			margin-top: 0;
			@include thumb
		}
		
		&::-ms-tooltip { display: none }
	}
}
