// Autorefresh notification styles
.ui-component-refresh-notification {
	position: fixed;
	margin-top: -36px;
	width: auto;
	height: auto;
	z-index: 999;
	text-align: center;
	left: 50%;
	transform: translateX(-50%);
	border: none;
	background: none;
	box-shadow: none;

	&:focus {
		border: none;
		outline: none;
		.refresh-notification {
			border: 1px solid $app-primary;
		}
	}

	.refresh-notification {
		box-shadow: 0px 3px 12px opacify($list-item-shadow, 0.06);  // -> rgba(0, 0, 0, 0.12);
		background-color: $app-white;
		padding: 6px;
		margin: 0 auto;
		display: inline-block;
		border-radius: 59px;
		animation: soft-drop-animate 0.2s ease-in;
		color: $primary-text-color-1;
		height: 40px;
		box-sizing: border-box;
		border: 1px solid $border-light-color;

		&:hover {
			border: 1px solid $app-primary;
		}

		.refresh-icon {
			float: left;
			position: relative;
			background: $app-primary;
			width: 28px;
			height: 28px;
			border-radius: 50%;
			margin-top: -1px;
			svg {
				width: 12px;
				height: 14px;
				margin-top: 7px;
			}
		}

		.notification-container {
			padding: 0 12px 0 12px;
			color: $primary-text-color-1;

			.notification-count {
				margin-right: 2px;
				font-weight: $text-weight-semi-bold;
			}

			.notification-label {
				margin-right: 8px;
				line-height: 1.8;
			}
		}
	}
}

.notification-with-close-wrapper {
	position: fixed;
	left: 50%;
	transform: translateX(-50%);
	z-index: 999;
}

.refresh-notification-with-close {
	display: flex;
	align-items: center;
	height: 40px;
	animation: soft-drop-animate 0.2s ease-in;
	border: 1px solid $border-light-color;
	background: $app-white;
	box-shadow: 0px 3px 12px opacify($list-item-shadow, 0.06);
	border-radius: 100px;
	padding: 0 6px;

	&:hover {
		border: 1px solid $linkColor;
		transition: 0.3s linear;
	}

	.refresh-icon {
		@include flexbox-simple(center, center);
		background: $app-primary;
		width: 28px;
		height: 28px;
		border-radius: 50%;
		margin-right: 8px;
		svg { fill: $app-white; }
	}

	.notification-container {
		color: $primary-text-color-1;
		.notification-count {
			font-weight: $text-weight-semi-bold;
		}
	}

	button {
		background: none;
		border: none;
		padding: 0;
		@include flexbox-simple(center, center);

		&:last-child {
			margin-right: 4px;
			margin-left: 16px;
		}

		&.refresh-notification-close {
			width: 24px;
			height: 24px;
			border-radius: 50%;
			svg {
				fill: $secondary-text-color-2;
			}
			&:hover {
				background-color: $background-color;
			}
		}
	}
}

.alerts_index, .module-tickets {
	.auto-update-icon  {
		background: $app-primary;
		border-radius: 50%;
		width: 12px;
		height: 12px;
		text-align: center;
		position: absolute;
		margin-left: -16px;
		margin-top: 4px;
		animation: 0.2s fadeIn linear;
		svg {
			fill: $white;
			width: 8px;
			height: 8px;
			margin-top: 2px;
			vertical-align: top;
		}
	}
}
