@import "shimmer-fast";

@keyframes shimmer {
	0%,
	100% {
		opacity: 1;
	}
	50% {
		opacity: 0.5;
	}
}

.shimmer-wrapper{
	height: fit-content;
}

.shimmer-loader {
	display: inline-block;
	position: relative;
	width: 100%;
	height: 100%;
	animation: shimmer 1.2s linear infinite;

	.shimmer-loader-card {
		margin: 12px;
		border-radius: 8px;
		display: flex;
		border: 1px solid $shimmer-background-color;
		height: 70px;
		box-sizing: border-box;
		padding: 12px 16px;
		.shimmer-30{
			margin-right: 45%;
		}
		&.split-card-with-logo {
			.card-img {
				margin: 0;
				border-radius: 4px;
			}
			.card-content {
				padding: 0;
				div {
					height: 12px;
				}
			}
			.shimmer-20 {
				height: 10px;
			}
		}
	}

	.shimmer-loader-item {
		margin: 16px;
		background: $shimmer-background-color;
		background-size: 1000px 100%;
		border-radius: 100px;
		height: 14px;
		max-width: 100%;

		&--large {
			height: 32px;
			border-radius: 4px;
		}
		&--small {
			height: 10px;
		}
		&--xlarge {
			height: 64px;
			border-radius: 4px;
		}
		&--xxlarge {
			height: 112px;
			border-radius: 4px;
		}
		&--no-margin {
			margin: 0;
		}
		&--no-ml {
			margin-left: 0;
		}
		&--no-mt {
			margin-top: 0;
		}
		&--mb {
			&-12 {margin-bottom: 12px;}
		}
	}

	.group {
		@include flexbox-simple(flex-start, center);
		margin: 16px;
		* {
			margin-right: 0;
			margin-top: 0;
			margin-bottom: 0
		}
		&--no-ml {
			margin-left: 0;
		}
		&--no-mr {
			margin-right: 0;
		}
		&--mt {
			&-27{
				margin-top: 27px;
			}
		}
		&--mb {
			&-27{
				margin-bottom: 27px;
			}
		}
		&--no-margin {
			margin: 0;
		}
		&-m16 { margin: 16px; }
		&-30 { width: 30%; }
		&-70 { width: 70%; }
		&-60 { width: 60%; }

		&--btns {
			justify-content: flex-end;
			margin-right: 32px;
		}

		&--body {
			height: 100vh;
			border-top: 1px solid $app-border-primary;
			.main-content {
				width: calc(100% - 415px);
				background: $white;
				height: 100%;
				margin: 32px 32px 0 16px;
				.card-content {
					background: $background-color-2;
				}
			}
			.sidebar-content {
				background: $background-color-2;
				width: 360px;
				height: 100%;
			}
			.quick-nav-content {
				width: 40px;
				height: 100%;
				.shimmer-btn {
					margin: 10px;
				}
			}
		}
	}

	// type = table
	.table-header {
		border-bottom: 1px solid $app-border-primary;
	}

	.table-header, .table-row {
		@include flexbox-simple(space-between, center);
		height: 50px;
	}
	
	.column-header {
		&:after {
			content: " ";
			background-color: #CFD7DF;
			height: 60px;
			width: 1px;
			margin-left: 25%;
		}
	}
	// ---

	// type = card
	.shimmer-border{
		border: 1px solid $border-light-color;
		box-sizing: border-box;
		border-radius: 8px;
	}

	.card-title{
		margin-left: 0;
	}

	.card-group{
		display: flex;
		.card-img{
			border-radius: 4px;
			margin: 18px 0 18px 18px;
		}
		.card-content{
			flex-grow: 2;
			padding: 0;
		}
	}

	.card-content{
		display: flex;
		flex-direction: column;
		justify-content: center;
		flex-grow: 2;
		padding: 18px 0;
		div{
			margin: 6px 18px;
			height: 10px;
		}
		.card-title{
			height: 14px;
			margin-bottom: 12px;
		}
	}

	.shimmer {
		&-heading {
			margin: 20px 15px;
		}
		&-title {
			margin-bottom: 30px;
		}
		&-content {
			height: 24px;
		}
		&-5 { width: 5%; }
		&-10 { width: 10%; }
		&-15 { width: 15%; }
		&-20 { width: 20%; }
		&-25 { width: 25%; }
		&-30 { width: 30%; }
		&-40 { width: 40%; }
		&-50 { width: 50%; }
		&-55 { width: 55%; }
		&-60 { width: 60%; }
		&-65 { width: 65%; }
		&-70 { width: 70%; }
		&-75 { width: 75%; }
		&-80 { width: 80%; }
		&-85 { width: 85%; }
		&-90 { width: 90%; }
		&-100, &-full { width: 100%; }
		&--mr {
			&-20{ margin-right: 20px; }
		}
		&--ml {
			&-20{ margin-top: 20px; }
		}
		&--mt {
			&-10{ margin-top: 10px; }
		}
		&--m16 { margin: 16px; }
		&-icon {
			background-color: $shimmer-background-color;
			border-radius: 50% !important;
			height: 32px;
			width: 32px;
			margin: 8px 0 8px 15px;
			&--small {
				width: 14px;
				height: 14px;
			}
			&--medium {
				width: 28px;
				height: 28px;
			}
			&--large {
				width: 32px;
				height: 32px;
			}
			&--xlarge {
				width: 48px;
				height: 48px;
			}
		}
		&-img {
			background-color: $shimmer-background-color;
			border-radius: $radius !important; // 4px
			margin-right: 0;
			&--small {
				width: 24px;
				height: 24px;
			}
			&--medium {
				width: 30px;
				height: 30px;
			}
			&--large {
				width: 44px;
				height: 44px;
			}
		}
		&-btn {
			border-radius: $radius !important; // 4px
			&--small {
				width: 32px;
				height: 32px;
			}
			&--medium {
				width: 65px;
				height: 32px;
			}
			&--large {
				width: 95px;
				height: 32px;
			}
		}

	}
}
