.badge-button-wrapper {
	position: relative;
	box-sizing: border-box;
	display: inline-block;
	margin-right: 10px;

	.ember-badge {
		padding-right: 28px;
		border: 1px solid transparent;
	}

	.badge-btn {
		border-radius: 4px;
		padding: 0px;
		height: 22px;
		line-height: 1;
		position: absolute;
		top: 0px;
		right: 0px;
	}

	.hidden-badge {
		padding-right: 8px;
	}
}
