.model-edit-form {

	.froala-editor-container {
		border-radius: 4px;

		.fr-toolbar {
			border-bottom: none;
			box-shadow: none;
			background-color: $white;
			svg.fr-svg {
				width: 20px;
				height: 20px;
			}
		}
	}

	.attach-files {
		padding: 0px;
		margin: 0px;
		margin-top: 15px;
		background: $white !important;
	}

	.attach-files .attachment-title.attach-icon {
		padding: 0px;
		border: none;
		background: $white !important;
	}

	.attach-files .attachment-uploader-dropdown {
		border: none;
		background: $white !important;
	}

	.attach-files .attachment-uploader-dropdown, .attach-files .attach-helptext {
		color: $linkColor;
	}

	.attach-files .attach-help-size {
		color: $app-grey;
	}

	.attach-files .attachment-list .list-file-item {
		width: 100%;
		transition: 0.3s;
	}

	.ember-power-select-trigger[aria-disabled=true] {
		background-color: $btnDisabledBackground !important;
	}
}
