.ticket-summary-wrap {
	padding: 0 !important;
	.ticket-summary-fields-wrap {
		padding: 0 16px;
	}
}

/*
For resolution time taken summary
*/
.resolution-hours {
	font-size: $text-normal;
	padding-bottom: 15px;
	margin-top: -5px;
	.resolution-hours-label {
		font-weight: $text-weight-medium;
		color: $app-form-label-color;
	}
	.resolution-hours-info {
		.resolution-clock {
			float: left;
			margin-top: 2px;
			padding-right: 8px;
		}
		.resolution-value {
			font-weight: $text-weight-semi-bold;
			line-height: 21px;
		}
	}
}

/*
For AWS alerts summary
*/
.aws-summary {
	display: flex;
	font-size: $text-normal;
	font-weight: $text-weight-semi-bold;
	padding: 0 0 5px 6px;
	margin-left: -16px;
	margin-right: -16px;
	box-sizing: border-box;
	border-bottom-left-radius: 8px;
	border-bottom-right-radius: 8px;
	background: $app-casablanca-light;
	border-top: 1px solid $border-light-blue;
	.aws-bell-icon {
		float: left;
		width: 32px;
		height: 32px;
		display: flex;
		border-radius: 8px;
		align-items: center;
		justify-content: center;
		background: $white;
		margin: 10px;
		svg {
			width: 16px;
			height: 18px;
		}
	}
	.aws-summary-info {
		float: left;
		padding-top: 7px;
		.aws-summary-resume-link {
			button {
				padding: 0;
				border: none;
				cursor: pointer;
				color: $app-primary;
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
				background: $app-casablanca-light;
			}
		}
	}
}

/*
For Ticket status summary
*/
.status-info {
	padding: 15px 0;
	.status-field {
		padding-bottom: 8px;
		font-size: $title-normal;
		font-weight: $text-weight-bold;
		@include line-clamp;
		.resolution-status-label {
			font-weight: $text-weight-normal;
		}
	}
}

/*
For Priority summary
*/
.priority-wrapper {
	@include flexbox;
	@include flex-direction(column);
	font-size: $text-normal;
	.priority-header {
		color: $secondary-text-color-1;
		font-weight: $text-weight-medium;
	}
	.priority-content {
		@include flexbox;
		@include align-items('center');
		.priority-color {
			width: 12px;
			height: 12px;
		}
		.priority-value {
			padding: 2px 0 0 4px;
			font-weight: $text-weight-semi-bold;
			max-width: 92%;
		}
	}
}

/*
For Approvals summary
*/
.approval-info {
	height: 65px;
	font-size: $text-normal;
	font-weight: $text-weight-medium;
	margin-top: -5px;
	padding: 0 0 5px 0;
	.approval-field {
		background: $white;
		color: $app-form-label-color;
		.approval-data {
			height: 30px;
			display: flex;
			.approval-status {
				padding: 2px 8px 2px 8px;
				margin-top: 3px;
				float: left;
				border: 1px solid $border-dark-color;
				box-sizing: border-box;
				border-radius: 4px;
			}
			.requested {
				background: $app-white;
				color: $primary-text-color-1;
				border: 1px solid $border-dark-color;
			}
			.not_requested, .cancelled {
				background: $background-color;
				color: $primary-text-color-1;
				border: 1px solid $border-dark-color;
			}
			.approved {
				background: $alertSuccessBackground;
				color: $alertSuccessColor;
				border: 1px solid $alertSuccessBorder;
			}
			.rejected {
				color: $alertWarningColor;
				background: $alertWarningBackground;
				border: 1px solid $alertWarningBorder;
			}
			.approval-summary {
				padding-top: 3px
			}
			/*
			Overriding remark-form scss here.
			*/
			.approval-container {
				#approval_form_container_summary {
					margin: 18px 0 0 -15px;
					&:after, &:before {
						left: 87%;
					}
					&.approval_posrel {
						&:after, &:before {
							left: 58%;
						}
					}
				}
			}
		}
	}
}

/*
For Requester Feedback side modal
*/
.requester-feedback {
	margin-top: -10px;
	padding-bottom: 15px;
	font-size: $text-normal;
	.requester-feedback-label {
		color: $app-form-label-color;
		font-weight: $text-weight-medium;
		line-height: 20px;
		padding-top: 5px;
	}
	.feedback-mini-wrapper {
		@include flexbox;
		@include align-items(center);
		.feedback-rating-mini {
			float: left;
			.feedback-score {
				font-weight: $text-weight-semi-bold;
				font-size: $text-normal;
				&::after {
					content: "";
					margin-left: 8px;
					height: 16px;
					border-left: 1px solid $border-light-color;
					margin-right: 8px;
				}
			}
			img {
				width: 21px;
				height: 21px;
			}
		}
		.requester-feedback-link {
			.view-feedback-link {
				font-size: $text-normal;
				font-weight: $text-weight-medium;
				padding-left: 0;
				position: relative;
				left: -2px;
			}
		}
	}
}
