.ticket-oncall-details {
	.oncall-details-container {
		border-radius: 8px;
		margin-bottom: 12px;
		background: $app-white;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.01);
		padding: 16px;

		.oncall-details-header {
			position: relative;
			@include font-size($title-normal);
			font-weight: $text-weight-bold;

			.ember-badge, .btn {
				position: absolute;
				right: 0px;
			}
		}

		.ticket-acknowledge-container {
			.first-ack-by {
				@include flexbox;
				@include flex-direction(column);
				padding-top: 16px;
				@include font-size($text-normal);

				.fr-ack-label {
					color: $app-form-label-color;
					font-weight: $text-weight-medium;
				}
				.fr-ack-value {
					color: $primary-text-color-1;
					font-weight: $text-weight-semi-bold;
				}
			}

		}

		.oncall-details-link {
			margin-top: 12px;

			button {
				padding-left: 0px;
			}

			.btn-link {
				display: block;
				margin-bottom: 12px;
				font-weight: $text-weight-semi-bold;
			}
		}
	}
}

.oncall-details-tab-container {
	h3 {
		font-weight: $text-weight-semi-bold;
		@include font-size($title-normal);
	}

	.ticket-assignment-details {
		@include flexbox;
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		border-radius: 8px;
		padding: 20px 12px;
		margin: 20px 0px;
		
		.non-oncall-detail {
			width: 52%;		
			h5 {
				margin-bottom: 12px;
				font-weight: $text-weight-medium;
			}
			.ember-badge-light {
				margin-top: 12px;
			}
		}
		.assigned-group-label {
			padding-top: 28px;
			margin-left: 16px;
		}		
		.ticket-assigned-label {
			font-weight: $text-weight-medium;
		}
		.ticket-assigned-label + .ticket-assigned-label {
			margin-left: 32px;
		}	
	}

	.ticket-oncall-group {
		margin: 20px 0px;
	}

	.max-responder-limit-text {
		@include font-size($title-small);
		color: $secondary-text-color-2;
		width: 68%;
		margin-top: 16px;
	}

	.responder-list {
		margin-top: 28px;
		margin-bottom: 20px;
		@include flexbox;
		.add-responder-icon {
			z-index: 1;
			position: absolute;
			padding: 8px 0 0 8px;
		}
		.ember-basic-dropdown {
			width: 300px;
			margin-right: 12px;
		}
		.ember-power-select-trigger-multiple-input {
			padding-left: 32px !important;
		}
	}
}

.responder-container {
	border: 1px solid $border-light-color;
	box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
	border-radius: 8px;
	@include flexbox;
	padding: 20px 12px;
	margin-bottom: 16px;

	.responder-icon {
		width: 4%;

		&.info {
			path:first-child {
				fill: none;
			}
			path {
				fill: $app-primary;
			}
		}
		&.success {
			path:first-child {
				fill: none;
			}
		}
		&.light {
			path:first-child {
				fill: none;
			}
			path {
				fill: $alertCancelColor;
			}
		}
	}

	.responder-group {
		width: 48%;
		border-right: 2px solid $border-light-color;

		h5 {
			font-weight: $text-weight-medium;
		}

		.ticket-details {
			margin-top: 20px;

			.ticket-notification-status {
				margin-left: 20px;
			}
		}
	}

	.responder-details {
		padding-left: 16px;
		.responder-text-container {
			margin: 8px 0px;
			.responder-label {
				font-weight: $text-weight-medium;
			}
		}
		.info-banner {
			margin: 12px 0px;
			.content{
				margin-top: 8px;
			}
		}
	}
}
