.status-list-wrapper {

	.ember-basic-dropdown-content {
		position: fixed !important;
	}

	.status-list-trigger {
		max-width: 100%;
		min-width: 70px;
		height: 32px;
		border-radius: 4px;
		display: inline-block;
		padding: 4px 8px;
		margin-top: 1px;
		cursor: pointer;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px $inputFocusBorder;
		}

		&:hover,  &[aria-expanded] {
			background: $white;
			box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.05);
		}

		&:hover, &:focus, &[aria-expanded] {
			.dropdown-chevron {
				fill: $primary-text-color-1;
			}
		}

		.dropdown-chevron {
			width: 14px;
			height: 20px;
			float: right;
			position: relative;
			left: 2px;
			top: 2px;
			fill: $disabled-text-color;
		}
	}

	.status-result {
		max-width: calc(100% - 14px);
		display: inline-block;
		vertical-align: middle;
	}

	.status-dropdown-content {
		width: 150px;
		min-height: 20px;
		border: none;
		z-index: 1000 !important;
		padding: 0px !important;
		margin-top: 13px !important;

		.status-search-input {
			width: 136px;
			margin: 8px 8px 0;
		}

		.status-list {
			max-height: 400px;
			overflow-y: auto;
			padding: 8px;
		}

		.status-option {
			cursor: pointer;
			margin: 6px 0;
			word-break: break-word;
			min-height: 32px;
			span {
				max-width: 90%;
				display: inline-block;
			}
		}

		.no-status-option {
			margin-top: 5px;
		}
	}
}
.req-stage-cancel-modal {
	textarea {
		height: 100px;
	}
	.error-msg {
		display: block !important;
	}
}
