.tcpr-widgets {
	.pagerduty {
		color: $primary-text-color-1;
		border-radius: 8px;
		box-shadow: $accordian-light-shadow;
		padding: 8px 16px;
		background: $white;
		.ui-accordion-panel-wrapper {
			padding-bottom: 0;
		}

		&-header {
			&__title {
				padding-left: 8px;
			}
		}

		&-panel {
			font-size: 12px;
			&__text {
				margin: 8px 0;  
			}
			&__details {
				font-weight: 500;
				display: flex;
				align-items: center;
			}

			.empty-state-pd {
				.title {
					font-weight: $text-weight-semi-bold;
					padding: 12px 0 4px 0;
					font-size: $title-small;
				}

				.sub-title {
					color: $inputHoverBorder;
					font-size: $text-normal;
				}
			}
		}

		&-open-new-tab {
			height: 10px;
			padding-left: 4px;
			fill: $app-primary;
		}
	}
}