.typing-wave {
	display: flex;
	align-items: center;
	padding-top: 16px;
	.dot {
		width: 3px;
		height: 3px;
		margin-right: 3px;
		border-radius: 50%;
		display: inline-block;
		background: $app-error-state;
		animation: typing 1.3s linear infinite;
		&:nth-child(2) { animation-delay: -1s; }
		&:nth-child(3) { animation-delay: -0.7s; }
	}
}

@keyframes typing {
	0%, 50%, 100% { transform: initial; }
	30% { transform: translateY(-6px); }
}
