.progress-charts {
	.progress-container {
		display: block;
		height: 15px;
		width: 100%;
		padding: 0 3px;
		overflow: visible;
		box-sizing: border-box;

		.progress-bar {
			width: 100%;
			stroke-linecap: round;
		}
	}

	.bar-content-wrapper {
		
		padding: 6px 12px;

		&:not(:first-child) {
			padding-top: 16px;
		}

		&:hover {
			border-radius: 2px;
			cursor: pointer;
			box-shadow: 0 4px 20px 0 $box-shadow-color-1;
			.bar-label {
				color: $linkColor;
			}
		}
		
		.details {
			color: $app-form-label-color;
			font-weight: $text-weight-medium;
	
			.ember-badge {
				margin-left: 8px;
				font-size: $text-small;
				font-weight: $text-weight-medium;
			}
	
			.progress-count {
				font-size: $title-normal;
				color: $app-form-label-color;
				font-weight: $text-weight-semi-bold;
			}
		}
	
		.total-text {
			text-align: right;  
			color: $mild-text;  
			font-size: $text-normal;
		}

	}

}