/* Shimmer Loader type 2
 Used in Dashboard */
.shimmer-loader-item-fast {
	background: linear-gradient(0deg, $shimmer-background-color, $shimmer-background-color);
	position: relative;
	height: 120px;
	min-width: 100px;
	margin: 8px;
	overflow: hidden;
	border-radius: $radius;

	&:after {
		content: "";
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		z-index: 1;
		width: 75%;
		height: 100%;
		transform: translateX(-50%);
		background: linear-gradient(to right, rgba(255,255,255,0), rgba(255,255,255,0.2), rgba(255,255,255,0.4), rgba(255,255,255,0.6), rgba(255,255,255,1) 50%, rgba(255,255,255,.6), rgba(255,255,255,.4), rgba(255,255,255,.2), rgba(255,255,255,0));
		animation: shimmerAnimation 1.2s linear infinite;
	}

	&.box-rectangle-large {
		height: 320px;
	}

	&.line {
		height: 16px;
		border: 0;
		width: 100%;
		border-radius: 25px;

		&-20 { width: 20%; }
		&-30 { width: 30%; }
		&-40 { width: 40%; }
		&-50 { width: 50%; }
		&-60 { width: 60%; }
		&-70 { width: 70%; }
		&-80 { width: 80%; }
		&-90 { width: 90%; }

		&.line-fat {
			height: 18px;
		}

		&.line-slim {
			height: 14px;
		}
	}


}

@keyframes shimmerAnimation {
	0% {
		transform: translateX(-50%);
	}
	60% {
		transform: translateX(250%);
	}
	100% {
		transform: translateX(320%);
	}
}
