.annotation-tooltip-btn {
	animation: pop-on-top 250ms forwards linear;
	position: absolute;
	display: none;
	background: $btnPrimaryBorder !important;
	.tooltip-text {
		font-size: $text-normal;
		display: inline-block;
		color: $white;
		margin-left: 5px;
	}
	.tooltip-icon {
		path {
			fill: $white;
		}
	}
	&:after,
	&:before {
		content: " ";
		border: solid transparent;
		position: absolute;
		border-width: 7px;
		border-top-color: $btnPrimaryBorder;
		top: 28px;
		left: 55px;
	}
}

.showAnnotationTooltip {
	@include flexbox;
	@include align-items(center);
}

.hideAnnotationTooltip {
	display: none;
	top: 0;
	left: 0;
}