.ember-modal-overlay .ember-modal-dialog.requesterFeedbackSideModal {
	animation: modal-open-animation 0.3s;
	@keyframes modal-open-animation {
		from { right: -600px; }
		to { right: 0; }
	}
	.ember-modal-header {
		padding: 24px 32px 8px;
		.title {
			font-weight: $text-weight-semi-bold;
			color: $primary-text-color-1;
		}
	}
	.ember-modal-body {
		height: calc(100% - 109px) !important;
		.feedback-rated-date {
			font-size: $title-small;
			color: $secondary-text-color-1;
		}
		.feedback-rating-container {
			margin: 40px 0 30px 0;
			.feedback-score-title {
				font-weight: $text-weight-medium;
				font-size: $title-normal;
				color: $primary-text-color-1;
				.feedback-score {
					padding: 13px 12px;
					margin-left: 8px;
					background-color: $background-color-2;
					border-radius: 8px;
				}
			}
		}
		.question-response-container {
			margin-top: 30px;
			margin-left: 12px;
			.question-response {
				font-size: $text-normal;
				.qr-container {
					padding: 0px 0 10px 40px;
					border-left: 1px solid $border-light-color;
					.question {
						font-size: $title-small;
						font-weight: $text-weight-normal;
					}
					.answer {
						font-weight: $text-weight-semi-bold;
						padding: 10px 0 15px 0;
						.answer-text {
							padding: 2px 6px 2px 8px;
							line-height: 18px;
							display: inline-flex;
							align-items: center;
							background: $section-background-blue;
							border: 1px solid $border-light-blue;
							box-sizing: border-box;
							border-radius: 4px;

							.small_emoticon {
								width: 16px;
								height: 20px;
								margin-right: 4px;
							}
							
						}
						.text-type-answer {
							font-weight: $text-weight-medium;
							font-size: $title-small;
							color: $primary-text-color-1;
							word-break: break-all;
						}
					}
				}
				&:last-child {
					.qr-container {
						border-left: 0;
					}
				}
			}
		}
		.circle-index {
			float: left;
			right: 12px;
			width: 24px;
			height: 24px;
			display: flex;
			border-radius: 50%;
			position: relative;
			align-items: center;
			justify-content: center;
			background: $background-color;
			.index-value {
				font-weight: $text-weight-semi-bold;
				color: $tab-icon-color;
			}
		}
	}
	.ember-modal-footer {
		display: none !important;
	}
}
