.export-fields-modal {
	.btn-primary {
		&.disabled,
		&[disabled] {
			background: linear-gradient(180deg, #92A2B1 4.39%, #7B8E9F 94.46%) !important;
			opacity: 0.5;
			color: $white !important;
		}
	}
	.filters-section {
		margin: 24px 0;
		padding-bottom: 16px;
		border-bottom: solid 1px $border-dark-color;
		.format-content,
		.date-content {
			@include flexbox;
			.filter-label {
				width: 25%;
				margin-right: 5px;
			}
		}

		.format-content .radio-field.form-field {
			vertical-align: middle;
		}

		.filter-options {
			@include flexbox;
			&:after {
				content: '';
				clear: both;
				display: table;
			}
			.filter-row:first-child {
				margin-right: 20px;
			}
			.export-filter {
				display: inline-block;
				color: $secondary-text-color-1;
				font-weight: $text-weight-semi-bold;
				max-width: 200px;
				.filter-icon {
					height: 16px;
					width: 20px;
					vertical-align: middle;
				}
				.ember-basic-dropdown-content {
					min-width: 180px;
				}
				.basic-dropdown-item {
					line-height: 1.3;
					padding: 7px 32px 7px 8px;
					margin-bottom: 8px;
					&.active {
						&:after {
							top: 5px;
						}
					}
				}
			}
			.ember-basic-dropdown-trigger {
				font-weight: $text-weight-semi-bold;
				color: $primary-text-color-1;
				cursor: pointer;
			}
			.date-filter-info {
				margin-left: 8px;
				&:focus {
					box-shadow: 0 0 0 2px $linkColor;
				}
				svg {
					fill: $linkColor;
					vertical-align: sub;
					cursor: pointer;
					pointer-events: auto;
				}
				&+.info-tooltip-container {
					padding: 8px;
					min-width: 316px;
				}
			}
		}
		.export-file-type .radio-field {
			margin-right: 42px !important;
		}
		.set-date-section {
			margin-left: 26%;
			margin-bottom: 8px;
			margin-top: 16px;
			.date-range-field:before {
				display: none;
			}
			label {
				padding-bottom: 8px;
				color: $secondary-text-color-1;
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
				display: inline-block;
			}
			.date-field-errors {
				color: $app-red;
				font-size: $text-normal;
				margin-top: 5px;
			}
			.ember-basic-dropdown-content.basic-dropdown-menu {
				left: unset;
				right: -60%;
			}
			.date-picker-wrapper {
				max-width: 245px !important;
				.date-range-picker-container .ember-power-calendar-weekday {
					margin-right: 2px;
					@include justify-content(flex-start);
					word-break: break-all;
				}
			}
		}
	}
	.select-all-section {
		margin-bottom: 44px;
		.ui-component-check-box {
			margin-left: 8px;
		}
	}
	.required-msg-container {
		color: $secondary-text-color-1;
		margin-bottom: 32px;
		margin-top: -8px;
	}
	.export-fields-section {
		display: flex;
		justify-content: space-between;
		margin-bottom: 18px;
		.selected-count {
			margin-right: 24px;
			color: $app-form-label-color;
		}
		.fields-selection-title {
			color: $primary-text-color-1;
			font-weight: $text-weight-semi-bold;
			.required-star {
				color: $app-error-state;
			}
		}
	}
	.export-as-field {
		.radio-group {
			.control-label {
				display: none;
			}
		}
		label {
			cursor: pointer;
		}
	}
	&.ember-modal-dialog {
		.ember-modal-body {
			padding-bottom: 0;
			height: calc(100% - 145px);
			.fieldset-wrapper {
				display: flex;
				flex-flow: column wrap;

			}
		}
	}
	.export-module-fields {
		&.ember-form  {
			.checkbox-field, .dependent-select-checkbox-field {
				margin: 0 0 24px;
				float: left;
				width: 49%;
				padding-left: 4px;
				margin-left: -4px;

				label {
					color: $primary-text-color-1;
					font-size: $title-small;
					vertical-align: middle;
					padding-bottom: 0;
					display: inline;
					font-weight: $text-weight-semi-bold;
					@extend .ellipsis;
					width: 100%;
				}
			}
			.dependent-select-checkbox-field {
				margin-bottom: 20px;
				label {
					@extend .ellipsis;
				}
				&.nested-checkbox-field {
					margin-left: 24px;
					width: 100%;
				}
			}
			.fieldset-wrapper {
				& > .ember-view {
					width: 49%;
				}
			}
		}
	}
}
