.inline-messages {
	height: auto;
	padding: 0 8px;
	align-items: stretch;

	&-info {
		background: $alertInfoBackground;
		border: 1px solid $alertInfoBorder;
		.alert-content::before {
			border-left: 1px solid $alertInfoBorder;
		}
		svg {
			fill: $alertInfoColor;
		}
	}
	&-error {
		background: $alertWarningBackground;
		border: 1px solid $alertWarningBorder;
		.alert-content::before {
			border-left: 1px solid $alertWarningBorder;
		}
		svg {
			fill: $alertWarningColor;
		}
	}

	&-icon {
		padding-top: 8px;
		width: 28px;
		height: 100%;
	}
	
	svg {
		margin-right: 8px;
	}

	.alert-content {
		&::before { // adding margin on separator line
			height: calc(100% - 12px);
			margin-top: 6px;
		}
		padding: 6px 8px;
		height: inherit;
	}
}
