.ember-modal-wrapper {
	opacity: 0;
	&:last-child {
		opacity: 1;
	}
	&:first-child {
		.ember-modal-dialog {
			&.ember-modal-side {
				&-right {
					animation: slide-right .25s;
				}
			}
		}
	}
}
.ember-modal-overlay {
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 1001;

	&.translucent {
		background-color: $app-overlay-hex;
	}

	.ember-modal-dialog {
		background-color: #fff;
		position: fixed;
		width: 512px;
		box-shadow: 0 1px 5px 0px $app-box-shadow-hex;
		border-radius: 4px;

		div[role="dialog"] {
			height: 100%;
		}

		/* modal close icon */
		.ember-modal-close {
			height: 24px;
			width: 24px;
			top: 0;
			left: -24px;
			border-radius: 2px 0 0 2px;
			background: linear-gradient(180deg, #264966 0%, #12344D 100%);
			position: absolute;
			cursor: pointer;
			border: none;
			svg{
				width: 13px;
				height: 13px;
				bottom: 6px;
				left: 6px;
				position: absolute;
				fill: $app-white;
			}
		}

		/* default modal styling */
		.ember-modal {
			&-header {
				padding: 24px 32px 16px 32px;
				box-sizing: border-box;
				.title {
					font-size: 18px;
					display: flex;
				}
				.back-btn {
					@include flexbox-simple(flex-start, center);
					margin-left: -6px;
					padding: 0;
					padding-right: 6px;
					font-weight: $text-weight-semi-bold;

					.chevron-icon {
						fill: $linkColor;
					}
				}
			}

			&-body {
				padding: 4px 32px 32px;
				// height: calc(100% - 165px);
				height: calc(100% - 125px);
			}

			&-footer {
				display: flex;
				height: 56px;
				align-items: center;
				justify-content: flex-end;
				width: 100%;
				padding: 0 24px;
				background-color: $app-sidebar-bg;
				border-bottom-left-radius: 6px;
				border-bottom-right-radius: 6px;
			}
		}

		&.module-view-list {
			.ember-modal-header {
				border-top: none !important;
				.ember-modal-close {
					display: none;
				}
			}

			.ember-modal-body {
				margin-top: 16px;
				padding: 0 16px 16px;
				height: calc(100% - 50px);
			}
		}

		/* center modal overridings */
		&.ember-modal-center {
			top: 30%;
			left: 50%;
			transform: translate(-50%, -50%);

			.ember-modal-close {
				display: none;
			}

			.ember-modal {
				&-body {
					max-height: 280px;
				}
			}
		}

		/* side modal overridings */
		&.ember-modal-side {
			width: 600px;
			height: 100%;
			border-radius: 0;

			.ember-modal {
				&-header {
					border-top: 1px solid #000;
					.title {
						font-size: 24px;
					}
				}

				&-body {
					overflow-y: auto;
				}

				&-footer {
					position: fixed;
					bottom: 0;
					border-radius: 0;
					border-top:	1px solid $modal-footer-border-color;
					box-sizing: border-box;
					width: 600px;
					z-index: 2;
				}
			}

			&-right {
				right: 0;
				left: auto;

				// TODO: to be moved out of this file
				&.module-view-list {
					width: 280px !important;
				}
			}

			&-left {
				left: 0;
				right: auto;
				animation: slide-left .25s;

				// TODO: to be moved out of this file
				&.module-view-list {
					width: 280px !important;
					left: 60px !important;
				}

				.ember-modal-close {
					right: -24px;
					left: initial;
				}
			}
		}

		/* confirm modal overrides */
		&.ember-modal-confirm{
			width: 424px;
			.ember-modal-body{
				max-height: 140px;
			}

			&.delete-modal {
				width: 512px;
				.ember-modal-body {
					padding-bottom: 36px;
				}
			}

			&.detach-confirm-modal {
				width: 610px;
				top: 18%;
			}
		}

		.modal-confirm-inside{
			position: absolute;
			top: 1px;
			left: 0;
			bottom: 0;
			padding: 32px 32px 16px;
			background: $white;
			z-index: 1;
			.title{
				font-size: 24px;
				margin-bottom: 40px;
			}
		}

		/* For modal with no footer, pass no-footer as containerClass to modal */
		&.no-footer {
			.ember-modal-body {
				height: calc(100% - 36px);
				padding-bottom: 0;
			}
		}

	}
}


@keyframes slide-right {
	0% {
		transform: translateX(100%);
	}
	100% {
		transform: translateX(0px);
	}
}

@keyframes slide-left {
	0% {
		transform: translateX(-100%);
	}
	100% {
		transform: translateX(0px);
	}
}
