.toggle-switch {
	position: relative;
	display: inline-block;
	width: 35px;
	height: 20px;

	.slider {
		position: absolute;
		cursor: pointer;
		top: 2px;
		left: 0;
		right: 0;
		height: 16px;
		border-radius: 35px;
		background: $toggleSlider;
		transition: 0.2s;
		svg{
			position: absolute;
			fill: $toggleSlider;
		}
		.checked{
			left: 20px;
			width: 12px;
			fill: $linkColor;
			top: 2px;
			height: 12px;
		}
		.unchecked{
			width: 18px;
			height: 18px;
			left: 1px;
			top: -1px;
		}
	}

	.slider:before {
		content: "";
		position: relative;
		width: 20px;
		height: 20px;
		top: -2px;
		left: 0;
		border-radius: 50%;
		background-color: $white;
		transition: 0.2s;
		display: inline-block;
		border: 1px solid $toggleSlider;
		box-sizing: border-box;
	}

	input {
		opacity: 0;

		&:focus + label .slider:before {
			box-shadow: 0px 0px 0px 3px lighten($linkColor, 30);
		}
	}

	input:checked + label .slider {
		background-color: $linkColor;
	}

	input:checked + label .slider:before {
		transform: translateX(16px);
	}

	input:disabled + label .slider {
		opacity: 0.4;
		cursor: not-allowed;
	}

	&.disabled {
		input:checked+ label .slider {
			background-color: $toggleDisabled;
			&:before { border-color: $toggleDisabled; }
			svg { fill: $toggleDisabled; }
		}
	}
}
