.rule-container {
	button:disabled {
		border: none;
		svg{
			opacity: 0.5;
		}
	}

	button.btn-delete-condition {
		padding: 6px 3px;
		box-sizing: border-box;
		height: 32px;
	}

	.btn-delete-condition, .btn-add-condition {
		background: transparent !important;
		border: 1px solid transparent;
		padding: 4px !important;
		box-shadow: none;
		&:hover {
			border-color: transparent !important;
		}
		svg {
			@extend .element-inline-middle;
			width: 16px;
			height: 16px;
		}
		span{
			@extend .element-inline-middle;
		}
	}

	.rule-row {
		padding: 8px;
		box-sizing: border-box;
		background: $app-sidebar-bg;
		border: 1px solid $app-border-primary;
		border-top: 0;
		display: flex;

		&:first-child {
			border: 1px solid $app-border-primary;
			border-top-left-radius: 4px;
			border-top-right-radius: 4px;
		}

		&:last-child {
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
		}

		.btn-delete-condition, .btn-add-condition {
			padding: 0;
			margin-right: 8px;
			margin-left: 4px;
		}

		> button, > div {
			@extend .element-inline-top;
		}

		.btn-add-condition span {
			margin-left: 12px;
			font-size: 14px;
		}

		// Nested rule_condition component
		.rule-container {
			.rule-row	{ 
				background: none;
				border: none;
				padding-right: 0px;
				&:first-child {
					padding-top: 0px;
				}
				&:last-child {
					padding-bottom: 0px;
				}
			}
		}
		
	}
}
