$sfbChatHeaderBgColor: #00aff0;
$sfbContainerBoxShadowColor: #b9b9b9;

#sfb-conversation-container {	
	.chat-header {
		padding: 10px;
		background: $sfbChatHeaderBgColor;
		height: 40px;

		.left-section, .right-section {
			display: inline-block;
		}

		.left-section {
			margin-left: 20px;

			.req-name {	
				font-weight: $text-weight-bold;
				font-size: $title-normal;
				color: $app-grey;
				position: relative;
				left: 20px;
				max-width: 200px;
				display: inline-block;
			}
		}

		.right-section {
			float: right;
		}

	}

	&.sfb-show-sidebar {
		position: fixed;
		display: block !important;
		width: 400px;
		height: 100% !important;
		right: 0 !important;
		top: 0;
		z-index: 99999;
		box-shadow: -7px 0 13px -6px $sfbContainerBoxShadowColor;
		transition: right 500ms;
		background: $white;
	}

	.wrapper {
		min-width: 192px !important;
	}

	.swx-chat-input {
		position: relative;
		bottom: 40px;
	}

	.ChatInput--optimizeSpace {
		.form {
			width: 350px;
			display: block;

			#chatInputAreaWithQuotes {
				height: 30px;
				width: 320px;
				box-shadow: none;
			}
		}

		.swx-expression-picker.wrapper {
			position: inherit;
			
			.bubble {
				width: 214px;
				height: 350px;
				bottom: 50px;
				left: 190px;
			}

			li.picker-item.emoticon {
				width: 24px;
				height: 24px;
				margin: 0;
			}
		}
	} 
}