.time-entries-loader-wrap {
	@include flexbox;
	@include justify-content(center);
	.time-entry-list-wrap {
		width: 100%;

		.add-time-btn {
			height: 24px;
			font-weight: $text-weight-semi-bold;
			border: 1px solid $app-grey;
			background: $btnDisabledGradientColor;
			padding: 0 8px;
			border-radius: 4px;
			font-size: $text-normal;
			color: $white;
		}

		.time-entries-empty {
			height: 100px;
			width: 100%;
			@include flexbox-simple(space-between, flex-start);
			flex-direction: column;
			background: $background-color;
			padding: 12px 16px 12px 16px;
			border-radius: 4px;
		}

		.time-tracked-container {
			@include flexbox-simple(space-between, center);
			border-radius: 4px;
			background: $background-color;
			padding: 12px 16px 15px;
			margin-top: 12px;

			.total-time-details {
				width: 70%;

				.time-tracked-label {
					font-size: $text-normal;
					font-weight: $text-weight-medium;
					color: $primary-text-color-1;
				}

				.total-time-spent {
					font-size: $title-large;
					font-weight: $text-weight-semi-bold;
					display: block;
					margin-top: 8px;
					margin-bottom: 0px;
				}

				.add-time-btn {
					margin: 4px 0;
				}
			}
		}
	}
}
