.ember-power-select-option.requester-options {
	&[aria-current=true]:not([aria-selected=true]) {
		background-color: $app-sidebar-bg !important;
	}

	.requester-name {
		color: $app-icon-color;
		margin-bottom: 0;
	}

	.requester-email, .requester-phone {
		color: $app-form-label-color;
		font-weight: $text-weight-normal;
		font-size: $text-normal;
		margin-bottom: 0;
	}
}