		}
	}
}

.basic-dropdown-wrap {
	.ember-basic-dropdown-content{
		a.active{
			background-color: $dropdown-option-selected;
		}

	}
}

// For Basic Dropdown animation
.ember-basic-dropdown-content.animate-dropdown-below {
	will-change: transform, opacity;
	&.ember-basic-dropdown-content--below.ember-basic-dropdown--transitioning-in {
		animation: drop-fade-below .15s;
	}
}

@keyframes drop-fade-below {
	0%   {
		opacity: 0;
		transform: translateY(-20px);
	}
	100% {
		opacity: 1;
		transform: translateY(0px);
	}
}
