$infoIconFillColor: #283DA5;
.requester-info-wrap {
	padding: 0 !important;
	.show-true {
		transition: opacity 0.4s ease-in-out;
		transform: translate(0);
		opacity: 1;
		max-height: 350px;
		padding-top: 8px;

		&[data-requester-type="salesforce"] {
			max-height: 392px;
		}
		&[data-requester-loaded="true"] {
			margin-bottom: 8px;
		}
	}
	.show-false {
		visibility: hidden;
		transition: height 0.2s ease-in-out;
		opacity: 0;
		height: 0;
	}
	.requester-widget {
		// padding: 8px 0px 8px 0px;
		padding: 15px 15px 15px 16px;
		.recent-tickets-nullify {
			border: 0px;
			padding: 0px;
		}
		.requester-show-more {
			cursor: pointer;
			text-align: left;
			@include flexbox-simple();
			padding-top: 8px;
			margin-left: 40px;
			font-weight: $text-weight-medium;
			font-size: $text-normal;
			line-height: 1.2;
			@include transition(all 0.5s ease-in-out);

			&.no-padding {
				padding-top: 0;
				margin-top: -2px;
			}

			.view-more-less {
				padding-top: 2px;
				font-weight: $text-weight-medium;
				font-size: $text-normal;
				line-height: 1.2;
				text-transform: lowercase;
				white-space: nowrap;
				&::first-letter {
					text-transform: capitalize;
				}
			}
			.more-less-section {
				@include flexbox-simple();
			}

			.toggle-widget-icon {
				padding-left: 2px; // icon is bigger then mock, so reduced padding from 4 to 2
				line-height: 1.2;
			}
			.toggle-widget-icon svg {
				fill: $app-primary;
				padding-top: 1px;
			}
		}
		.requester-header {
			@include flexbox-simple();
			color: $primary-text-color-1;
			.requester-title-name {
				padding-left: 3px;
				.requester-side-header {
					display: flex;
					height: 24px;
					.badge-info {
						padding-left: 8px;
						background-color: $white;
					}
					.requester-name-info {
						font-weight: $text-weight-semi-bold;
						font-size: 16px;
						line-height: 24px;
						color: $inputFocusBorder;
						max-width: 195px;
						display: inline-block;
					}
				}

				.requester-job-title {
					font-size: 12px;
					line-height: 16px;
					color: $inputColor;
					word-wrap: break-word;
					max-width: 195px;
					display: flex;
					flex-direction: column;
				}
			}

			.avatar__icon--small {
				height: 32px;
				width: 32px;
				margin-right: 5px;
			}

			.avatar__icon--circle img {
				height: 28px;
				width: 28px;
				position: relative;
				bottom: 2px;
			}
			.avatar__icon--circle div {
				height: 28px;
				width: 28px;
				position: relative;
				bottom: 2px;
			}
			.toggle-arrow {
				width: 18px;
				height: 18px;
			}
			.requester-image {
				background-blend-mode: normal;
				border: 2px solid $section-background-blue;
				box-sizing: border-box;
				border-radius: 80px;
				float: left;
				margin-top: 4px;
			}
		}

		.requester-toolbar {
			@include flexbox-simple(space-between, center);
			margin-bottom: 4px;

			.requester-toolbar__content {
				@include flexbox-simple(space-between, center);

				.requester-toolbar__icon {
					@include flexbox-simple(flex-start, center);
					width: 32px;
					height: 32px;
					padding-left: 2px;
					@include svg-fill($app-grey);
				}

				.requester-toolbar__title {
					font-weight: $text-weight-semi-bold;
					line-height: 20px;
					color: $app-grey;
				}
			}

			.requester-detail-change-dropdown {
				.ember-basic-dropdown-trigger:focus-visible {
					box-shadow: none;
				}
				button {
					@include flexbox-simple(space-between, center);
					padding: 0;
					margin: 0;
				}
				.requester-selected-icon {
					@include flex-center;
					height: 100%;
					flex: 3; // 3/5 of total
					svg {
						&.salesforce-requester-info {
							width: 28px;
						}
					}
				}
				.requester-dropdown-divider {
					height: calc(100% - 2px);
					border-right: 1px solid $modal-footer-border-color;
				}

				.ember-basic-dropdown-content {
					padding: 11px 8px;
					margin-top: 2px;
					min-width: 162px;
				}

				.requester-dropdown-item {
					height: 34px;
					margin: 0;
					padding: 8px;

					&:not(:first-of-type) {
						margin-top: 2px;
					}
				}
			}
		}

		// Mkp apps accordions inside requester widget
		.marketplace-app-container {
			.expandable-app { 
				width: 100% !important;
				border-color: $border-light-color !important;
				&:hover { border-color: $inputHoverBorder !important; }
			}
		}
	}
	.hangouts-block {
		padding-top: 14px;
	}
	.skype-block {
		padding-top: 11px;
	}
	.office-365-block {
		.meeting-schedule-loader {
			margin: 10px auto;
		}

		.calendar-icon {
			margin-top: 6px;
			svg {
				height: 20px;
			}
		}
		.meeting-scheduled-icon {
			margin-top: 3px;
		}

		.meeting-link {
			display: block;
			margin: 2px 0 8px 8px;
		}
		.meeting-scheduled,
		.schedule-meeting-link {
			font-size: $text-normal;
		}
		.schedule-meeting-link {
			font-family: inherit;
		}
		.margin-left-8 {
			margin-left: 8px;
		}
	}
}

.requester-detail-btn {
	@include flex-center;
	flex: 2; // 2/5 of total
	height: 100%;
}

.requester-selection-button {
	width: 64px;
	height: 32px;
	background: none;
}

.requester-details-item {
	display: flex;
	flex-direction: column;
	padding-top: 17px;
	.recent-tickets {
		width: max-content;
		font-size: 12px;
		font-weight: 400;
		text-align: left;
		color: $inputFocusBorder;
	}
	.reporting-manager {
		display: flex;
		flex-direction: column;
	}
	.repo-manager-name {
		font-weight: 400;
		text-align: left;
		color: $inputFocusBorder;
		line-height: 21px;
	}
	.repo-manager-email {
		font-size: 12px;
		font-weight: 500;
		text-align: left;
		color: $primary-text-color-1;
		line-height: 20px;
	}
}

.requester-details {
	@include transition(all 0.25s ease-in-out);
	.requester-details-item {
		display: flex;
		flex-direction: column;
		padding-top: 17px;
		.reporting-manager {
			display: flex;
			flex-direction: column;
		}
		.repo-manager-name {
			font-weight: $text-weight-normal;
			text-align: left;
			color: $inputFocusBorder;
			line-height: 21px;
		}
		.repo-manager-email {
			font-size: $text-normal;
			font-weight: $text-weight-medium;
			text-align: left;
			color: $primary-text-color-1;
			line-height: 20px;
		}
		.recent-tickets {
			font-size: $text-normal;
			font-weight: $text-weight-normal;
			text-align: left;
			color: $inputFocusBorder;
		}
	}
}

.requester-custom-heading {
	font-weight: $text-weight-normal !important;
	font-size: $text-normal !important;
	line-height: 21px;
	color: $primary-text-color-1;
}

.requester-phone {
	display: flex;
	.requester-phone-column {
		display: flex;
		flex-direction: column;
	}
	.skype-icon {
		margin-left: auto;
		padding-left: 11px;
		border-left: 1px solid $app-border-primary;
		margin-top: auto;
	}
}
.skype-text {
	.skype-status {
		font-size: 12px;
		line-height: 20px;
		font-weight: 500;
		text-align: left;
	}
}
.hangouts-status {
	@include flexbox-simple();
	.btn {
		margin-right: 0px;
	}
	.hangouts-btn {
		background: $hangoutsGreen;
		height: 24px;
		margin-left: auto;
		@include flexbox-simple(space-between, center);
		border: none;
		padding-top: 2px;
		svg {
			height: 20px;
			width: 14px;
			padding-top: 1px;
			padding-right: 2px;
		}
	}
	.hangouts-text {
		margin: auto 0;
	}
	.hangouts-btn-text {
		font-size: $text-normal;
		font-weight: 500;
		color: $app-white;
		padding-left: 5px;
		padding-top: 2px;
	}
}

.skype-info {
	@include flexbox-simple(space-between, center);
	padding-top: 4px !important;
	margin-bottom: -3px;
	.btn {
		margin-right: 0px;
	}
	.skype-subheading.sign-in-btn {
		padding-top: 10px;
	}
	.skype-btn-item {
		margin-left: auto;
		padding-top: 10px;
	}
	.skype-btn {
		margin-left: auto;
		height: 24px;
		padding: 1px 8px 4px 8px;
		background: $skypeBlue;
		border-radius: 4px;
		display: flex;
		padding-left: 8px;
		border: none;
		svg {
			width: 18px;
			height: 24px;
		}
		.btn-text {
			font-size: 12px;
			font-weight: 500;
			padding-left: 4px;
			color: $white;
		}
	}
}

.requester-sub-heading {
	font-weight: $text-weight-semi-bold;
	font-size: $text-normal;
	line-height: 16px;
	color: $toggleSlider;
	padding-bottom: 0px;
}
.ph-number {
	max-width: 245px !important;
}
.requester-sub-details {
	padding-top: 4px;
	font-style: normal;
	font-weight: $text-weight-semi-bold;
	font-size: $title-small;
	color: $inputColor;
	max-width: 270px;
	word-wrap: break-word;
}

.dashed-border {
	border-top: 1px solid $inputDisabledBorder;
	padding-top: 10px;
}
a {
	&:focus {
		outline: 2px auto -webkit-focus-ring-color;
		outline-offset: 2px;
	}
}

// Recent Tickets Styles - Starts
.all-recent-tickets {
	padding-top: 28px;
	font-weight: $text-weight-semi-bold;
}
.recent-tickets-modal {
	.title {
		width: 450px;
		@include line-clamp(1);
		word-wrap: break-word;
		display: block !important;
	}
	.ember-modal-footer {
		display: none !important;
	}
	.recent-ticket-container {
		height: 102px;
		border-radius: 8px;
		background: $white;
		border: 1px solid $border-light-color;
		box-sizing: border-box;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		margin-top: 8px;
		padding: 16px;
		@include flexbox-simple(space-between, center);
		&:hover {
			border: 1px solid $border-light-blue;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		}
		.recent-ticket-details {
			width: 250px;
			.recent-ticket-top {
				word-wrap: break-word;
				.subject-text {
					font-size: $title-small;
					font-style: normal;
					font-weight: $text-weight-medium;
					line-height: 17px;
					color: $app-primary;
				}
				.recent-ticket-display {
					float: left;
					padding-right: 4px;
					font-weight: $text-weight-medium;
				}
			}
			.recent-ticket-bottom {
				.recent-ticket-details-lower {
					padding-top: 8px;
					color: $app-form-label-color;
					font-size: 0;
					span {
						font-size: $title-small;
					}
				}
			}
		}
		.recent-ticket-separator {
			height: 64px;
			width: 1px;
			border-radius: 2px;
			background: $border-light-color;
			margin-top: 3px;
		}
		.recent-ticket-info {
			display: flex;
			flex-direction: column;
			max-width: 172px;
			text-align: left;
			justify-content: center;
			.assign-to {
				padding-top: 0px !important;
			}

			.responder-name {
				width: 37px;
			}

			.department {
				width: 37px;
			}
			.info-field {
				max-width: 172px;
				display: flex;
				padding-left: 8px;
				padding-top: 4px;
				.assign-value {
					display: flex;
				}
			}
			.ticket-info-header {
				width: 64px;
				color: $app-form-label-color;
			}
			.info-value {
				width: 72px;
				font-weight: $text-weight-medium;
				line-height: 17px;
				color: $primary-text-color-1;
				align-items: center;
			}
			.priority-color {
				@include priority-colors(".priority-Low", ".priority-Medium", ".priority-High", ".priority-Urgent");
				float: left;
				margin: 4px 6px 4px 1px;
			}
		}
		.recent-ticket-state {
			margin-left: auto;
			display: flex;
			align-items: center;
			.ticket-state {
				display: flex;
				align-items: center !important;
				justify-content: center;
			}
			.subject-style {
				font-weight: $text-weight-semi-bold;
				display: flex;
				white-space: nowrap;
				font-size: 12px;
				line-height: 14px;
				padding-right: 8px;
				text-transform: capitalize;
				.state-style {
					padding: 3px 8px !important;
					border-radius: 4px;
				}
				@include subject-style();
			}
		}
	}
	// Overriding Hover card styles
	.subject-card-wrapper {
		&:focus {
			outline: 1px solid $inputFocusBorder;
			outline-offset: -1px;
		}
		.subject-card-content {
			padding-left: 0px;
			min-height: 0px !important;
			max-height: 188px !important;
			.subject-card-info .requester-name {
				color: $primary-text-color-1;
				font-size: 14px;
				font-weight: $text-weight-semi-bold;
				line-height: 17px;
				letter-spacing: 0px;
			}
			.subject-card-description {
				color: $primary-text-color-1;
				font-weight: $text-weight-semi-bold;
				line-height: 17px;
				letter-spacing: 0px;
				text-align: left;
				height: auto;
				max-height: 100px !important;
			}
		}
	}
	// Recent Tickets Styles - Ends

	.info-tooltip-container {
		word-wrap: break-word;
	}
}

.requester-info {
	font-size: $text-normal;
	height: 52px;
	background: $section-background-blue;
	border: 1px solid $alertInfoBorder;

	.alert-content::before {
		border-color: $alertInfoBorder;
	}

	.info-icon {
		fill: $infoIconFillColor;
		position: relative;
		bottom: 5px;
	}
}
