.marketplace-app-container {
	.expandable-app {
		@extend .tcpr-widget;

		// Add override on top of .tcpr-widget generic styles
		margin-top: 8px;
		margin-bottom: 0;

		.ui-accordion-item {
			.ui-accordion-header-trigger {
				.accordion-title-wrap {
					img {
						width: 16px;
						height: 16px;
						vertical-align: middle;
						margin-right: 4px;
					}
				}
			}
		}
	}

	&.ticket_top_navigation,
	&.ticket_conversation_editor {
		@include flexbox;
	}

	&.mkp-apps-quick-nav-more-dd {
		display: block;
	}

	// Scroll for apps - start
	// Rendering multiple apps inside scroll in froala editors 
	// since we have only limited space in froala toolbar/footer to render apps 
	&.ticket_conversation_editor {
		max-width: 150px;
		overflow: auto;
		padding: 2px;
		margin-left: 4px;
	}

	&.new_ticket_description_editor {
		max-width: 115px;
		overflow: auto;
	}

	@media (max-width: 1200px) {
		&.new_ticket_description_editor {
			max-width: 60px;
		}
	}
	// Scroll for apps - end
}

.marketplace-iframe {
	iframe {
		width: 100%;
		height: 100%;
		position: relative;
	}
	&.mkp-iframe-btn {
		width: 32px;
		height: 32px;
		padding: 2px;
		background: $white;
		img {
			width: 16px;
			height: 16px;
			vertical-align: middle;
		}
		&:hover {
			background: $background-color-2;
		}
	}
}

.marketplace-side-modal {
	.ember-modal-body iframe {
		width: 100%;
		height: 100%;
	}
}

#create-new-ticket-new-form-froala-editor {
	.fr-toolbar .fr-float-right {
		button[id^="mkpPlaceholderBtn"] {
			&:hover {
				background: transparent;
			}
		}
		#mkp-placeholder-new-tkt-desc {
			height: 24px;
			.mkp-iframe-btn {
				height: 24px;
				width: 24px;
				padding: 0;
				margin: 0;
				img {
					margin: 0;
				}
			}
		}
	}
}
