
/*--- Common animations to be added here --*/

//-- Auto Refresh Notification Animation
@keyframes soft-drop-animate {
	0% {
		opacity: 0;
		transform: translateY(-30px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@keyframes fadeIn {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes fadeOut {
	0% {
		opacity: 1;
	}

	100% {
		opacity: 0;
	}
}

// For modals - slide in from right
@keyframes slide-right {
	0% {
		transform: translateX(100%);
	}
	100% {
		transform: translateX(0px);
	}
}

.basic-dropdown-slide-down {
	will-change: transform, opacity;
	@extend .drop-down-fade-below;
	&.ember-basic-dropdown-content--below.ember-basic-dropdown--transitioning-out {
		animation: fadeOut 0.2s;
	}
}

.drop-down-fade-below {
	&.ember-basic-dropdown-content--below.ember-basic-dropdown--transitioning-in {
		animation: soft-drop-animate .15s;
	}
}

@-moz-keyframes spin {
	100% {
		-moz-transform: rotate(360deg);
	}
}

@-webkit-keyframes spin {
	100% {
		-webkit-transform: rotate(360deg);
	}
}

@keyframes spin {
	100% {
		-webkit-transform: rotate(360deg); transform:rotate(360deg);
	}
}


@keyframes dots {
	0%, 20% {
		color: $white;
		text-shadow:
			10px 0 0 $white,
			20px 0 0 $white;
	}
	40% {
		color: #B6B9BC;
		text-shadow:
			10px 0 0 $white,
			20px 0 0 $white;
	}
	60% {
		text-shadow:
			10px 0 0 #B6B9BC,
			20px 0 0 $white;
	}
	80%, 100% {
		text-shadow:
			10px 0 0 #B6B9BC,
			20px 0 0 #B6B9BC;
	}
}
