$toaster-bg:(
	success : #00A886,
	info    : #2C5CC5,
	warning : #E86F25,
	danger  : #D72D30
);

$toaster-primary: white;
$toaster-text-color: #12344D;
$toaster-close-color: #686a76;
$alert-shadow-color: rgba(0,0,0,0.22);

.toaster__container{
	position: fixed;
	font-family: $baseFontFamily;
	z-index: 999999;
	top: 12px;
	right: 12px;
}

.alert {
	display: block;
	position: relative;
	background: $toaster-primary;
	margin: 0;
	transition: 0.4s ease-in-out;
	right: -200px;
	box-shadow: 0 2px 20px 0 $alert-shadow-color;
	border-top: 4px solid $toaster-primary;
	overflow: hidden;
	border-radius: 4px;

	&-success {
		border-color: map-get( $toaster-bg, success );
		svg path {
			fill: map-get( $toaster-bg, success );
		}
	}

	&-danger {
		border-color: map-get( $toaster-bg, danger );
		svg path {
			fill: map-get( $toaster-bg, danger );
		}
	}

	&-info {
		border-color: map-get( $toaster-bg, info );
		svg path {
			fill: map-get( $toaster-bg, info );
		}
	}

	&-warning {
		border-color: map-get( $toaster-bg, warning );
		svg path {
			fill: map-get( $toaster-bg, warning );
		}
	}

	&.active{
		right: 0;

		&.existing{
			opacity: 0.5;
			right: 0;
		}
	}

	.toaster__left{
		top: 16px;
		position: absolute;
		display: inline-block;
		svg {
			position: relative;
			height: 20px;
			width: 50px;
			font-size: 20px;
		}
	}

	.toaster__message{
		display: inline-block;
		word-break: break-word;
		padding: 16px 40px 16px 50px;
		font-weight: 500;
		background: $toaster-primary;
		color: $toaster-text-color;
		width: 280px;
		
		b { font-weight: $text-weight-semi-bold; }

		ul {	list-style-type: disc; }

		a {	cursor: pointer; }

		div.render-as-list-container {
			.view-more-component.render-as-list {
				.btn-link.view-more-toggle {
					margin-top: 0px;
				}
				&.is-open {
					.btn-link.view-more-toggle {
						display: none;
					}
				}
				.list {
					list-style-type: none;
					margin: 4px 0 0;
					padding: 0 0 0 16px;
					> .no-bullet {
						list-style-type: none;
					}
					> .bullet {
						list-style-type: disc;
						> button {
							display: contents;
						}
					}
				}
			}
		}

		.hyperlink-btn {
			padding-left: 4px;
			margin-top: -4px;
		}
	}

	.toaster__close{
		display: inline-block;
		position: absolute;
		right: 12px;
		top: 12px;
		cursor: pointer;
		font-size: 22px;
		font-weight: 600;
		background: none;
		border: none;
		.ember-svg path {
			fill: $toaster-close-color !important;
		}
	}
}

.flash-message {
	padding: 0px;
	margin-bottom: 8px;

	* {
		box-sizing: content-box;
	}

	svg{
		width: 20px;
	}
}
