.associate-asset-card-wrapper {
	.asset-heading {
		font-weight: $text-weight-semi-bold;
	}
	.associate-asset-card {
		position: relative;

		.asset-card {
			width: 780px;
			height: 70px;
			background:	$app-white;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			border-radius: 8px;
			margin-bottom: 10px;
			
			.asset-card-container {
				text-align: left;	
				position: relative;
				left: 40px;
				display: flex;
			}
			
			.asset-icon-box {
				width: 32px;
				height: 32px;
				display: inline-block;
				background: $section-background-blue;
				border: 1px solid $border-light-blue;
				border-radius: 4px;
				margin-top: 12px;
			}

			.asset-icon {
				fill: $inputFocusBorder;
				position: relative;
				top: 7px;
				left: 7px;
			}

			.asset-title {
				position: relative;
				left: 15px;
				top: 5px;
				p {
					max-width: 650px;
					&:first-child {
						margin: 0;
					}
				}
				.asset-user-name {
					font-size: 12px;
					font-weight: $text-weight-normal;
					span {
						font-weight: $text-weight-medium;
					}
				}
			}
		}
		.asset-delete-icon {		
			position: absolute;
			left: 16px;
			top: 26px;
			height: 18px;
			padding: 0 !important;	
			margin: 0;
			cursor: pointer;
		}
	}
}