$shimmerContainerBg: #FFFFFF;
$shimmerItemColor: #EBEFF3;

.dashboard-shimmer {
	width: 100%;
	height: calc(100vh - 60px);
	background: $shimmerContainerBg;

	.shimmer-loader-container {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 8px;
		box-sizing: border-box;

		.shimmer-loader-item-fast {
			flex: 6;
		}
	}
}

