.search-input-wrapper {
	height: 32px;
	&.show-icons {
		@include flex-center;
		padding: 0 8px;
		border-radius: 4px;
		border: 1px solid $app-border-primary;
		box-shadow: inset 0 1px 2px 0 rgba($app-primary-black, 0.05);
		svg.search-icon { width: 16px; height: 16px; }
		input[type='text']{
			width: 91%;
			height: 30px;
			border: none;
			box-shadow: none;
			&:focus {
				border-color: transparent !important;
				box-shadow: none !important;
			}
		}
		.search-clear{
			display: none;
			cursor: pointer;
			&.visible { @include flexbox; }
			svg.close-icon { width: 24px; height: 24px; vertical-align: middle; }
		}
		&.active {
			border: 1px solid $app-primary;
			box-shadow: 0 0 0 1px $app-primary;
		}
	}
}
