$attach-container-bg: #fff;
$attach-error: #D7461F;
$drop-zone-height: 121px;
.unsupported-file-drop-error {
	.unsupported-content-warning {
		margin-bottom: 15px;
	}
}

.attach-files,
.tkt-editor-attachment-list {
	padding: 16px;
	margin: 16px;
	background: $button-hover-color;
	.attachment-list-container-drag-drop {
		position: relative;
		min-height: $drop-zone-height;
		.drop-zone-container {
			position: absolute;
			top: 0;
			bottom: 0;
			left: 0;
			right: 0;
			&.hide-drag-drop {
				height: 0;
				.drag-drop-zone {
					display: none;
				}
			}
			&.highlight-drag-drop {
				.drag-drop-zone {
					border-color: $linkColor;
				}
			}
			.drag-drop-zone {
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
				margin-top: 8px;
				@include flexbox-simple(center, center);
				@include flex-direction(column);
				background-color: $background-color-2;
				opacity: 0.9;
				border: 1px dashed $app-border-primary;
				border-radius: 8px;
				color: $secondary-text-color-1;
				line-height: 24px;
				min-height: $drop-zone-height;
				z-index: 1;
				.drop-text-message {
					font-weight: $text-weight-medium;
					font-size: $title-normal;
				}
				.file-size-message {
					font-weight: $text-weight-normal;
					font-size: $title-small;
				}
			}
		}
	}

	.attachment-uploader-dropdown {
		display: inline-block;
		min-width: 252px;

		.ember-basic-dropdown-trigger {
			display: inline-block;
		}
		.ember-basic-dropdown-content {
			margin-top: -5px;
			.basic-dropdown-item {
				font-weight: $text-weight-normal;
				padding: 0;

				.btn {
					background: none;
					border: none;
					width: 100%;
					text-align: left;
					height: 38px;
					
					&:focus, &:hover {
						background: none;
					}
				}
			}
		}

		/* file upload dropdown with cloud apps */
		.file-upload{
			padding: 0;
			margin-top: 0 !important;
			label{
				cursor: pointer;
				line-height: inherit;
				.attach-size-limit {
					color: $app-form-label-color;
				}
			}
		}

		.attachment-title {
			display: flex;
			align-items: center;
			border: 1px solid transparent;
			background: $background-color-2;
		}

		.cloud-apps,
		.file-upload label {
			border: 2px solid transparent;
			padding-bottom: 0;
		}

		.cloud-apps {
			&:focus {
				border: 2px solid $inputFocusBorder;
			}
		}

		.file-upload input {
			opacity: 0;
			position: absolute;
		}
		input:focus + label{
			border: 2px solid $inputFocusBorder;
			border-radius: 4px;
		}
	}

	.attachment-uploader {
		margin-bottom: 8px;
		position: relative;
		label {
			cursor: pointer;
			padding: 5px;
			position: absolute;
			top: 0;
		}
		input {
			opacity: 0;
			&[type="file"] {
				& + label {
					@include border-style(transparent);
				}
				&:focus + label {
					border-color: $linkColor;
				}
			}
		}
	}

	.attachment-title{
		cursor: pointer;
		@include flexbox-simple(flex-start, center);

		&.attach-icon {
			padding: 5px;
			&:hover, &:active {
				background: $white !important;
			}
		}

		svg:not(.attachment-arrow) {
			margin-right: 4px;
			@include flexbox;
		}

		.attachment-arrow{
			fill: $linkColor;
		}

		&.read-only{
			color: $app-form-label-color !important;
			cursor: default;
			svg{
				fill: $app-form-label-color !important;
			}
		}

		&.extra-info {
			@include flexbox;
			svg {
				margin-top: 2px;
			}
		}

		.attach-helptext {
			color: $linkColor;
			padding-left: 6px;

			.attach-help-size {
				color: $app-form-label-color;
			}
		}
	}

	.attachment-list {
		font-size: 12px;
		@include flexbox();
		@include flex-direction(column);
		&.max-size-file-list {
			margin-top: 15px;
			.cumulative-size-error {
				.size-error-text {
					font-size: 12px;
					color: $app-red-1;
				}
			}
		}
		.list-file-item {
			@extend .element-inline;
			width: 60%;
			border-radius: 4px;
			height: 35px;
			border: 1px solid $app-border-primary;
			box-sizing: border-box;
			background: $white;
			display: inline-flex;
			margin-top: 8px;
			padding: 5px 0 5px 10px;
			align-items: center;
			&:hover{
				background: $attachmentCardHover;
			}

			.file{
				@extend .ellipsis;
				padding-bottom: 4px;
				padding-top: 2px;
				display: inline-block;
				position: relative;
				top: 3px;
				&.local {
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
					max-width: 75%;
				}
				.file-name{
					color: $primary-text-color-1;
					padding-top: 2px;
					font-size: 12px;
				}
			}

			.file-info{
				margin: 0 10px;
				width: 75%;
			}

			.file-size{
				font-size: 12px;
				color: $secondary-text-color-1;
				display: inline-block;
				position: relative;
				top: -6px;
			}

			.file-limit-error{
				font-size: 12px;
				color: $attach-error;
			}

			.file-icon{
				width: 18px;
				height: 18px;
				margin-top: 4px;
			}

			.delete-file {
				opacity: 0;
				cursor: pointer;
				margin-left: auto;
				margin-right: 5px;
				display: flex;
				&.cross-svg {
					opacity: 1;
					svg {
						width: 20px;
						height: 20px;
						fill: $secondary-text-color-1;
					}
				}
			}


			.retry-upload {
				font-size: 12px;
				font-weight: 600;
				cursor: pointer;
				width: 25%;
				text-align: right;
				span {
					color: $attach-error;
				}
				span:last-child {
					color: $linkColor;
				}
			}

			.cancel-button {
				padding: 5px 6px 0;
				width: 10%;
				text-align: right;
				.cancel-upload {
					svg{
						width: 16px;
						height: 16px;
						fill:	$disabled-text-color
					}
				}
			}



			.ember-loader {
				display: flex;
			}

			.ember-loader,
			.retry-upload,
			.cancel-upload {
				display: none;
			}

			&.limit-error,
			&.failed {
				color: $attach-error;
				border-color: $attach-error !important;
			}

			&:hover.uploaded.upload-on-progress {
				.delete-file {
					display: none;
				}
			}

			&:hover.uploaded,
			&:hover.limit-error {
				.delete-file {
					opacity: 1;
				}
			}

			&.uploaded .delete-file:focus {
				opacity: 1;
			}

			&.failed {
				.retry-upload {
					@extend .element-inline;
				}
				.delete-file {
					display: none;
				}
			}

			&.upload-on-progress {
				.spin-loader {
					text-align: right;
					width: 6%;
					padding-top: 5px;
					.ember-loader {
						@extend .element-inline;
						fill: $linkColor;
						width: 24px;
						height: 24px;
					}
				}

				.delete-file{
					display: none;
				}

				&.limit-error .delete-file,
				&.failed .delete-file{
					@extend .element-inline;
				}


				.cancel-upload{
					@extend .element-inline;
				}

				.file-name,
				.file-size {
					color: $disabled-text-color;
				}
			}

			&.cloud-file {
				.file {
					width: 100%;
					.file-info {
						color: $linkColor;
						vertical-align: bottom;
					}
					.file-name {
						white-space: nowrap;
						overflow: hidden;
						@extend .ellipsis;
						max-width: 75%;
						display: inline-block;
						vertical-align: middle;
					}
				}
			}

			&.new-attachment {
				background: $background-color-2;
				border: 1px dashed $border-dark-color;
			}
		}
	}

	.field-required-error {
		padding: 8px 6px;
	}

	.file-upload-indicator{
		width:200px;
		height:4px;
		background:linear-gradient(to right,$linkColor,$linkColor);
		background-color:#ccc;
		border-bottom-left-radius:4px;
		border-bottom-right-radius:4px;
		background-size:0%;
		background-repeat:repeat-y;
		background-position:0 0;
		animation:upload 4s ease-in-out 1 normal forwards;
		position: absolute;
		margin-top: 43px;
		// margin-top: 51px;
		// width: 380px;
		margin-left: -1px;
	}
	@keyframes upload{
		10%{background-size:10%;background-position:0 0;}
		30%{background-size:30%;background-position:0 0;}
		50%{background-size:50%;background-position:0 0;}
		75%{background-size:75%;background-position:0 0;}
		100%{background-size:95%;background-position:0 0;}
	}
}


.ember-modal-dialog.ember-modal-side-right .attach-files .attachment-list .list-file-item.new-attachment {
	width: 100%;
}