.ui-accordion-header-trigger {
	width: 100%;
	padding: 0;
	margin: 0;
	border: none;
	font-family: inherit;
	font-size: 100%;
	text-align: left;
	overflow: visible;
	text-transform: none;
	background: transparent;
	outline: none;
}

.ui-accordion-header-trigger:active {
	color: inherit;
}

.ui-accordion-panel-wrapper {
	overflow: hidden;
	visibility: hidden;
	will-change: height;
	transition: height 200ms ease-in-out, visibility 100ms ease-in-out; // the difference in ms is to hide the content asap when collapsing to avoid visual glitch
}

.ui-accordion-item-expanded .ui-accordion-panel-wrapper {
	visibility: visible;
}

.sortable-item {
	transition: all 0.2s;

	&.is-dragging {
		transition-duration: 0s;
	}
}

.chevron-arrow {
	fill: $app-icon-color;
}
