.multitab-navbar {
	width: 100%;
	height: 42px;
	background-color: $app-light-bg;
	box-shadow: 0px 10px 10px 0px $box-shadow-color-4;

	button {
		margin: 0;
		background-color: transparent;
		border: none;
	}

	.multitab-navlist {
		margin: 0;
		padding: 0;
		margin-top: 1px;
		padding-left: 16px;
		@include flexbox-simple(normal, center);
		list-style: none;
		@include transition(all 0.3s ease);

		.content-count {
			background-color: $app-light-bg;
			padding: 3px 9px;
			margin-left: 4px;
			border: 1px solid $border-light-color;
			box-sizing: border-box;
			box-shadow: 0px 2px 4px 0px $list-item-shadow;
			border-radius: 20px;
			font-weight: 400;
			color: $tab-icon-color;
			font-size: 12px;
			line-height: 12px;
			height: 20px;
		}

		.multitab-navitem {
			cursor: pointer;
			display: inline-block;
			padding: 10px 16px;

			> .multitab-navlink {
				text-decoration: none;
				color: $secondary-text-color-1;
				font-weight: $text-weight-semi-bold;
				line-height: 20px;
				@include flexbox-simple(space-between, center);

				> .content-wrapper {
					@include flexbox-simple(space-between, center);
				}
			}

			&.active:not(.multitab-more-btn) {
				> .multitab-navlink {
					color: $linkColor;
					> .content-wrapper {
						> .content-count {
							background-color: $linkColor;
							color: $app-light-bg;
							border-color: $linkColor;
							box-shadow: none;
						}
					}
				}
			}

			> .multitab-more-btn {
				padding: 10px 16px;
				margin: 0;
				background-color: transparent;
				border: none;
				&:hover {
					cursor: pointer;
				}
				> .more-tabs-text {
					margin-top: -4px;
					> .chevron-down {
						width: 18px;
						position: relative;
						top: 5px;
						fill: $secondary-text-color-1;
						@include transition(transform 0.25s ease-in-out);
					}
				}

				&.active {
					color: $linkColor;
					.chevron-down {
						fill: $linkColor;
						transform: rotate(180deg);
					}
				}
			}

			> .active-indicator {
				visibility: hidden;
				position: relative;
				width: calc(100% + 2px);
				height: 2px;
				border-radius: 4px;
				border: none;
				border: 1px solid transparent;
				top: 7px;
				left: -1px;
				&.active {
					top: 9px;
					visibility: visible;
					border: none;
					background: $linkColor !important;
					// border-color: $linkColor !important;
				}
			}

			&:hover {
				> .active-indicator {
					top: 9px;
					visibility: visible;
					border-color: $border-dark-color;
				}
				> .multitab-navlink {
					color: $app-grey;
				}
			}

			.pending-notification {
				width: 10px;
				height: 10px;
				margin-left: -8px;
				margin-bottom: 10px;
				border-radius: 50%;
				background: $app-red;
				border: 1px solid $disabled-button-color;
			}
		}

		.multitab-more-container {
			display: inline-flex;
			padding: 0;
			margin: 0 !important;
		}
	}
	.multitab-morelist {
		list-style: none;
		display: block;
		margin: 44px 0 0 16px;
		position: absolute;
		width: 150px;
		box-shadow: 0px 2px 4px $box-shadow-color-1, 0px 2px 16px $box-shadow-color-2;
		background-color: $app-light-bg;
		padding: 9px 8px;
		border-radius: 4px;
		z-index: 1000;

		> .multitab-moreitem {
			cursor: pointer;
			padding: 9px 8px;

			&:focus-visible {
				outline: auto -webkit-focus-ring-color;
			}

			> .multitab-morelink {
				@include flexbox-simple(space-between, center);
				text-decoration: none;
				color: $primary-text-color-1;
				font-size: $title-small;
				font-weight: $text-weight-normal;
				line-height: 21px;

				> .content-wrapper {
					display: flex;

					> .content-count {
						float: right;
					}
				}
			}

			&:hover,
			&:focus {
				background: $dropdown-hover;
				border-radius: 4px;
			}

			.pending-notification {
				width: 8px;
				height: 8px;
				margin-left: -8px;
				border-radius: 50%;
				background: $app-red;
				border: 1px solid $disabled-button-color;
			}
		}

		&.hide {
			display: none;
		}
	}
}
.pane-content {
	background-color: $white;
	padding: 0 34px 32px 34px;
	height: 100%;
	.tab-pane-content {
		display: none;
		&.selected {
			display: block;
			height: 100%;
		}
	}
}

.multitab-navbar-wrapper {
	&.vertical-navbar {
		@include flexbox;
		padding: 16px;
		.multitab-navbar {
			height: calc(100% - 16px);
			min-width: 280px;
			background: $background-color-2;
			border-radius: 8px;
			border: 1px solid transparent;
			flex: 3;
			box-shadow: none;
			.multitab-navlist {
				@include flexbox-simple(flex-start, normal);
				flex-direction: column;
				padding: 0;
				margin: 16px;
			}
			.multitab-navitem {
				text-align: left;
				padding: 12px 16px;
				margin-bottom: 4px;
				width: 95%;
				border-left: 4px solid transparent;
				.multitab-navlink {
					@include flexbox;
					align-items: center;
					position: relative;
					.chevron-right {
						right: -5px;
						position: absolute;
						fill: $secondary-text-color-2;
					}
				}
				&.active {
					background: $white;
					border-radius: $radius;
					border-left: 4px solid $linkColor;
					.multitab-navlink {
						color: $primary-text-color-1 !important;
					}
				}
			}
		}
		.pane-content {
			height: 100%;
			flex: 7;
			padding-right: 0;
		}
	}
}
