.entity-search-input-wrapper {
	width: 100%;
	position: relative;

	.ember-basic-dropdown-trigger {
		display: inline-flex;
		align-items: center;
		width: 100%;
		margin-left: 8px;
		box-sizing: border-box;

		svg {
			position: absolute;
			left: 20px;
			top: 8px;
			fill: $disabled-text-color;
		}

		.ember-form {
			width: calc(100% - 8px);
			.form-field {
				input {
					padding-left: 32px !important;
					padding-right: 64px !important;
					height: 32px;
					::-ms-clear { 
						display: none; width : 0; height: 0; 
					}
				}
			}
		}

		.entity-search-input-clear, .ember-loader {
			position: absolute;
			right: 10px;
			font-size: $text-normal;
		}

		.ember-loader {
			width: 20px;
			height: 20px;
			left: initial;
			right: 0;
		}

		.entity-search-input-clear {
			padding: 0;
		}
	}

	.ember-basic-dropdown-content {
		width: calc(100% - 8px);
		margin-left: 8px;
		max-height: 300px;
		overflow-y: scroll;
		padding: 16px;
	}
}
