$border-bottom: #CFD7DF;

.ui-list-filter {
	.filter-header {
		padding: 16px;
		font-weight: $text-weight-semi-bold !important;
		background: $app-white;
		box-shadow: 0px 2px 4px rgba(22, 29, 37, 0.05);
		margin-bottom: 5px;
		.title {
			font-size: $title-normal;
			display: inline-block;
		}
		.btn-reset-filter {
			float: right;
			&:focus {
				outline: none;
			}
		}
	}

	.filter-row {
		margin-bottom: 8px;
		padding: 0 16px;
		.title-small {
			padding-top: 8px;
			padding-bottom: 5px;
			text-transform: lowercase;

			&:first-letter {
				text-transform: uppercase;
			}
		}
		.ember-basic-dropdown {
			.ember-power-select-trigger {
				box-sizing: border-box;

				.ember-power-select-status-icon {
					position: absolute !important;
					left: auto;
					right: 4px;
				}

				.ember-power-select-trigger-multiple-input {
					padding: 0px 0px 0px 8px;
					&:focus {
						border: none;
					}
				}
			}

			.ember-power-select-single-list > .ember-power-select-option[aria-selected="true"] { color: $linkColor; }

			.ember-basic-dropdown-content .ember-power-select-options { padding: 0; }

			// hide chevron for multi select
			.ember-power-select-multiple-trigger .ember-power-select-status-icon { display: none; }

			// hide already selected items from dropdown
			.ember-power-select-multiple-list > .ember-power-select-option[aria-selected="true"] { display: none; }
		}
		.ember-basic-checkbox {
			margin-top: 16px;
		}
		.combo-filter-field {
			@include flex-center;
			border: 1px solid $app-border-primary;
			border-radius: 4px;

			.ember-basic-dropdown {
				border-radius: 0;
				border-right: 1px solid $app-border-primary;
				width: 60%;
	
				.ember-power-select-trigger {
					@include flexbox;
					@include align-items(center);
					border: unset !important;
				}
	
				.ember-power-select-status-icon {
					display: none;
				}
			}
	
			.ember-text-field {
				width: 40%;
				box-shadow: none;
				border: hidden;
			}
	
		}
	}

	.btns-wrapper {
		padding: 0 16px;
		margin-top: 20px;
		height: 65px;
		background: $app-white;
		box-shadow: 0px -2px 4px rgba(22, 29, 37, 0.05);

		box-sizing: border-box;
		position: sticky;
		bottom: 0;
		right: 0;
		width: 100%;

		.btn {
			float: right;
			border-radius: 3px;
			margin-left: 0;
			margin-top: 10px;
			outline: none;

			&-primary {
				margin-right: 0;
				width: 100%;
				margin-top: 16px;
			}
		}
	}
}
