.star-rating-answer {
	> .star-container {
		display: inline-flex;
		flex-direction: column;
		align-items: center;
		margin-right: 16px;

		.star {
			width: 24px;
			height: 24px;
		}

		.star-value {
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
			margin-top: 4px;
		}

		&.readonly {
			.star path {
				fill: $app-border-primary;
			}

			.star-value {
				color: $disabled-text-color;
			}
		}

		&.selected {
			.star path {
				fill: $linkColor;
			}

			.star-value {
				color: $linkColor;
			}
		}
	}
}
