

/* --- START : Multi value / Multi select Input field --- */
.fs-multiple-options-content {
	background-color: $app-white;
	border: 1px solid $primary-border;
	border-radius: 4px;
	padding: 0 2px;
	box-sizing: border-box;
	min-height: 32px;
	display: flex;
	&:hover {
		border: 1px solid $inputHoverBorder;
	}
	
	&.focus {
		border-color: $inputFocusBorder;
		box-shadow: 0 0 0 1px $inputFocusBorder;
	}

	.fs-multiple-options {
		padding: 0;
		margin: 0;
		cursor: pointer;
		max-height: 160px;
		overflow-x: auto;

		.fs-multiple-option {
			display: flex;
			background-color: $dropdown-hover;
			border-radius: 4px;
			margin: 3px 2px;
			padding: 0px 6px;
			align-items: center;
			line-height: 26px;
			float: left;
			word-break: break-word;
			&:focus {
				outline: none;
				background: $btnBackground;
				border-color: $inputFocusBorder;
				box-shadow: 0 0 0 1px $inputFocusBorder;
			}

			&.invalid {
				border: 1px solid $btnDangerBorder;
			}
			
			&.has-invalid-icon {
				border: 1px solid $svg-orange;
			}

			.fs-multiple-option-text {
				line-height: 26px;
				font-size: 14px;
				font-weight: 500;
				color: $primary-text-color-1;
			}
			.fs-error-icon { 
				@include flexbox;
				margin-left: 8px;
				
				svg {
					width: 16px;
					path {
						fill: $app-error-state;
					}
				}
			}
			.fs-multiple-option-remove-btn {
				@extend .element-inline;
				position: relative;
				text-align: center;
				font-weight: 500;
				font-size: 20px;
				margin: 0 0 0 6px;
				width: 20px;
				line-height: 26px;
				background: none;
				border: none;
				padding: 0px;
				cursor: pointer;

				&:focus {
					outline: none;
					background: $btnBackground;
					border-radius: 2px;
					border-color: $inputFocusBorder;
					box-shadow: 0 0 0 1px $inputFocusBorder;
				}

				&:not(:hover) {
					opacity: .5;
				}
			}
		}

		.fs-multiple-input-field {
			display: inline-flex;
			float:left;

			input {
				border: none;
				width: auto;
				min-width: 200px;
				box-shadow: none;
				padding: 6px;

				&:focus {
					border: none !important;
					box-shadow: none !important;
				}	
			}
		}
	}
}
/* --- END : Multi value / Multi select Input field --- */
