:lang(ar), :lang(he) {
	.fr-element {
		direction: ltr;
	}

	.fr-quick-insert {
		display: none;
	}

	.fr-tooltip {
		left: inherit;
	}

	// adding this css to fix [FD-33232]-Curosr and the typed text appears from left for RTL languages
	.fr-box.fr-basic.fr-rtl .fr-element {
		text-align: left;
	}

	.fr-popup {
		direction: ltr !important;

		.fr-color-set {
			min-width: 240px;
		}

		&.fr-rtl {
			right: 80px !important;
		}
	}

}

html[dir="rtl"], body[dir="rtl"] {
	.ember-light-table table {
		max-width: initial;
	}

	.ember-breadcrumbs{
		> .chevron-right {
			transform: rotate(-180deg);
		}
	}

	.chevron-left, 
	.chevron-right {
		transform: rotate(-180deg);
	}

	.announcement-banner {
		&::before{
			transform: scaleX(-1);
		}
	}

	.review-wrapper {
		&::before{
			transform: scaleX(-1);
		}
	}

	.ember-power-calendar-weekday {
		font-size: 12px !important;
	}

	// ticket show page adj tkts nav
	.btn-group.navigate-items {
		> .btn:first-child { margin-left: 8px; }
		> .btn + .btn  { margin-left: 0px; }
	}
}
