.ember-alert {
	height: 40px;
	@include flexbox-simple(flex-start, center);
	padding: 0 10px;
	margin: 10px 0;
	border-radius: 4px;
	box-sizing: border-box;
	background-color: $app-grey-bg;

	&-warning {
		border: 1px solid $alertPartialBorder;
		background-color: $app-casablanca-light;

		.alert-content{
			&::before {
				border-left: 1px solid $alertPartialBorder;
			}
		}
	}

	&-info {
		border: 1px solid $border-light-blue;
		background-color: $section-background-blue;
		box-shadow: 0 2px 3px rgba(0, 0, 0, 0.06);
		.alert-content{
			&::before {
				border-left: 1px solid $border-light-blue;
			}
		}
	}

	svg {
		fill: $app-warning-state;
		width: 16px;
		height: 16px;
		margin-right: 10px;
	}

	p, .alert-content {
		padding-left: 10px;
		color: $primary-text-color-1;
		position: relative;
		margin-bottom: 0;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			height: 100%;
		}
	}
}
