@import '../../ui-components/shimmer/shimmer-fast';
@import '../../modules/dashboard/shimmer-loader';

$menuLinkColor     : #2C5CC5;
$menuAppGreen      : #00A886;
$menuLightGreen    : #E0F5F1;
$menuMutedColor    : #ADAFB5;
$shimmerBackground : #EBEFF3;
$dropMenuBgColor   : #E5F2FD;
$disabled-text-color: #92A2B1;

.burger-menu-container .menu-list-container {
	.new-dashboard, .menuitem.new-dashboard {
		display: flex;
		align-items: center;
		padding-bottom: 4px;
		transition: 0.1s ease-in;
		color: $menuLinkColor;

		.plus {
			margin-right: 8px;
		}

		&.disabled {
			border: 0;
			background: none;
			opacity: 0.6;

			.green-plus {
				opacity: 0.5;
			}
		}
	}

	.sub-text {
		font-size: 12px;
		padding: 0 12px;
		color: $secondary-text-color-2;
		padding-left: 32px;
		margin: 0;
	}

	.search-textbox-container {
		position: relative;

		svg {
			width: 20px;
			height: 20px;
			position: absolute;
			top: 50%;
			left: 12px;
			transform: translateY(-50%);
		}

		input#dashboard-list-search {
			height: 38px;
			padding: 8px 4px 8px 36px;

			&:focus {
				box-shadow: 0 0 0 1px $inputFocusBorder;
			}

			&:hover {
				background: $app-white;
			}
		}
	}
}

.dashboard-menu-list-container {
	.page-heading {
		color: $primary-text-color-1;

		svg {
			fill: $primary-text-color-1;
		}
	}
}

.burger-menu-container.dashboard-menu-list-container .menu-list-container {
	.menuitem {
		padding: 6px 10px;
		margin: 8px 0;

		&.active {
			font-weight: $text-weight-medium;
		}
	}

	.default-dashboard {
		margin: 20px 0 10px;

		&+ span.seperator {
			margin-bottom: 14px;
		}
	}

	.new-dashboard {
		margin: 2px 0;
	}
}

.dashboard-shimmer {
	height: 100%;
	display: flex;

	.shimmer-loader-container {
		justify-content: normal;

		.shimmer-loader-item-fast {
			flex: none;
			height: 18px;
			min-width: 48px;
			border-radius: 50px;

			&.line-short {
				width: 40px;
			}

			&.line-medium {
				width: 280px;
			}
		}
	}
}

.dashboard-dropdown-menu  {
	position: relative;
	margin-left: 16px;

	.ember-basic-dropdown-trigger {
		padding: 8px;
		width: 32px;
		height: 32px;
		display: flex;
		align-items: center;

		svg {
			height: 16px;
		}

		&[aria-disabled=true] {
			cursor: none;
		}
	}

	.ember-basic-dropdown-content {
		margin-top: 5px;
		position: absolute;
		box-sizing: border-box;
		border: 1px solid #ebeff3;
		border-radius: 4px;
		background-color: #fff;
		padding: 8px;
		box-shadow: 0 2px 4px 0 rgba(18, 52, 77, 0.06), 0 2px 18px 0 rgba(18, 52,77, .16);

		// &.ember-basic-dropdown-content--right {
		// 	right: 0;
		// }

		[role="menuitem"] {
			width: 100%;
			padding: 6px;
			box-sizing: border-box;
			border-radius: 4px;
			color: inherit;
			display: block;
			text-align: left;
			border: none;
			margin: 2px 0;
			background: none;
			cursor: pointer;

			svg {
				width: 16px;
				height: 16px;
				padding: 0;
				margin-right: 8px;
			}

			svg, span {
				display: inline-block;
				vertical-align: middle;
			}

			&:hover {
				background: $dropMenuBgColor;
			}

			&.disabled {
				pointer-events: none;
				cursor: default;
				color: $disabled-text-color;

				svg {
					fill: $disabled-text-color;
				}
			}
		}
	}
}

