// Common to both IE 10+, Edge 12+
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	@supports (-ms-accelerator:true) or (-ms-ime-align:auto) {

		// Module : AMS admin
		// Source selection page mini cards - 2 liner description in other browsers, single line ellipsis in IE10+
		.ember-modal-dialog.full-page-modal.source-selection-modal {
			.mini-card .content {
				.desc {
					display: block !important;
					text-overflow: ellipsis !important;
					white-space: nowrap !important;
					overflow: hidden !important;
				}
			}
		}
	}
}
