.priority-list-wrapper {
	.ember-basic-dropdown-content {
		position: fixed !important;
	}
	.priority-list-trigger {
		max-width: 100%;
		min-width: 70px;
		height: 32px;
		border-radius: 4px;
		display: inline-block;
		padding: 4px 8px;
		margin-top: 1px;
		cursor: pointer;

		&:focus {
			outline: none;
			box-shadow: 0 0 0 2px $inputFocusBorder;
		}

		&:hover,  &[aria-expanded] {
			background: $white;
			box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.05);
		}

		&:hover, &:focus, &[aria-expanded] {
			.dropdown-chevron {
				fill: $primary-text-color-1;
			}
		}

		.dropdown-chevron {
			width: 14px;
			height: 20px;
			float: right;
			position: relative;
			left: 2px;
			top: 2px;
			fill: $disabled-text-color;
		}
	}

	.priority-result {
		max-width: calc(100% - 14px);
		display: inline-block;
		vertical-align: middle;
	}

	.priority-color {
		float: left;
		height: 8px;
		width: 8px;
		border-radius: 2px;
		margin-left: 0;
		margin-top: 7px;

		&.priority {
			&-1 {
				background:	$priorityLowColor;
			}
			&-2 {
				background:	$priorityMediumColor;
			}
			&-3 {
				background:	$priorityHighColor;
			}
			&-4 {
				background:	$priorityUrgentColor;
			}
		}
	}

	.priority-dropdown-content {
		width: 158px;
		border: none;
		padding: 0px;
		z-index: 1000 !important;
		margin-top: 13px !important;

		.priority-option {
			cursor: pointer;
			margin: 6px 0;
			word-break: break-word;
			min-height: 32px;
			font-family: inherit;
			font-weight: $text-weight-normal !important;

			.priority-color {
				margin-right: 8px;
				margin-top: 6px;
			}

			.priority-text {
				max-width: 78%;
				display: inline-block;
			}
		}
	}
}
