$std-pad: 32px;

.module-view-query-form {
	width: 300px;
	font-family: inherit;

	// Fade
	&.is-loading {
		@include fade-loader;
	}

	&-header {
		position: fixed;
		top: 65px;
		right: 0;
		z-index: 9;
		background: $background-color-2;
		width: 300px;
		border-left: 1px solid $border-dark-color;
		box-sizing: border-box;
		display: flex;
		justify-content: space-between;
		align-items: center;
		h2 {
			font-size: $title-normal;
			font-weight: $text-weight-semi-bold;
			padding: 20px 24px;
		}
		#view-query-form-reset {
			display: flex;
			align-items: center;
			font-weight: $text-weight-semi-bold;
			span {
				margin: 0 16px 0 4px;
			}
			svg {
				fill: $linkColor;
			}
		}
	}

	&-wrapper {
		width: 300px;
	}

	&-search {
		position: fixed;
		top: 120px;
		right: 0;
		z-index: 9;
		.form-search-field {
			.form-search-field-icon svg {
				fill: $secondary-text-color-1;
			}
			input::placeholder {
				font-family: inherit !important;
			}
		}
	}

	form {
		.fieldset-wrapper {
			padding: 10px 24px;
			margin-top: 113px;
			height: calc(100% - 238px);
			overflow-y: auto;
			position: fixed;
			width: 300px;
		}
	}

	&-footer {
		position: fixed;
		bottom: 0;
		right: 0;
		z-index: 9;

		width: 300px;
		height: 60px;
		@extend .flex-center;
		background: $background-color-2;
		border-top: 1px solid $border-dark-color;
		border-left: 1px solid $border-dark-color;
		box-sizing: border-box;
		padding: 0 24px;
		button {
			width: 100%;
			margin-right: 0;
		}
	}
}

.calendar-open {
	.module-view-query-form-header, .module-view-query-form-search, & + .module-view-query-form-footer {
		z-index: -1 !important;
	}
}
