.fs-phone-number {
	margin-bottom: 24px;
	position: relative;
			
	.ph-type {
		margin-bottom: 8px;
		font-weight: $text-weight-semi-bold;
		color: $app-form-label-color;
	}
		
	.ph-container {
		@include flexbox;
		
		.ember-power-select-option{
			@include flexbox;
			@include align-items(center);
			
			.country-code {
				margin-left: 16px;
			}
			.country-ext {
				color: $disabled-text-color;
				margin-left: 12px;
			}
			.country-flag:empty{
				border: 1px solid $inputDisabledBorder;
				background-color: $inputDisabledBackground;
				width: 20px;
				height: 16px;
			}
		}
		
		.ember-basic-dropdown {
			margin-right: 4px;
		}
			
		.ember-power-select-dropdown {
			width: 200px;
		}
			
		.ember-text-field {
			width: 156px;
		}
		
		.error-state-border {
			border: 1px solid $app-red;
		}
		
		.ember-power-select-selected-item {
			.country-flag,.country-code{
				display: none;
			}
			.country-ext {
				margin-left: 0px;
				color: $app-grey;
			}
		}
	}	
	.error-text {
		margin-top: 4px;
		@include font-size($text-normal);
		color: $app-error-state;
		position: absolute;
		left: 74px;
		width: 172px;
		b {
			font-weight: $text-weight-semi-bold;
		}
	}
}
