// Variables
$table-header-row-height: 40px;
$table-body-row-height: 56px;
$table-body-row-height-compact: 40px;

$scroll-indicator--right-shadow: -4px 2px 4px
	rgba(
		$color: $app-black,
		$alpha: 0.03,
	);

@mixin pseudo-border($dir) {
	content: " ";
	position: absolute;
	right: 0;
	top: 0;
	height: 100%;
	width: 100%;
	box-sizing: border-box;
	box-shadow: 2px 6px 4px rgba(0, 0, 0, 0.08);
	border-#{$dir}: 1px solid $tableColumnSeparator;
}

// Top level wrapper class
.ember-table-wrapper {
	margin-bottom: 70px;
	position: relative;

	// Applies when the first column is not a check box column (view only priv)
	// This class gets added by the 'noRowSelectable = true'
	&.no-row-selection {
		.table-options {
			.left-section {
				.table-sort-default-dropdown {
					margin-left: -8px; // adjust for selectAll checkbox absence
				}
			}
		}
	}

	.is-loading {
		// setting opacity fade on immediate child levels, instead of top level, helps keep the blue spinner at 100% opacity
		> * { @include fade-loader; }
		position: relative;
	}
}

// Table options
.ember-table-wrapper > .table-options {
	top: 64px; // initial value, dynamic value set later in code based on a window event
	position: fixed;
	height: 60px;
	z-index: 999;
	margin: 0 0 0 -28px;
	padding: 0 32px 0 30px;
	box-sizing: border-box;
	background-color: $app-white;
	right: auto;
	@include flexbox-simple(space-between, center);

	.btn {
		background: $white;
		&:hover {
			background: $background-color-2;
		}
	}

	.left-section {
		&.bulk-action-section {
			.select-all-btn {
				margin-right: 8px;
				margin-left: 3px;
				input[type="checkbox"] {
					margin-top: -2px;
					margin-right: 8px !important;
				}
				.ui-component-check-box {
					&:after {
						// separator
						content: " ";
						width: 1px;
						height: 24px;
						background-color: $border-light-color;
						display: inline-block;
						margin: -6px 0px -6px 12px;
					}
					input[type='checkbox']:indeterminate {
						background: $inputColor;
						border-color: $inputColor;
					}
				}
				&.has-selection {
					.ui-component-check-box:after {
						width: 0;
					}
				}
			}

			.table-sort-default-dropdown {
				@include flex-center;
				cursor: pointer;
				.ember-basic-dropdown-trigger {
					padding: 8px;
					.table-sort-dropdown-title {
						@include flex-center;
						b {
							font-weight: $text-weight-semi-bold;
							padding-left: 4px;
						}
						svg {
							vertical-align: middle;
							fill: $app-icon-color;
						}
					}
				}
				.ember-basic-dropdown-content {
					min-width: 200px;
					margin-top: 12px;

					.basic-dropdown-item.active {
						margin-right: 16px;
					}
				}
			}
		}
	}

	.left-section,
	.right-section {
		@include flex-center;
		.shimmer-loader {
			width: 300px;
			margin-left: -8px;
		}
	}

	.right-section {
		.table-export-btn {
			margin-right: 8px;
			svg {
				width: 16px;
				height: 16px;
				fill: $app-form-label-color;
			}
			&:after {
				font-family: inherit;
			}
		}
		.ember-pagination {
			height: auto;
			padding-top: 0; /* IE fix */
			padding-top: unset;
			margin: unset;
			#top-pagination-prev:focus {
				// margin-right: 1px;
				border-right-width: 1px;
				&.disabled {
					border-right-width: 0px !important;
				}
			}
		}
	}
}

// Table Common Styles (Applies for both preferences: default & compact)
.ember-table-wrap {
	position: relative;
	margin-top: 44px;
	margin-left: -7px; // adjust left
	width: calc(100% + 7px); // adjust width for negative left indent

	// Show blue spinner only on table section fade, not in right filter or in table option sections
	&.is-loading {
		&:after {
			content: '';
			position: absolute;
			top: 170px;
			left: 50%;
			@include blue-spinner;
		}
	}

	&.has-info-banner {
		.ember-table .scroll-indicator__right {
			top: 57px; // adjust table right scroll indicator when banner appears
		}
	}

	&.default-checkbox-selection {
		.ember-table {
			thead tr th:first-child {
				padding-right: 16px;
				border-right: 1px solid $tableColumnSeparator;
				border-left: none;
			}
			tbody tr td:first-child {
				padding-right: 16px;
				.et-toggle-select {
					padding-right: 16px;
				}
			}
		}
	}

	#info-banner {
		margin-bottom: 16px;
	}

	// Ember Table
	.ember-table {
		position: initial !important;
		width: 100% !important;
		height: auto !important;
		@extend .overflow-y-auto; // assign-to breakage default hidden
		border-bottom: 1px solid $border-light-color;

		// intentional override for marking as important
		&.overflow-x-hidden {
			overflow-x: hidden !important;
		}

		&:not(.overflow-x-hidden) {
			@extend .overflow-x-auto;
		}

		//assign-to breakage added new lines her
		.ember-table-overflow {
			@extend .overflow-visible;
		}

		table {
			border: none;
			border-radius: 4px 4px 0 0;
			box-shadow: none;
			background: $app-white;
			border-collapse: collapse;
			overflow: visible;
			width: 100% !important;
			min-height: 80px; // 1 header + 1 row

			// Adding this breaks all table dropdowns
			// -webkit-transform: translate3d(0,0,0); // null transform hack to improve render perf.
			// transform: translate3d(0,0,0);

			thead {
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
				z-index: 99;
				tr {
					height: $table-header-row-height;
					th {
						background-color: $background-color !important;
						border-bottom: none;
						font-size: $baseFontSize;
						font-weight: $text-weight-semi-bold;
						padding: 8px 16px;
						text-align: left !important; // TODO: Configurable via code
						text-transform: unset;
						// will-change: transform; // Hint to browsers to set up optimizations before the element is actually changed
						border-right: none;
						border-left: none;
						box-sizing: border-box;
						vertical-align: middle;
						user-select: auto !important;

						h3 {
							display: inline-block;
						}

						&:first-child {
							border-radius: 4px 0 0 4px;
						}

						&:last-of-type {
							border-radius: 0 4px 0 0;
							padding-right: 32px; // extra padding-right because of the col.customizer icon
							&:after {
								display: none;
							}
						}

						&.is-fixed-left {
							// Fixed columns - Except last, rest every other header column needs separator
							&:not(.draggable):not(:first-child) {
								&:before {
									@include pseudo-border(left);
								}
							}
						}

						// Non-fixed columns -  needs separator for all
						&:not(.is-fixed-left) {
							&:after {
								@include pseudo-border(right);
							}
						}

						&.is-sortable {
							.et-sort-indicator.et-sort-toggle {
								float: right;
								width: 16px;
								height: 16px;
								padding: 0;
								margin-right: -8px;
								background: transparent;
								border: transparent;
								outline: none;
							}
						}

						&.is-reorderable {
							cursor: grab;
						}

						.et-header-resize-area {
							// resize handler
							cursor: col-resize;
							width: 12px;
							z-index: 1;
						}
					}
				}
			}

			tbody {
				tr {
					height: $table-body-row-height !important;
					@include set-content-visibility(auto);

					&.is-selected,
					&.selected {
						td {
							background: $tableRowSelected !important;
						}
					}

					&:not(.is-selected):not(.selected) {
						background-color: $app-white;

						&:hover {
							td {
								background-color: $tableRowSelected;
								&:first-child {
									border-radius: 4px 0 0 4px;
								}
							}
						}
					}

					td {
						color: $primary-text-color-1;
						font-size: $title-small;
						padding: 8px 16px;
						border: none;
						vertical-align: middle !important;

						// `border-bottom` does not work with `position:sticky`
						// Using `box-shadow` for cross-browser compatibility
						box-shadow: inset 0px 1px 0px $border-light-color;

						&.cell_check_box_item {
							padding: 0 12px !important;
							cursor: default; // Overriding the `pointer` cursor set at `tr` level, since checkbox cell will not navigate to details page on click

							label.screen-reader-only {
								position: fixed;
							}
						}

						&.is-first-column {
							input[type="checkbox"] {
								margin-bottom: 2px;
							}
						}

						.data-table-link {
							color: $app-grey;
							cursor: pointer;
							font-weight: 600;

							&:hover {
								color: $linkColor;
							}
						}
					}
					&.highlight-marker {
						position: relative;
						td {
							&.is-first-column {
								&:before {
									content: " ";
									top: 1px;
									width: 3px;
									left: 0;
									height: 100%; // to include top-bottom border lines
									position: absolute;
									border-radius: 4px 0 0 4px;
									background: $tableRowHighlightMarker;
								}
								&:not(.is-fixed-left) {
									position: relative; // since the :before content is position:absolute
								}
							}
						}
					}
				}
			}

			// Common to both header and body cells
			thead th,
			tbody td {
				// will-change: transform, left, right;

				+ .ember-tooltip.drag-handle-tooltip {
					text-transform: unset;
					max-height: 40px;
					width: auto;
					max-width: 100%;
					box-sizing: border-box;
					z-index: 9;
				}

				&.is-fixed-left {
					position: -webkit-sticky;
					position: sticky;
					background-color: $app-white;

					// Firefox specific fix :
					// - Fixed cols out of view port are visible underneath near the last columns in FF,
					// 		FF requires explicit setting of reduced z-index value than the current one (=4)
					// -`in-view` class is added for all visible fixed columns in the table, with viewport
					// - This is applicable only when there are more number of fixed columns than the viewport width
					&:not(.in-view) { z-index: 1; }

					// Right most fixed header column has box-shadow
					&.last {
						&::after {
							content: "";
							position: absolute;
							right: 0;
							top: 0;
							height: 100%;
							width: 1px;
							box-shadow: 2px 0px 4px rgba(0, 0, 0, 0.08);
							background-color: $tableColumnSeparator !important;
						}
					}

					// Drag-drop interaction turned on ?
					&[class*="drag_handle"] {
						border-bottom: none;
						padding: 0 !important; // Safari, Firefox needs this, to honor the actual width (2px)
						box-shadow: none; // Fixes overlapping lines from table cell's border-bottom

						&.draggable {
							background-color: $tableColumnSeparator !important;
							&::after {
								background-color: transparent !important;
							}
							&:not(.no-focus) {
								&:hover,
								&:focus {
									&::before {
										// blue drag handle on hover
										content: "";
										position: absolute;
										height: 100%;
										width: 2px;
										left: 0;
										right: 0;
										top: 0;
										background-color: $app-primary !important;
										border-radius: 4px;
									}
								}
							}
						}
					}
				}
			}
		}

		.scroll-indicator {
			background: transparent;
			box-shadow: $scroll-indicator--right-shadow;
			-moz-box-shadow: $scroll-indicator--right-shadow;
			-webkit-box-shadow: $scroll-indicator--right-shadow;

			&__left {
				box-shadow: none !important;
			}
			&__right {
				right: -8px !important;
			}
			&__top,
			&__bottom {
				display: none;
			}
		}

		// Cell
		.et-cell-container {
			@include flexbox;
			.et-cell-content {
				flex: 1;
				padding: 0;
			}
		}

		// Resizing case
		.ember-table-overflow.is-resizing * {
			cursor: col-resize !important;
			user-select: none;
		}
		// ----

		// Default checkbox case
		tr.is-selectable {
			cursor: pointer;
			&:hover td {
				background: $tableRowSelected;
			}
		}
		// ----

		// Sort via dropdown and not via header click
		&.sort-in-dropdown {
			thead tr th {
				.et-sort-indicator {
					display: none;
				}
			}
		}
	} // --> .ember-table

	// Default State
	#default-state-container {
		@include flex-center;
		color: $secondary-text-color-1;
		font-size: $baseFontSize;
		.default-content {
			text-align: center;
			height: 190px;
			margin-top: 150px;
		}
		.empty-state-msg {
			color: $app-form-label-color;
			font-size: $title-normal;
			margin-bottom: 20px;
			margin-top: 16px;
		}
		a {
			font-weight: $text-weight-medium;
		}
		svg {
			margin-right: 5px;
			vertical-align: middle;
		}
	}
} // --> .ember-table-wrap

.draggable {
	user-select: none;
	cursor: col-resize;
}

// Temp list on drag-drop freeze column interaction
.clone-list {
	width: 100%;
	display: flex;
	overflow: auto;
	.draggable {
		.clone-table {
			display: block;
			border-spacing: 0px;
			th {
				vertical-align: middle;
				padding: 8px 16px;
				box-sizing: border-box;
				&.dh { padding: 0 !important; }
			}
		}
	}
	.dragging {
		z-index: 998;
		height: 100%;
		cursor: col-resize;
		background: $border-dark-color !important;
	}
	.placeholder {
		top: 0;
		z-index: 9;
		position: relative;
		opacity: 1 !important;
		background-color: $inputFocusBorder !important;
	}
}

// (TABLE VIEW PREFERENCE: COMPACT VIEW)
.ember-table-wrapper > .ember-table-wrap.compact {
	.ember-table {
		tbody {
			tr,
			tr td {
				height: $table-body-row-height-compact !important;
				padding: 0 16px;
			}
			tr.highlight-marker {
				position: relative;
				td.is-first-column {
					&:before {
						height: 39px; // to include top-bottom border lines
					}
				}
			}
		}
	}
}

// Shimmer placement
.ember-table-wrapper > .table-shimmer {
	position: absolute;
	top: 0;
	width: 100%;
}

.disable-fixed-column {
	left: auto !important;
}

// Fixed position table-options widths
.page-layout__page-wrapper.new-layout {
	.page-content {
		.table-options {
			position: fixed; // since `fixed` position, need to include rail-nav-width for full page width calculation
			width: calc(100% - #{$global-nav-width});
		}
		&.page-content-with-sidebar {
			.table-options {
				position: fixed; // since `fixed` position, need to include rail-nav-width for full page width calculation
				width: calc(100% - #{$global-nav-width} - #{$page-sidebar-width});
			}
		}
	}
}

// #ember-body-content.ember-application.expanded --> when left nav bar is expanded, adjust table-options widths
&.expanded {
	@media (min-width: 1921px) {
		.page-layout__page-wrapper.new-layout {
			.page-content {
				.table-options {
					width: calc(100% - #{$global-nav-width-expanded});
				}
				&.page-content-with-sidebar {
					.table-options {
						width: calc(100% - #{$global-nav-width-expanded} - #{$page-sidebar-width} - 1px);
					}
				}
			}
		}
	}
}

// For safari only fix - Table column freeze issue fix
@media not all and (min-resolution: 0.001dpcm) {
	@supports (-webkit-appearance: none) {
		.ember-table-wrap {
			.ember-table table {
				display: block !important;
			}
		}
	}
}
