
// Theme vars
$card-bg: #fff;
$card-box-shadow: 0 1px 0 0 #dbdfe3;

.card {
	border-radius: 4px;
	background-color: $card-bg;
	box-sizing: border-box;
	box-shadow: $card-box-shadow;
	padding: 16px 24px;
	width: auto;
	.heading {
		font-size: 16px;
		font-weight: 600;
		line-height: 2;
		text-transform: capitalize;
	}
	.sub-details {
		display: flex;
		justify-content: space-between;
	}
	.sub-heading {
		font-size: 12px;
		font-weight: normal;
		line-height: 1.5;
		padding-bottom: 4px;
	}
	.value {
		font-size: 14px;
		font-weight: 600;
		line-height: 1;
		overflow-wrap: break-word;
	}
}
