.froala-insert-code-modal {
	z-index: 2147483650 !important;
	.ember-modal-body{
		.ember-basic-dropdown{
			width: 300px;
			margin-bottom: 16px;
		}

		.form-field textarea {
			height: 200px;
		}
	}

	.ember-modal-footer {
		.froala-delete-code-insert {
			margin-left: 8px;
			position: absolute;
			left: 24px;
		}
	}
}

// .fr-tooltip { ... } -->  Moved into 'public/src/ember/base/ember-override.scss' since it gets added directly to the <body> tag

.froala-editor-container {
	border: 2px solid transparent;
	padding: 0;
	border-radius: 6px;

	.fr-element {
		padding: 20px 0 !important;
		color: $primary-text-color-1;
	}

	.fr-placeholder {
		padding-left: 0 !important;
		color: $secondary-text-color-1;
	}

	.fr-view {
		blockquote {
			border-color: $white;
			border-left: 1px dotted $app-black;
			color: initial;
			padding-left: 10px;
		}

		/* solves image alignment issue, but cant add images next to each other */
		img.fr-dib {
			display: inline-block;
		}
	}

	[data-cmd="codeInsert"] {
		width: 40px;
		display: inline-flex;
		align-items: center;
		justify-content: center;

		svg {
			margin: 0 !important;
			width: 14px !important;
			display: inline-block;
			pointer-events: none;
		}
	}

	pre[rel="highlighter"] {
		background-color: #f0f0f0;
		padding: 5px;
		cursor: pointer;
	}

	/* override for ordered & unordered functionality to work properly */
	ul {
		list-style-type: disc;

		li {
			list-style: initial;
			list-style-type: inherit;
		}
	}

	.fr-box.fr-code-view textarea.fr-code{
		box-shadow: none !important;
	}

	.fr-btn {
		margin: 0 !important;
		border-radius: 0 !important;

		&.fr-active {
			box-shadow: inset 0 0 0 2px $linkColor;
			border-radius: 4px !important;
		}

		svg.fr-svg {
			height: 20px;
		}
	}

	.fr-popup {
		/* color picker's */
		.fr-color-set {
			& > span{
				margin: 2px;
				cursor: pointer;
				width: 22px;
				height: 22px;

				svg {
					width: 22px;
					height: 22px;
					margin: 0;
				}
			}

			&.fr-selected-set{
				padding: 8px !important;
			}
		}

		/* open a new tab */
		.fr-checkbox-line{
			margin-top: 0 !important;
			label{
				display: inline-block;
			}

			.fr-label-target-1{
				display: inline-block;
				position: absolute;
			}

			.fr-checkbox{
				padding: 0;
				margin-top: -6px;
			}
		}

		.fr-select-color {
			&.fr-active-item {
				position: relative;
				&:after {
					content: "";
					position: absolute;
					top: 50%;
					left: 10px;
					color: #fff;
				}

				&[data-param1="#FFFFFF"] {
					&:after {
						color: #000;
					}
				}
			}

			.fr-selected-color {
				display: none;
			}
		}
	}

	// Styling for standard usages with borders (new incident description, etc.,)
	.fr-toolbar {
		background-color: $button-hover-color;
		border: 1px solid $border-dark-color;
		@include svg-fill($app-icon-color);

		.fr-btn-grp {
			margin: 0 8px;
		}

		&.fr-top { border-radius: 4px 4px 0 0; }

		.fr-newline { height: 0; }
	}

	.fr-wrapper {
		border-radius: 0 0 4px 4px;
		border: 1px solid $border-dark-color;
		border-top-width: 0;
		box-sizing: content-box;

		.fr-element { margin: auto 20px; }
	}

	.fr-second-toolbar { display: none; }

	&.focus {
		border-color: $inputFocusBorder;

		.fr-toolbar {
			border-color: transparent transparent $border-dark-color transparent;
		}

		.fr-wrapper {
			border-color: $border-dark-color transparent transparent transparent;
		}
	}

	.fr-disabled {
		cursor: not-allowed;
		.fr-btn {
			pointer-events: none;
		}
	}
}


/* table background color picker */
.fr-table-colors {
	padding: 8px;

	& > span {
		margin: 2px;
		cursor: pointer;
	}
}

.froala-shortcut-helper-text {
	width: 100%;
	min-height: 33px;
	transition: 0.1s linear;
	font-size: $text-normal;
	box-sizing: border-box;
	padding: 8px;

	strong { font-weight: bold; }

	& > div {
		span:last-child {
			padding-left: 16px;
		}
	}
}
