.ember-form {

	form,
	.form-wrapper {
		outline: none;

		.form-field {
			margin: 16px 0;

			&.form-field--has-errors {

				& > input,
				& > textarea,
				input[type='checkbox'],
				.ember-basic-dropdown-trigger {
					border-color: $app-red !important;
					box-shadow: none;
				}

				&.is-mini-para .para-count {
					color: $app-error-state;
				}

				.froala-editor-container {
					border: solid 1px $app-red !important;
				}

				// common across component instances
				.calendar-dropdown-month, .calendar-dropdown-year {
					.ember-basic-dropdown-trigger {
						border-color: $border-dark-color !important;
					}
				}

				// common across component instances
				.date-range-text-field, .date-text-field {
					border-color: $app-red !important;
					box-shadow: none;
					&.focus {
						box-shadow: none;
					}
				}
			}

			input[type="search"]::-webkit-search-cancel-button,
			input[type="search"]::-webkit-search-decoration {
				-webkit-appearance: none;
			}

			&:not(.form-field--has-errors) {

				&>input,
				textarea {
					&:focus {
						border-color: $inputFocusBorder !important;
						box-shadow: 0 0 0 1px $inputFocusBorder !important;
						outline: 0;
						outline: thin dotted \9;
						/* IE6-9 */
					}

					&:hover {
						border-color: $inputHoverBorder;
					}
				}
			}

			//below codes to be removed once merged with style guide

			legend {
				margin-bottom: 0;
				border: none;
				line-height: 1;
			}

			&:not(.checkbox-field) > label, legend label {
				display: block;
				padding-bottom: 4px;
				color: $app-form-label-color;
				font-size: $text-normal;
				font-weight: 600;
				line-height: 20px;
				word-break: break-word;

				.star {
					color: $app-red;
				}

				&.hide {
					display: none !important;
				}
			}


			&>input:not([type='checkbox']):not([type='radio']) {
				width: 100%;
				height: 32px;
				font-weight: 500;
				padding: 2px 8px;
				color: $app-icon-color;
				font-size: $title-small;
				border: 1px solid $border-dark-color;
				border-radius: 4px;
				box-sizing: border-box;
				box-shadow: none;
				font-family: inherit;

				/* IE 10+ */
				&:-ms-input-placeholder{
					color: #ADAFB5;
				}

				&::placeholder {
					font-family: inherit;
				}
			}

			&>input[type='checkbox'] {
				min-width: 14px !important;
			}

			&.is-mini-para {
				position: relative;

				.para-count {
					text-align: right;
					font-size: $text-normal;
					color: $btnDisabledLightColor;
					font-weight: $text-weight-medium;
					margin-top: 4px;
					width: 100%;
					position: absolute;
				}
			}

			&>textarea {
				width: 100%;
				resize: none;
				min-height: 85px;
				font-weight: 500;
				padding: 8px;
				color: $app-icon-color;
				font-size: $title-small;
				border: 1px solid $primary-border;
				border-radius: 4px;
				box-sizing: border-box;

				/* IE 10+ */
				&:-ms-input-placeholder{
					color: #ADAFB5;
				}
			}

			&--hint {
				margin-top: 4px;
				color: $secondary-text-color-1;
				font-size: $text-normal;
				font-weight: $text-weight-medium;
			}

			&--errors {
				margin-top: 4px;
				color: $app-red;
				font-size: $text-normal;
				font-weight: 500;
			}

			&:first-child {
				margin-top: 0;
			}

			&:last-child {
				// margin-bottom: 0;
			}
		}

		.label-sub-text {
			font-size: 12px;
			color: $secondary-text-color-1;
			line-height: 1.8;
			margin-bottom: 10px;
		}

		.checkbox-field.form-field, .dependent-select-checkbox-field.form-field {
			&:not([hidden]) {
				display: flex;
				align-items: center;
			}

			margin: 4px 0;
			label {
				padding-bottom: 0;
				display: inline-flex;
				align-items: center;
				color: $app-form-label-color;
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
			}
		}

		.radio-group {
			> label {
				display: block;
			}

			.radio-field.form-field {
				display: inline-flex;
				align-items: center;
				margin: 0 8px 0 0;
				.form-field--label, label {
					padding-bottom: 0;
					cursor: pointer;
					font-size: $title-small;
				}
			}
		}

		.autocomplete-field {
			.ember-power-select-status-icon {
				display: none;
			}
		}

		// show placeholder as selected option
		.created-at-field .ember-power-select-placeholder {
			color: $primary-text-color-1 !important;
		}
	}
}

.entity-form-search-field {
	background: $background-color-2;
	position: relative;
	width: 300px;
	padding: 0 24px 24px;
	border-bottom: 1px solid $primary-border;
	border-left: 1px solid $primary-border;
	box-sizing: border-box;
	margin: 0 !important;

	input {
		padding: 0 32px !important;
		font-weight: 400 !important;
		::-ms-clear {
			display: none; width : 0; height: 0;
		}
		&:hover {
			border-color: $inputHoverBorder !important;
		}
	}

	&-icon {
		position: absolute;
		margin-left: 8px;
		top: 8px;
		svg {
			fill: $disabled-text-color;
		}
	}
	&-clear {
		position: absolute;
		right: 30px !important;
		top: 7px !important;
		background: none;
		border: none;
		height: 20px;
		width: 20px;
		padding: 0;
		line-height: 1;
		border-radius: 50%;

		svg {
			fill: $secondary-text-color-1;
		}

		&:hover {
			background: $background-color !important;
			svg {
				fill: $linkColor;
			}
		}
	}
}

/* to be used for standalone fields which doesn't have form wrappers */
.form-field--errors {
	margin-top: 8px;
	color: $app-red;
	font-size: $text-normal;
}
