.priority-field {
	.ember-power-select-selected-item {
		line-height: 1.2;
	}
	.selected-option-wrap {
		display: flex;
		align-items: center;
		height: 30px;
	}
	.priority-option-color {
		@include priority-colors('.option-1-color', '.option-2-color', '.option-3-color', '.option-4-color');
		width: 8px;
		height: 8px;
		display: inline-block;
		margin-right: 2px;
		margin-top: 1px;
		border-radius: 1px;
		&.selected {
			margin-left: 9px;
		}
	}
	.priority-value {
		margin-left: 3px;
	}
}
