.page-layout__page-wrapper {
	width: 100%;
	position: relative;

	.page_layout__title-bar {
		.page-title {
			display: flex;
			align-items: center;
			min-height: 32px;
			div:last-child {
				margin-left: auto;
			}
			.titlebar-btns{
				margin-left: auto;
				.btn:last-child{
					margin-right: 0;
				}
			}
		}

		h4 {
			font-size: $title-normal;
			font-weight: $text-weight-semi-bold;
		}

		.sticky-element {
			padding: 8px 16px;
			background: $app-grey-bg;
			max-height: 65px;

			&.sticky-element--sticky {
				z-index: 8;
				box-sizing: border-box;
				box-shadow: 0px 10px 10px rgba(0, 0, 0, 0.05);
			}
		}
	}

	&.is-sticky {
		.sticky-element-container + .page-content {
			margin-left: 312px;
		}
	}

	.page-layout-content{
		display: flex;
		width: 100%;
		// background-color: $app-white; // to be enabled for new layout/table styles

		.sticky-element-container {
			/* page-sidebar inside sticky-element */
			.page-sidebar {
				box-sizing: border-box;
				/* firefox fix and works across other browsers as well.
					 Issue - padding not taken into account if it has overflow scroll/auto */
				&:after {
					content: "";
					display: block;
					padding-bottom: 200px;
				}
			}

			/* page-sidebar state if sticky */
			.sticky-element--sticky {
				border-radius: 0;
				.page-sidebar {
					height: 100vh;
				}
			}
		}
	}

	/* if padding needs to be added, perform calc and override the width in your feature scss file */
	.page-content {
		min-height: 650px;
		margin: $page-margin-top $page-margin-right $page-margin-bottom $page-margin-left;
		border-radius: 4px;
		width: calc(100% - #{$page-margin-left} - #{$page-margin-right});
	}

	/* if padding needs to be added, perform calc and override the width in your feature scss file */
	.page-sidebar {
		background: $app-sidebar-bg;
		width: $page-sidebar-width;
		min-height: calc(100% - 56px);
		transition: width 0.4s ease-in;
		box-sizing: border-box;
		border-left: 1px solid $border-dark-color;
		animation: fadeIn 0.3s;

		&.page-sidebar-sticky {
			position: fixed;
			right: 0;
			top: 65px;
			overflow-y: auto;
			height: 100%;
		}
	}

	/* fix page-content width */
	.page-content-with-sidebar {
		width: calc(100% - #{$page-sidebar-width} - #{$page-margin-left} - #{$page-margin-right});
	}

	/* if sidebar is present on right side add .page-content-with-sidebar to page-content */

	/* if sidebar is present on left side adjust page-content width */
	.page-sidebar + .page-content, .sticky-element-container + .page-content {
		@extend .page-content-with-sidebar;
	}

	// New layout with 32px left-right margins
	&.new-layout {
		.page-layout-content {
			background-color: $app-white;

			.page-content {
				background-color:$app-white;
				margin: $new-page-margin-top $new-page-margin-right $new-page-margin-bottom $new-page-margin-left;
				width: calc(100% - #{$new-page-margin-left} - #{$new-page-margin-right});

				&.page-content-with-sidebar {
					width: calc(100% - #{$page-sidebar-width} - #{$new-page-margin-left} - #{$new-page-margin-right});
				}
			}
		}
	}
}

.page_loader {
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	padding: 0;
	margin: auto;
	max-width: 100%;
	max-height: 100%;
	position: absolute;
}

.empty-state {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	.placeholder-img img {
		height: 200px;
		width: 230px;
	}
	p {
		font-size: 16px;
		margin-bottom: 0;
	}
	a {
		margin: 8px 0 0 0;
	}
}
