// Pagination Common Styles
.ember-pagination {
	background: $app-white;
	text-align: center;
	margin: 0 -1px 0 1px;
	border-radius: 0 0 4px 4px;
	height: 60px;
	padding-top: 12px;
	line-height: 30px;
	box-sizing: border-box;

	.pagination-wrapper {
		@include flex-center;
		height: 50px;

		.pagination__link {
			@include flexbox;
			color: $primary-text-color-1 !important;
			padding: 5px 10px 10px 10px;
			cursor: pointer;
			box-sizing: border-box;
			text-align: center;
			margin-left: 5px;
			margin-right: 5px;

			&:hover {
				color: $linkColor !important;
			}

			&:active, &:focus {
				box-shadow: transparent;
				border-color: transparent;
			}

			&.pagination__page {
				min-width: 30px;
				@include justify-content(center);
				position: relative;
			}

			&.pagination__previous,
			&.pagination__next {
				padding: 0 10px;
				font-weight: 500;
				background: transparent !important;
				border: 1px solid transparent;
				box-shadow: none;
				&:hover {
					border-color: transparent !important;
				}
				svg.pagination__arrow {
					height: 18px;
					width: 18px;
					fill: $secondary-text-color-1;
					vertical-align: middle;
					padding: 5px 12px;
				}
			}

			&.pagination__previous::after,
			&.pagination__next::before {
				content: "";
				height: 30px;
				border-right: 1px solid $border-light-color;
			}

			&.pagination__previous::after { margin-left: 20px; }

			&.pagination__next::before { margin-right: 20px; }

			&--disabled {
				color: rgba($disabled-text-color, 0.5) !important;
				border-color: transparent !important;
				cursor: default;
				user-select: none;
				pointer-events: none;

				svg {
					fill: rgba($disabled-text-color, 0.5) !important;
				}
			}

			&--active {
				color: $linkColor !important;
				font-weight: 500;

				&:after {
					content: "";
					border-bottom: 2px solid $inputFocusBorder;
					width: 30px;
					position: absolute;
					bottom: -5px;
					left: 0;
				}
			}
		} // --> .pagination__link

		.pagination__gap {
			min-width: 40px;
			position: relative;
			&:after {
				content: '...';
				position: absolute;
				left: 12px;
				top: 7px;
			}
		}

	} // --> .pagination-wrapper
}

.pagination-details {
	height: 32px;
	line-height: 32px;
	margin-left: 16px;
}

html[dir="rtl"] {
	.pagination-wrapper {
		direction: rtl;
	}
}