$table-bottom-pagination-height: 60px;

// Bottom Pagination Common Styles
.ember-pagination-new {
	@include flexbox-simple(flex-start, center);
	background: transparent;
	text-align: center;
	height: $table-bottom-pagination-height;
	line-height: 2;
	box-sizing: content-box;

	// Bottom sticky - not yet
	// position: fixed;
	// bottom: 0;
	z-index: 999;
	border-top: 1px solid $border-light-color;

	.pagination-wrapper {
		width: 100%;
		@include flexbox-simple(flex-start, center);

		.pagination__link {
			@include flex-center;
			color: $primary-text-color-1 !important;
			cursor: pointer;
			box-sizing: border-box;
			text-align: center;
			margin-left: 4px;
			margin-right: 4px;
			height: 24px;
			width: auto;
			min-width: 32px;
			border-radius: 4px;
			background: transparent;

			&:hover {
				background: $background-color !important;
				color: $linkColor !important;
			}

			&:active {
				box-shadow: none;
				border: none;
			}

			&.pagination__page {
				min-width: 24px;
				@include justify-content(center);
				position: relative;
				border-radius: $radius;
			}

			&.pagination__previous,
			&.pagination__next {
				font-weight: $text-weight-semi-bold;
				background: transparent !important;
				border: 1px solid transparent;
				box-shadow: none;
				&:hover {
					border-color: transparent !important;
				}
				svg.pagination__arrow {
					fill: $secondary-text-color-1;
				}
			}

			&.pagination__previous {
				padding: 0 8px 0 0 !important;
				margin-right: 8px;
			}
			&.pagination__next {
				padding: 0 4px 0 8px !important;
				margin-left: 8px;
			}

			&--active {
				background: $linkColor !important;
				color: $app-white !important;
				font-weight: $text-weight-semi-bold;
				&:hover {
					background: $linkColor !important;
					color: $app-white !important;
				}
				&:disabled {
					background: rgba($secondary-text-color-1, 0.5) !important;
					color: rgba($secondary-text-color-1, 0.5) !important;
				}
			}

			&--disabled { 
				color: rgba($secondary-text-color-1, 0.5) !important;
				border-color: transparent !important;
				cursor: default;
				user-select: none;
				pointer-events: none;
				svg { fill: rgba($secondary-text-color-1, 0.5) !important; }
			}

		} // --> .pagination__link

		.pagination__gap {
			min-width: 40px;
			position: relative;
			&:after {
				content: '...';
				position: absolute;
				color: $secondary-text-color-1;
				left: 12px;
				top: 2px;
			}
			&--disabled:after {
				color: rgba($disabled-text-color, 0.5) !important;
			}
		}

		.pagination-count {
			margin-left: auto;
			display: inherit;
			.ember-basic-dropdown-trigger {
				user-select: none;
				padding: 0 6px 0 8px;
				&--above {
					background: $btnDisabledBackground;
					border-radius: 4px;
				}
				// dropdown down chevron
				svg.dropdown-chevron {
					height: 20px;
					width: 20px;
				}
			}
			.ember-basic-dropdown-content {
				width: 138px;
				margin-top: -5px;
			}
		}
	} // --> .pagination-wrapper
}
