/* APP HEADER */

// Uncomment the following line when ember application is running is port:4200
//@import "page-layout/page-layout-dev";

@import "page-layout/page-layout";

@import "app-header/profile-pic";


.assumed-identity{
	background-color: #FCF8E3;
	border: 1px solid #FBEED5;
	height: 30px;
	text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
	box-sizing: border-box;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #82683c;
}

.app-main-wrapper {
	min-height: 700px;
	margin-left: $global-nav-width;
	.sub-header {
		min-height: 50px;
		background: #f8f9fa;
		border-bottom: solid 1px #dadfe3;
		font-size: 17px;
		padding: 15px 15px 0 20px;
	}
	.slider-wrapper {
		background: #fff;
		display: inline-block;
		height: 30px;
		width: 30px;
		padding-top: 5px;
		border: 1px solid #dadfe3;
		text-align: center;
	}
}

.app-footer {
	min-width: 1120px;
	padding: 20px;
	text-align: center;
	border-top: 1px solid $app-footer-border-top;
	border-bottom: 1px solid $app-footer-border-bottom;
	box-shadow: $app-footer-box-shadow;
	background: $app-footer-bg;
}

