$tab-active: $app-primary;
$tab-link: $app-info-color;

.tabs {

	.has-content-count:after {
		content: attr(data-count);
		background: $background-color;
		border-radius: 12px;
		font-size: $text-normal;
		color: $secondary-text-color-1;
		padding: 4px 12px;
	}

	&__items {
		height: 40px;
		list-style: none;
		margin: 0;
		padding: 0 0 0 12px;
	}

	&__item {
		float: left;
		border-bottom: 1px solid $background-color-2;
		position: relative;

		// a {
		// 	text-decoration: none;
		// 	color: $tab-link;
		// }

		&__nav {
			margin: 0 8px 0 0;
			line-height: 40px;
			color: $secondary-text-color-1;
			position: relative;
			overflow: hidden;
			display: block;
			padding: 0 8px;
			text-decoration: none;

			&:hover::after {
				background: $border-dark-color;
				border-radius: 4px;
			}

			&.active {
				color: $inputFocusBorder;
				font-weight: $text-weight-medium;
				&::after {
					background: $inputFocusBorder;
					border-radius: 4px;
				}

				.app-icon {
					fill: $inputFocusBorder;
				}
			}

			&.disabled {
				cursor: not-allowed;
			}
		}

		&__nav::after {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 2px;
			content: '';
			transition: height transform 0.5s;
		}
	}

	&__content {
		clear: both;
		float: none;

		&__pane {
			padding: 20px;
			display: none;

			&.active {
				display: block;
			}
		}
	}
}
