.ui-rule-attrs {
	.fields-container {
		display: flex;
		background-color: $app-white;
		border: 1px solid $primary-border;
		border-radius: 4px;
		padding: 0 2px;
		box-sizing: border-box;
		min-height: 32px;

		&:hover {
			border: 1px solid $inputHoverBorder;
		}

		&.focus {
			border-color: $inputFocusBorder;
			box-shadow: 0 0 0 1px $inputFocusBorder;
		}

		// CSS overwrite for ui-components/form-fields/textfield-multiselect
		.fields-container-key .fs-multiple-options-content {
			.fs-multiple-option-text {
				font-weight: normal;
			}
			.fs-multiple-option-remove-btn {
				display: none;
			}
		}

		.fs-multiple-options-content {
			border: none;
			box-shadow: none;
			padding: 2px 2px;
		}

		.fieldsInputEle {
			width: 96px;
			border: none !important;
			box-shadow: none;
			margin-top: 3px;
		}
		.fields-container-key-dropdown {
			.ember-power-select-options {
				.divider:not(:first-child) {
					height: 1px;
					background-color: $border-dark-color;
					margin: 8px 12px 10px;
				} 
			}
		}		

		// CSS overwrite for freshdesk/ember-form/form-fields/auto-complete/
		.ember-basic-dropdown {
			.ember-basic-dropdown-trigger--below, .ember-power-select-trigger {
				border: none !important;
				box-shadow: none;
				min-height: 36px;
				display: inline-flex;
				align-items: center;
				justify-content: unset;
				min-width: auto;
			}
			.ember-power-select-trigger:focus {
				border: none !important;
				box-shadow: none ;
			}
		}
		.form-field--label {
			display: none;
		}
		.form-field--control {
			border: none;
			box-shadow: none;
		} 
	}
	.single-pill {
		.ember-power-select-clear-btn {
			right: unset;
			position: relative;
			margin-left: -32px !important;
			top: 5px !important;
			&:not(:hover) {
				opacity: .5;
			}
		}

		.ember-power-select-multiple-option {
			height: 28px;
			margin: 0 2px;
		}

		.ember-power-select-trigger {
			justify-content: unset;

			.ember-power-select-selected-item {
				background-color: $background-color;
				border-radius: 4px;
				padding: 0 24px 0 8px;
				margin: 4px;
				width: 100% !important;
			}
			.ember-power-select-status-icon {
				display: none;
			}
		}
	}

}
