.avatar__icon {
	cursor: pointer;
	outline: none;
	display: flex;
	align-items: center;
	&--xsmall {
		width: 24px;
		height: 24px;
	}
	&--small {
		width: 32px;
		height: 32px;
	}
	&__img {
		max-width: 100%;
		height: 100%;
	}
	&--xxlarge .avatar__initial, &--xxlarge .avatar__icon__img{
		width: 72px;
		height: 72px;
		font-size: 40px;
		box-sizing: border-box;
		border: 2px solid $app-white;
		box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.21);
	}
	&--circle{
		img, div{
			border-radius: 50%;
		}
	}
}
.avatar__img_placeholder {
	background: url('data:image/svg+xml;base64,') ;
	background-repeat: no-repeat;
	background-position: center;
	background-size: 110%;
	height: 24px;
	width: 24px;
}
.avatar__initial{
	height: 32px;
	width: 32px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 18px;
	font-weight: 500;
	color: #fff;
	cursor: pointer;
	&--bg--1{
		background-color: #4F7D41;
	}
	&--bg--2{
		background-color: #783369;
	}
	&--bg--3{
		background-color: #835841;
	}
	&--bg--4{
		background-color: #932728;
	}
	&--bg--5{
		background-color: #31507C;
	}
}
