.approval-container {
	display: flex;
	margin-right: 8px;
	align-items: center;
	justify-content: left;
	.agent-approve {
		margin-left: 10px;
		font-size: $text-normal;
		padding: 4px 10px 6px 6px;
		font-weight: $text-weight-semi-bold;
		svg {
			width: 12px;
			height: 9px;
		}
		cursor: pointer;
		border: none;
		background: $app-white;
		color: $app-success-state1;
		&:hover {
			color: darken($app-success-state1, 5%);
		}
	}
	.approval-divider {
		float: left;
		padding-top: 6px;
	}
	.agent-reject {
		display: flex;
		align-items: center;
		font-size: $text-normal;
		padding: 4px 8px 6px 4px;
		font-weight: $text-weight-semi-bold;
		svg {
			float: left;
			width: 22px;
			height: 22px;
		}
		cursor: pointer;
		border: none;
		background: $app-white;
		color: $app-error-state;
		&:hover {
			color: darken($app-error-state, 5%);
		}
	}
	.pos-rel-form {
		position: relative;
	}
	.show {
		display: block;
	}
	.hide {
		display: none;
	}
	#approval_form_container_approval, #approval_form_container_summary {
		position: absolute;
		width: 260px;
		right: 0;
		padding: 10px;
		background: $app-white;
		border: 1px solid $white;
		z-index: 100;
		margin: 25px 0 0 -15px;
		border-radius: 4px;
		box-shadow: $remark-box-shadow;
		&:after, &:before {
			bottom: 100%;
			border: solid transparent;
			left: 83%;
			content: " ";
			height: 0;
			width: 2px;
			position: absolute;
			pointer-events: none;
		}
		&:after {
			border-color: $approval-after-border-color;
			border-bottom-color: $app-white;
			border-width: 5px;
			margin-left: -5px;
		}
		&:before {
			border-color: $approval-before-border-color;
			border-bottom-color: $app-white;
			border-width: 7px;
			margin-left: -7px;
		}
		&.approval_posrel {
			&:after, &:before {
				left: 45%;
			}
		}
		.remark_form {
			padding: 0;
			.remark_label {
				padding-bottom: 5px;
				font-size: $title-small;
				.remark-required {
					display: inline;
					color: $remark-required;
				}
			}
			textarea {
				width: 98%;
				height: 64px;
				resize: none;
			}
			.container-footer {
				padding-top: 10px;
				float: right;
				button {
					font-size: $title-small;
				}
			}
		}
	}
}
