.sticky-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	height: 17px;
	#export-activities {
		background: none;
		border: none;
		color: $app-primary;
		font-size: 14px;
		&:hover {
			cursor: pointer;
		}
		> .export-icon {
			width: 16px;
			height: 16px;
			position: relative;
			top: 2px;
			background: $app-jade-light;
			border-radius: 50%;

			path {
				fill: $app-green;
				stroke: $app-green;
			}
		}
		> span {
			font-weight: $text-weight-medium;
			line-height: 17px;
		}
	}
	
	.prepend-activities-container {
		position: absolute;
		width: calc(100% - 64px);
		display: flex;
		justify-content: center;
		align-items: center;
		> #prepend-button {
			cursor: pointer;
			color: $secondary-text-color-1;
			height: 40px;
			background-color: $app-white;
			border-radius: 100px;
			padding: 10px 20px 10px 0;
			display: flex;
			justify-content: center;
			align-items: center;
			border: 0;
			box-shadow: 0px 3px 12px $list-item-shadow;
			z-index: 1;
			
			> .load-new-icon-bg {
				margin: 6px;
				width: 28px;
				height: 28px;
				background: $linkColor;
				border-radius: 50%;
				display: flex;
				justify-content: center;
				align-items: center;
				> .load-new-icon {
					width: 14px;
					height: 14px;
					fill: $app-white;
				}
			}
			> b {
				font-weight: $text-weight-semi-bold;
				margin: 0 5px 0 5px;
			}
			&:hover {
				border: 1px solid $linkColor;
			}
		}
	}

	&.active {
		> span.lower-line {
			position: absolute;
			border-left: 2px solid $border-light-color;
			left: 236px;
			top: 14px;
			height: 25px;
			z-index: auto;
		}
		
		&.short {
			> span.lower-line {
				position: absolute;
				top: 84px;
				left: 47px;
				z-index: -1;
			}
			> #sticky-title {
				position: relative;
				left: -12px;
				margin-left: 12px;
				text-align: left;
				padding-left: 45px;
				&::after {
					display: none;
				}
			}
		}

		> #sticky-title.slide-up {
			animation: slideUp 50ms ease-out;
		}

		&:not(.short) {
			> #sticky-title::before {
				display: none;
			}
		}
	}
}

.sticky-header.active #sticky-title, .timeline-list-container .activity-date {
	font-weight: $text-weight-semi-bold;
	color: $primary-text-color-1;
	&::before, &::after {
		content: "";
		position: absolute;
		width: 10px;
		height: 10px;
		border-radius: 50px;
		background: $app-warning-state;
		margin-left: 34px;
		margin-top: 3px;
		z-index: 1;
	}
	&::before {
		margin-left: 0;
		left: 11px;
	}
}

.sticky-header.active #sticky-title, .timeline-list-container .activity-date, .activity-entry .activity-time {
	font-size: 14px;
	margin-left: 32px;
	left: 0;
	min-width: 150px;
	text-align: right;
}

.timeline-container {
	background-color: $app-white;
	width: calc(100% + 32px);
	height: calc(100% - 65px);
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow: auto;

	.activity-timeline {
		position: relative;
		width: 100%;
		.timeline-list-container {
			.activity-date {
				margin-top: -10px;
			}
			.activity-date, .activity-time {
				position: absolute;
			}
			.timeline-list{
				list-style: none;
				padding: 0 110px;
				position: relative;
				.activity-entry {
					border-left: 2px solid $border-light-color;
					padding: 0 32px;
					margin-left: 110px;
					.activity-time {
						font-weight: $text-weight-normal;
						color: $primary-text-color-1;
						margin-top: 16px;
					}       
					.activity-user {
						position: absolute;
						left: 205px;
						margin-top: 6px;
						.avatar__initial {
							font-size: 14px;
						}
						> .system-executed {
							width: 32px;
							height: 32px;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50%;
							background-color: $section-background-blue;
							> .system-icon {
								width: 16px;
								height: 16px;
								fill: $tab-icon-color;
							}
						}
					}
					.content {
						margin-top: 6px;
						.avatar-name {
							color: $primary-text-color-1;
							font-weight: $text-weight-semi-bold;
							margin-bottom: 8px;
							&:hover {
								color: $linkColor;
							}
						}
						.activity-done {
							word-break: break-word;
							font-weight: $text-weight-medium;
							margin-top: 8px;
							padding: 16px;
							border: 1px solid $border-light-color;
							border-radius: 4px;
							max-width: 560px;
							min-width: 100%;
							.activity-content {
								color: $toggleSlider;
								margin: 0;
								word-break: break-all;
								&::first-letter {
									text-transform: uppercase;
								}
								> b {
									font-weight: $text-weight-medium;
									color: $primary-text-color-1;
								}
							}
							.activity-subcontent-container {
								margin-left: 24px;
								list-style: initial;
								.activity-subcontent {
									&::marker {
										color: $secondary-text-color-2;
									}
									color: $toggleSlider;
									padding: 8px 0 0;
									> span {
										b {
											font-weight: $text-weight-medium;
											color: $primary-text-color-1;
										}
									}
								}
							}
						}
					}
					&:nth-child(2) {
						> .activity-time {
							margin-top: 24px;
						}
						> .activity-user, > .content {
							margin-top: 14px;
						}
					}
					&:last-child {
						padding-bottom: 16px;
					}
					a:not(.requester-name) {
						font-weight: $text-weight-medium;
						color: $linkColor;
						&:focus {
							outline: auto;
							> .avatar-name {
								color: $linkColor;
							}
						}
					}
				}
				&.short {
					padding: 0;
					> li.activity-entry {
						margin-left: 15px;
						display: flex;
						align-items: flex-start;
						padding-right: 0;
						> .activity-user {
							left: 0;
							margin-top: 24px;
						}
						> .content {
							margin-top: 0;
							display: flex;
							flex-direction: column;
							justify-content: center;
							align-items: flex-start;
							> .activity-done {
								margin-top: 0;
								border: 0;
								padding: 0;
								> .activity-content {
									color: $primary-text-color-1;
									font-weight: $text-weight-normal;
									margin: 22px 0 7px 0;
								}
							}
							> .activity-time {
								position: relative;
								margin: 0;
								text-align: left;
								font-size: 12px;
								font-weight: $text-weight-normal;
								color: $secondary-text-color-1;
							}
						}
					}
				}
				.requester-card-wrapper {
					display: inline-block;
					
					.requester-card-content{
						height: 160px;
					}

					.ember-svg {
						width: 14px;
						height: 14px;
					}
				}
			}
			&.short {
				> .activity-date {
					margin-left: 0;
					text-align: left;
					padding-left: 45px;
					&::after {
						display: none;
					}
				}
			}
			&:not(.short) {
				> .activity-date::before {
					display: none;
				}
			}
		}
	}

	.activity-timeline .timeline-list-container:last-child .timeline-list:last-child > .activity-entry:last-child {
		border-left: 0;
		> .activity-user::before {
			content: "";
			position: absolute;
			width: 2px;
			height: 33px;
			margin-left: 15px;
			background: $border-light-color;
			margin-top: -33px;
		}
	}

	
	&.empty {
		> .empty-container {
			position: relative;
			top: 175px;
			display: flex;
			flex-direction: column;
			align-items: center;
			> .helptext {
				color: $secondary-text-color-1;
				padding: 24px;
				font-size: 16px;
				font-weight: $text-weight-normal;
			}
		}
	}
	
	&:not(.empty) {
		padding: 25px 32px 8px 0px;
		margin-top: 15px;
	}
}

.loader-container {
	display: flex;
	justify-content: center;
	align-items: center;
	.load-spinner {
		width: 37px;
		height: 37px;
		fill: $app-primary;
	}
}
@keyframes slideUp {
	0% {
		opacity: 0;
		transform: translateY(10%);
	}
	30% {
		opacity: 0.3;
		transform: translateY(10%);
	}
	50% {
		opacity: 0.5;
		transform: translateY(10%);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}