.entity-template {
	margin-bottom: 24px;
	max-width: 848px;
	background-color: $background-color-2;
	border-radius: 8px;
	@include flexbox;
	.template-list-dropdown {
		position: relative;
		width: 50%;
		margin: 14px 43px 16px 24px;
		.select-template-label {
			font-weight: $text-weight-semi-bold;
			padding-bottom: 8px;
		}
		.select-field {
			margin-bottom: 7px;
			label {
				font-weight: $text-weight-semi-bold;
			}
		}
		.template-filling-info {
			font-size: $text-normal;
			line-height: 18px;
			font-weight: $text-weight-normal;
			margin-top: 7px;
		}
		.clear-template {
			position: absolute;
			right: 0;
			padding: 0;
			line-height: 20px;
			font-size: $text-normal;
			font-family: $baseFontFamily;
			color: $linkColor;
			cursor: pointer;
		}
	}
	.separator {
		width: 1px;
		background-color: $border-dark-color;
		margin: 12px 0px 15px;
	}
	.recent-templates {
		width: 50%;
		padding: 9px 16px 10px;
		.recent-templates-label {
			font-weight: $text-weight-semi-bold;
			line-height: 18px;
			font-size: $text-normal;
			color: $secondary-text-color-1;
		}
		.recent-template-list-item {
			@include flexbox-simple(flex-start, center);
			margin-top: 4px;
			.ember-svg {
				fill: $svgFocusedBorder;
			}
			.recent-template-link {
				font-size: $text-normal;
				line-height: 20px;
				font-weight: $text-weight-normal;
				margin: 0 0 0 7px;
				padding: 0;
				border: 0;
				cursor: pointer;
				color: $linkColor;
				font-family: $baseFontFamily;
				text-align: left;
			}
		}
	}

	.new-template-btn-wrapper {
		background: $white;
		margin: -6px -8px;
		padding: 44px 0;
		@include flexbox-simple(center, center);
		@include flex-direction(column);
		.no-template {
			color: $app-form-label-color;
			font-weight: $text-weight-normal;
			margin-bottom: 16px
		}
		.create-template {
			@include flexbox-simple(center, center);
			width: 100%;
			font-weight: $text-weight-medium;
			svg {
				margin-right: 8px;
			}
		}
	}
}
