.page-nav-wrap {
	float: right;

	.page-nav-text {
		margin-right: 16px;
	}

	.page-nav-btns {
		display: inline-block;

		.btn {
			padding: 4px 5px;
		}

		.btn-prev {
			margin-right: -1px;
		}

		.btn-next {
			margin-left: 0;
			margin-right: 0;
		}

		svg {
			width: 20px;
			height: 22px;
			fill: $app-icon-color;
		}

		.disabled {
			svg {
				fill: rgba($disabled-text-color, 0.5) !important;
			}
		}
	}
}
