.empty-tab-content-list-section,
.empty-sidemodal-list-content {
	text-align: center;
	margin: 130px auto;
	.empty-state-messages {
		font-size: 16px;
		color: $secondary-text-color-1;
		margin: 16px 0 20px 0;
		.hint-text {
			font-size: $baseFontSize;
			margin-top: 8px;
		}
	}
	.btn-link {
		font-weight: $text-weight-medium;
	}
	.add-tab-content-icon {
		height: 16px;
		vertical-align: text-bottom;
	}
}

.empty-sidemodal-list-content {
	margin: 70px auto;
}
