@import '../../../app-components/native-integrations/time-entry-app';

.ember-form.time-entry-form .fieldset-wrapper {
	.form-field.inline-elem {
		@include inline-flexbox;
		@include flex-direction(column);
		width: 200px;
		margin: 0;
		> .time-spent-hint {
			margin: 10px 0 0;
			order: 2;
			width: 475px;
			line-height: 1.4;
		}
		&.billable {
			margin-left: 20px;
			position: relative;
			top: 32px;
			@include align-items(flex-start);
			> .form-field--label {
				@include flexbox;
				@include align-items(center);
				.form-field--control {
					margin-right: 12px;
				}
			}
		}
	}
	.time-picker-wrapper {
		width: 140px;
		.time-control {
			width: 100%;
			.ember-power-select-trigger {
				width: 100%;
			}
		}
	}
	.form-field.task-search-field {
		.task-item {
			.task-id::after {
				content: "-";
				margin-left: 4px;
			}
		}

		.selected-task-item {
			margin-left: 8px;
		}
	}
}
