$logMeIn-widget-container-box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.01);

.logMeIn-widget {
	padding: 8px 16px;
	color: $primary-text-color-1;
	font-size: $text-normal;

	.logMeIn-head-container {
		.logMeIn-title {
			padding-left: 8px;
			font-size: $title-small;
		}
	}

	.pincode {
		font-weight: $text-weight-semi-bold;
	}

	.logMeIn-panel-container {
		.loader-container {
			margin: 10px 0;
			fill: $inputFocusBorder;
		}

		.gen-text, .logMeIn-helpText {
			color: $secondary-text-color-1;
		}

		.logMeIn-error {
			font-size: $baseFontSize;
			height: 72px;
			background: $alertWarningNewBackground;
			border: 1px solid $alertWarningBorder;
			margin: 0;

			.alert-content::before {
				border-color: $alertWarningBorder;
			}

			.warning-icon {
				align-self: flex-start;
				position: relative;
				top: 10px;

				svg {
					fill: $app-error-state;
				}
			}
		}

		.gen-session {
			margin-top: 8px;

			.gen-instruction {
				padding: 0;
				margin-top: 8px;
				font-size: $text-normal;
				font-family: inherit;
			}
		}

		.gen-instruction, .tech-link {
			font-weight: $text-weight-medium;
		}

		.remote-btn {
			background: $white;
			display: block;
			margin: 8px 0;

			&:hover {
				background: $background-color-2;
			}
		}
		.new-tab-icon {
			fill: $primary-text-color-1;
			position: relative;
			top: 2px;
		}
	}
}
