.merge-entities-modal {
	.ember-tooltip {
		word-break: break-word;
	}

	.ember-alert-warning {
		position: relative;
		span {
			padding-left: 16px;
			position: relative;
			font-size: $text-normal;
			font-weight: $text-weight-normal;
			&:before {
				content: "";
				top: 0;
				left: 4px;
				height: 100%;
				position: absolute;
				border-left: 1px solid $alertPartialBorder;
			}
		}

		svg { fill: $app-warning-state; }
	}

	h1 {
		font-size: $title-extra-large;
		font-weight: $text-weight-semi-bold;
	}

	.ember-modal-body {
		padding: 0 32px 32px !important;
		overflow-x: hidden;
		height: calc(100% - 157px) !important;
	}

	.ember-modal-footer {
		padding: 0 32px !important;
		.btn-primary {
			margin-right: 0;
		}
	}

	.back-to-selection-view {
		display: inline-flex;
		align-items: center;
		margin: 0 0 8px -8px;
		font-weight: $text-weight-semi-bold;
		font-size: $title-normal;
		padding: 0;
		svg {
			margin-right: 4px;
			fill: $linkColor;
		}
	}

	#entity-search-wrapper {
		display: flex;
		margin-bottom: 16px;

		.entity-search-dropdown {
			width: 150px;
			display: inline-block;
			.ember-basic-dropdown-content {
				width: 150px;
			}
			.ember-power-select-selected-item {
				width: 96px;
			}
		}

		.entity-search-input-wrapper {
			.form-field {
				margin-bottom: 0 !important;
			}

			.entity-properties .entity-content a {
				width: 270px !important;
			}
		}

		.btn.dummy-focus {
			border: 1px solid #2C5CC5;
			box-shadow: 0 0 0 1px #2c5cc5;
		}
		a.dummy-focus {
			border: 1px solid #2C5CC5;
			box-shadow: 0 0 0 1px #2c5cc5;
			border-radius: 4px;
		}
	}

	.merge-entities-view {
		.entity-row:last-child .entity-line:after { display: none; }
	}

	.shimmer-loader { margin-left: -16px; }

	.entity-count {
		font-weight: $text-weight-medium;
		margin-bottom: 4px;
		color: $app-primary-black;
	}

	.entity-description {
		color: $app-form-label-color;
		margin-bottom: 24px;
	}

	.ember-alert-info {
		margin-bottom: 24px;
	}

	.entity-row {
		padding: 12px 24px 12px 16px;
		min-height: 88px;
		display: flex;
		align-items: center;
		border: 1px solid $border-dark-color;
		border-radius: 4px;
		margin-bottom: 8px;
		box-sizing: border-box;

		.entity-properties {
			display: flex;
			flex-direction: column;
			width: 350px;
			margin-left: 8px;

			.entity-content {
				display: flex;
				margin-bottom: 4px;
				a {
					width: 345px;
					color: $primary-text-color-1;
					&:hover { color: $linkColor; }
				}

				.entity-subject {
					width: 345px;
					display: inline-block;
				}
			}

			.entity-requester , .entity-state {
				font-size: $text-normal;
				margin-bottom: 4px;
				display: flex;

				span { color: $secondary-text-color-1; }

				.entity-requester-name, .entity-state-date {
					word-break: break-word;
					padding-left: 4px;
					font-weight: $text-weight-semi-bold;
					&.ellipsis { width: 310px; }
				}

				.entity-requester-name {
					@include flexbox;
					width: 100%;
					.rname, .srname {
						max-width: 90%;
						@extend .ellipsis;
					}
					.srname {
						max-width: 75%;
					}
				}

				.entity-state-type {
					max-width: 200px;
					display: inline-block;
					word-break: break-word;
				}
			}
		}

		.entity-primary {
			display: flex;
			flex-direction: column;
			align-items: center;
			span {
				font-size: $text-normal;
				color: $secondary-text-color-1;
			}
		}

		.entity-properties + .entity-primary { margin-left: auto; }

		.toggle-entity-primary {
			margin-left: auto;
		}
	}

	.selected-entities-view {
		.entity-row:last-child {
			margin-bottom: 0 !important;
		}
	}

	.edit-primary-note, .edit-secondary-note {
		min-width: 28px;
		height: 28px;
	}

	.entity-primary-row {
		display: flex;
		margin-bottom: 4px;
		padding: 0;
		min-height: 96px;

		.entity-line {
			padding: 16px;
			width: calc(100% - 32px);
			&:before, &:after { display: none; }
		}

		.btn-icon { margin-left: auto; }

		.entity-primary {
			align-self: flex-start;
			margin-top: 4px;
			margin-right: 8px;
			span {
				display: none;
			}
		}

		.edit-primary-note {
			align-self: flex-start;
			margin-top: 16px;
			margin-right: 16px;
		}

		.primary-note {
			border-top: 1px solid $border-dark-color;
			border-radius: 0 0 4px 4px;
		}

		&.open {
			display: block;
			max-height: 100%;

			.entity-line {
				border-bottom: 1px solid $border-dark-color;
			}

			&:before { display: none; }
		}

		&.focus-class {
			border-color: $linkColor;
		}
	}

	.entity-secondary-row {
		margin-left: 50px;
		position: relative;

		/* bullet point */
		&:before {
			content: "";
			position: absolute;
			left: -24px;
			width: 7px;
			height: 7px;
			background: $border-dark-color;
			border-radius: 50%;
		}

		/* tree view branch */
		&:after {
			content: "";
			width: 20px;
			height: 1px;
			background: $border-dark-color;
			position: absolute;
			left: -20px;
		}
	}

	.entity-line {
		display: flex;
		align-items: center;
		width: 100%;

		&:before, &:after {
			content: "";
			position: absolute;
			height: 50%;
			width: 1px;
			left: -21px;
			background: $border-dark-color;
		}

		&:before { top: 0; }

		&:after {
			top: 50%;
			height: calc(50% + 10px);
		}
	}

	.entity-primary-note-form .textarea-field,
	.entity-secondary-note-form .textarea-field {
		& > label { display: none; }

		.fr-toolbar {
			border-radius: 0;
			background-color: transparent;			

			.fr-command.fr-btn svg.fr-svg {
				width: 18px;
				height: 18px;
			}

			[data-cmd=codeInsert] {
				width: 32px !important;
			}

		}

		.froala-shortcut-helper-text {
			border-top: 1px solid $border-dark-color;
		}

		.froala-editor-container, .fr-toolbar, .fr-wrapper { border: none; }
	}

	.entity-secondary-note-form {
		.textarea-field {
			border: 1px solid $border-dark-color;
			border-bottom: none;
			border-radius: 4px 4px 0 0;
		}

		.secondary-note {
			border: 1px solid $border-dark-color;
			border-radius: 0 0 4px 4px;
			border-top-color: $border-dark-color !important;
		}

		&.focus-class {
			.textarea-field, .secondary-note {
				border-color: $linkColor;
			}
		}
	}

	.entity-secondary-helptext {
		position: relative;
		padding: 16px 0 0 50px;
		font-weight: $text-weight-medium;
		margin-bottom: 8px;

		.btn-icon { margin-left: 8px; }

		&:before {
			content: "";
			height: calc(100% + 20px);
			width: 1px;
			background: $border-dark-color;
			position: absolute;
			left: 30px;
			top: -4px;
		}

		& + .entity-secondary-note-form {
			.textarea-field {
				margin-left: 50px;
				position: relative;

				.froala-editor-container:before {
					content: "";
					height: calc(100% + 144px);
					width: 1px;
					background: $border-dark-color;
					position: absolute;
					left: -21px;
				}

				.fr-toolbar { border-radius: 4px 4px 0 0 !important; }
			}
		}
	}

	.entity-primary-note-form + .entity-secondary-helptext:before { top: 0; }

	.primary-note, .secondary-note {
		padding: 12px 16px;
		background: $background-color-2;
		display: flex;
		align-items: center;

		.btn:not(.btn-primary) {
			margin-left: auto;
		}

		.ember-form {
			width: 280px;
			input { margin-right: 0 !important; }
			label {
				display: inline !important;
				margin-left: 6px;
			}
		}
	}

	.secondary-note { margin: 0 0 20px 50px; }
}

.focused-cell {
	border: 1px dotted red;
}