@charset 'utf-8';

#ember-body-content {
	&.ember-application {
		margin-left: 64px;

		// Base configuration files and utility files
		@import "base/main";

		//Layout
		@import "app-layout/main";

		//Components
		@import "ui-components/main";
		@import "app-components/main";
		@import "form-components/main";
		@import "marketplace/main";

		&.stand-alone-app, &#ember-testing {
			margin: 0;
			background-color: $background-color;
			color: $primary-text-color-1;
			font-family: $baseFontFamily;
			font-size: $baseFontSize;
		}
	}
}

@import "app-layout/app-title/main";
@import "ui-components/common/burger-menu";
@import "modules/rtl-overwrite";
@import "modules/browser_specific_styles";


.ember-application-body, #ember-body-content.ember-application {
	/* Importing files related to dropdowns to render ourside of #ember-body-content */
	@import "base/variables.scss";
	@import "ember-power-select";
	@import "ui-components/common/power-select";
	@import "ui-components/overlays/basic-dropdown";
}