.new-ticket-form-elements-container {
	&.submit-in-progress {
		// setting opacity fade on immediate child levels, instead of top level, helps keep the blue spinner at 100% opacity
		> * { @include fade-loader; }
		&:after {
			content: '';
			position: fixed;
			top: 230px;
			left: calc((100% - 464px + #{$global-nav-width}) / 2);
			@include blue-spinner;
		}
	}

	// Ticket side modal flow specific styles
	&.side-modal-flow {
		&.submit-in-progress {
			&:after {
				left: initial;
				right: calc(600px / 2) // 600px is global side modal width
			}
		}

		.entity-template {
			width: 534px;
			margin-bottom: 20px;

			.template-list-dropdown {
				margin-right: 12px;
				width: 252px;
			}

			.recent-templates {
				width: 50%;
			}

			.recent-template-list-item {
				overflow: hidden;

				svg.ember-svg {
					flex: none;
				}

				.recent-template-link {
					margin-left: 8px;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
					max-height: 100%;
					text-align: left;
				}
			}
		}

		.capture-parent-ticket {
			margin-bottom: 20px;
			@include flexbox;
			@include align-items(center);
			label {
				padding-bottom: 0;
			}
		}

		.new-ticket-form {
			.fieldset-wrapper {
				.form-field {
					width: 100%;
					&.description-field {
						width: 100%;
						.froala-editor-container {
							.fr-toolbar {
								.fr-btn {
									.fr-svg {
										width: 20px;
									}
								}
							}
						}
					}
					&.dependent-select-dropdown-field.nested-parent-field {
						& + div {
							.form-field {
								width: 100%;
								& + div {
									.form-field {
										width: 100%;
									}
								}
							}
						}
					}
				}
			}
			.attach-files {
				.attachment-list-container-drag-drop {
					width: 100%;
				}
			}

			.associate-asset-card-wrapper {
				.associate-asset-card {
					.asset-card {
						width: 100%;
						.asset-title {
							p {
								max-width: 425px;
							}
						}
					}
				}
			}
		}
	}
	// Side modal flow ends

	.new-ticket-form {
		.attach-files {
			margin: 0;
			padding-left: 0;
			padding-right: 0;
			background-color: transparent;
			.attachment-list-container-drag-drop {
				max-width: 752px;
			}
			.attachment-list {
				.list-file-item {
					max-width: 530px;
					width: 100%;
					&.new-attachment {
						border: 1px solid $app-border-primary;
						background: $white;
					}
				}
			}
		}

		.fieldset-wrapper {
			.form-field {
				& > .form-field--label {
					@include line-clamp;
				}
				&.checkbox-field > .form-field--label {
					@include line-clamp;
					display: inline;
				}
				max-width: 600px;
				&.description-field {
					max-width: 800px;
					.froala-editor-container {
						// transparent Froala toolbar
						.fr-toolbar {
							background: transparent;
							border-bottom: none;
						}

						.fr-wrapper {
							box-sizing: content-box;
						}
					}
				}

				&.checkbox-field {
					label,
					input {
						margin-left: 0;
					}

					label {
						@include flexbox;
						margin-right: 5px;
						word-break: break-word;
						input {
							flex-shrink: 0;
						}
					}
				}

				// Will update with general class specific styles here - @jayasenthil
				&.dependent-select-dropdown-field.nested-parent-field {
					& + div {
						margin-left: 8px;
						.form-field {
							border-left: 1px solid $primary-border;
							padding-left: 14px;
							width: calc(600px - 8px);
							margin-left: 8px;
							&.nested-first-level {
								margin-bottom: 0;
							}
							&.nested-second-level {
								label {
									padding-top: $page-margin-top;
								}
							}
							& + div {
								.form-field {
									width: calc(600px - $page-margin-top);
								}
							}
						}
					}
				}
			}

			.render-component {
				.add-cc-btn {
					padding-left: 0;
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;
					cursor: pointer;
				}
			}
			.add-cc-wrapper {
				margin-top: -12px;
				margin-bottom: 0;
				@include flexbox;
				.add-cc-field {
					flex-grow: 1;
					margin-bottom: 0;
					margin-top: $page-margin-top;
				}
				.hide-cc-btn {
					flex-shrink: 0;
					margin-top: 42px;
					margin-left: $page-margin-left;
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;
					cursor: pointer;
				}
			}
		}

		.asset-associate-link {
			font-weight: $text-weight-medium;
			color: $app-primary !important;
			border: none !important;
			box-shadow: none !important;

			svg {
				position: relative;
				top: 3px;
			}
		}

		.requester-field {
			position: relative;
			.add-new-requester-btn {
				position: absolute;
				top: 0;
				right: 0;
				@include inline-flexbox;
				@include align-items(center);
				border: 0;
				padding: 0;
				span {
					margin-left: 7px;
					font-family: $baseFontFamily;
				}
			}
		}

		.static-rich-text-field {
			word-break: break-word;
			ul {
				list-style: initial;
			}
		}
	}
}
