@mixin new-tooltip-style() {
	background: $app-grey;
	border-radius: 4px;
	text-shadow: none;
	// text-transform: capitalize; //If needed, Pls overwrite this style in the corresponding style-sheet.
	font-style: normal;
	font-weight: $text-weight-medium;
	font-size: $text-normal;
	line-height: 14px;
	color: $app-white;
}

.ember-tooltip {
	@include new-tooltip-style();

	.ember-tooltip-arrow {
		display: none; // arrow not needed as per new mocks
	}
}


[class*=hint--] {
	&:before { // arrow not needed as per new mocks
		border: none;
	}
	&:after {
		@include new-tooltip-style();
	}
}
