.solutions_text {
	table td,
	table th {
		&.fr-selected-cell {
			border: 1px double #1e88e5;
			background: #e8f0fd;
		}

		&.fr-highlighted {
			border: 1px double #ff0000 !important;
		}

		&.fr-thick {
			border-width: 2px;
		}
	}

	table {
		width: 100%;
	}

	table, table td, table th {
		border-color: #ddd;
	}

	table.fr-dashed-borders th,
	table.fr-dashed-borders td {
		border-style: dashed;
	}

	table.fr-alternate-rows tbody tr:nth-child(2n) {
		background: #f5f5f5;
	}

	table.fr-no-borders th,
	table.fr-no-borders td {
		border-style: none;
	}

	table th {
		font-weight: bold;
		text-align: center;
		background: $background-color;
	}

	.fr-video {
		display: block;
		&.fr-fvc {
			text-align: center;
		}
		&.fr-fvl {
			text-align: left;
		}
		&.fr-fvr {
			text-align: left;
		}
	}

	.fr-img-caption {
		.fr-img-wrap {
			width: inherit;
			/* the caption goes to the right if not inline block */
			display: inline-block;
			text-align: center;
			img {
				width: inherit;
			}
		}
	}
}
