$box-shadow-pt-widget: 0px 4px 4px rgba(0, 0, 0, 0.01);
$show-more-container-drop-shadow: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.01));

.pt-widget {
	color: $primary-text-color-1;
	padding: 8px 16px;
	.pt-issue-container {
		.pt-title{
			padding-left: 8px;
		}
	}
	.link-pt-button{
		margin: 14px 0 0;
		background: $white;
	}
	.associated-stories {
		padding: 12px;
		font-size: $text-normal;
		font-weight: $text-weight-semi-bold;
		line-height: 18px;
		.linked-stories {
			line-height: 20px;
			font-weight: $text-weight-medium;
			list-style: none;
			display: flex;
			flex-direction: column;
			li{
				word-break: break-word;
				display: flex;
				padding-top: 12px;
				.story-content{
					display: flex;
					flex-direction: column;
					padding-left: 9px;
					.sub-story-line {
						color: $app-form-label-color;
						font-weight: $text-weight-normal;
					}
					.open-new-tab{
						height: 10px;
						width: 10px;
						margin-left: 2px;
						fill: $linkColor
					}
				}
				&:before {
					content: "\2022";
					color: $secondary-text-color-2;
					font-weight: $text-weight-bold;
					display: inline-block;
					margin-left: -1px;
				}
			}
		}
	}
	.pit-no-stories-linked {
		padding-top: 8px;
	}
	.pt-container {
		// padding-top: 12px;
		.pt-story-container{
			border: 1px solid $border-dark-color;
			box-sizing: border-box;
			filter: $show-more-container-drop-shadow;
			border-radius: 8px;
			overflow-y: hidden;
		}
		.pt-li-disabled {
			display: none !important;
		}
		.expand_container {
			max-height: 376px !important;
			overflow-y: scroll !important;
		}
		.view-more {
			color: $linkColor;
			text-align: left;
			position: -webkit-sticky; /* Safari */
			position: sticky;
			bottom: 0;
			width: 100%;
			background: $white !important;
			padding-left: 12px !important;
		}

	}
}
// New Form
.pivotal-new-form {
	textarea {
		height: 136px !important;
	}
}