.tcpr-prop-section {
	.property-update-btn:disabled {
		background: $disabled-button-bg;
		border: 1px solid $disabled-button-border;
		color: $disabled-button-color !important;
		opacity: 1;
	}

	.properties-wrap {
		.prop-panel-container {
			overflow: visible;
			.shimmer-loader { width: 180%; }
		}

		.ui-accordion-panel-wrapper {
			padding-left: 0;
		}

		.properties-body {
			position: relative;
			max-width: 334px;
			.toggle-suggestion {
				padding: 8px;
				margin-top: 4px;
				margin-bottom: 10px;
				border: 1px solid $section-background-blue;
				background: $section-background-blue;
				border-radius: 4px;
				box-shadow: inset 0px 1px 2px $dropdown-search-shadow-color;
				height: 48px;

				.field-suggestions-switch {
					margin-right: 8px;
				}

				.switch-title-container {
					@include flexbox-simple();
					@include flex-direction(column);

					.switch-title {
						font-weight: $text-weight-medium;
						font-size: $text-normal;
						color: $app-grey;
					}

					.switch-content {
						font-weight: $text-weight-normal;
						color: $secondary-text-color-1;
						font-size: $text-normal;
					}
				}
			}

			&.is-loading-suggestion {
				> * {
					@include fade-loader;
				}
				&:after {
					content: "";
					position: absolute;
					top: 250px;
					left: calc(50% - 8px);
					@include blue-spinner;
				}
			}

			.ember-power-select-option.custom-select-option {
				word-break: break-all;
			}
		}

		.ember-basic-dropdown {
			.ember-power-select-clear-btn {
				display: none;
			}
			.ember-power-select-trigger {
				padding-left: 4px;
			}
		}

		.inline-split {
			display: inline-flex;
			width: 146px;
			flex-direction: column;
			margin-bottom: 0;
			&:nth-child(even) {
				margin-left: 4px;
			}
			&:nth-child(2) {
				margin-top: 0;
			}
			.ember-power-select-selected-item {
				max-width: 110px;
			}
		}

		.ember-form:not(.property-updated) .suggested-field {
			.ember-power-select-trigger,
			&.nested-parent-field + div .ember-power-select-trigger {
				border: 1px solid $alertPartialBorder;
				background: $app-casablanca-light;
				border-left: 3px solid $app-warning-state;
				box-shadow: inset 0px 1px 2px $dropdown-search-shadow-color;

				.ember-power-select-search-input-trigger {
					background: inherit;
				}
			}
		}

		.form-field {
			.form-field--label {
				@include line-clamp;
			}

			.ember-power-select-multiple-option {
				padding-left: 8px;
				.ember-power-select-multiple-remove-btn {
					align-self: baseline;
				}
			}

			.ember-power-select-search {
				padding: 8px 8px 4px;
			}

			.date-time-split-field-wrapper {
				& > div {
					width: 50% !important;
				}
			}

			&.dependent-select-dropdown-field.nested-parent-field {
				margin-bottom: 0;
				& + div {
					.form-field {
						border-left: 1px dotted $primary-border;
						padding-left: 14px;
						width: 100%;
						.form-field--label {
							padding-top: $page-margin-top;
						}
						&.nested-first-level {
							margin-bottom: 0;
						}
					}
				}
			}

			&.checkbox-field {
				// to fix broken border on active state
				input { margin-left: 2px; }

				.form-field--label {
					display: inline;
				}
			}
			&.priority-field {
				.priority-value { max-width: 92px !important; } // To accommodate the overflowing text without colliding with chevron
			}
		}

		.property-update-btn-container {
			width: 100%;
			// padding-bottom: 8px;
			.property-update-btn {
				width: 100%;
				display: flex;
				justify-content: center;
			}
		}

		.ember-power-select-option {
			margin: 4px 8px;
		}
	}

	#prop-update-sticky-container {
		position: sticky;
		bottom: -24px;
		background: $white;
		width: 332px;

		.property-update-btn-container {
			border-top: 1px solid $app-border-primary;
			height: 60px;
			display: flex;
			align-items: center;
			padding: 0px 16px;

			> .property-update-btn {
				width: 100%;
				margin: 0;
			}
		}
	}

	.ui-accordion-item {
		.toggle-suggestion {
			display: none;
		}
		&.ui-accordion-item-expanded .toggle-suggestion {
			@include flexbox-simple(flex-start, center);
		}
	}
}
