.dot-loading:after {
	content: ' .';
	font-size: 35px;
	color: #B6B9BC;
	-webkit-animation: dots 1.25s steps(5, end) infinite;
	-moz-animation: dots 1.25s steps(5, end) infinite;
	-o-animation: dots 1.25s steps(5, end) infinite;
	animation: dots 1.25s steps(5, end) infinite;
}

// Loader styles
.ember-loader {
	width: 20px;
	height: 20px;
	-webkit-animation: spin 1s linear infinite;
	-moz-animation: spin 1s linear infinite;
	animation: spin 1s linear infinite;
}

.fs-animation {
	width: 100%;
	height: 100%;
	background: url('/a/assets/images/logos/app-loader.svg') no-repeat center center;
	background-size: 88px 88px;
	padding: 100px 0;

	&.fs-animation-large {
		background-size: 176px 176px;
	}
}