.agent-group-wrapper {
	button, div{
		font-family: inherit;
	}
	.ember-basic-dropdown-content {
		position: fixed !important;
	}
	.assign-to-tooltip {
		position: fixed !important;
		max-width: 400px;
		word-break: break-word;
		transition: opacity .5s ease-in-out;
		z-index: 100;
		// hack to hide duplicate tooltip
		.tooltip-inner {
			div { display: none; }
			span + div { display: block; }
		}
	}
	.ember-basic-dropdown {
		position: inherit;
	}

	.agent-group-trigger {
		width: 100%;
		height: 32px;
		padding: 4px 8px 4px 8px;
		margin-top: 1px;
		line-height: $baseLineHeight;
		border-radius: 4px;
		cursor: pointer;
		@include flexbox-simple(flex-start, center);

		.dropdown-chevron {
			display: block;
			float: right;
			width: 16px;
			height: 20px;
			position: relative;
			top: 1px;
			fill: $disabled-text-color;
		}

		&:hover, &[aria-expanded] {
			background: $white;
			box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.05);
		}

		&:focus {
			z-index: 0;
			outline: none;
			box-shadow: 0 0 0 2px $inputFocusBorder;
		}

		&:hover,
		&:focus, &[aria-expanded] {
			.dropdown-chevron {
				fill: $primary-text-color-1;
			}
		}
	}

	.assign-btn-trigger {
		&[aria-expanded] {
			.assign-btn {
				background: $background-color-2;
				border-color: $btnBorder;
				box-shadow: none !important;
			}
		}
		&:focus {
			box-shadow: none;
			.assign-btn {
				outline: none;
				box-shadow: 0 0 0 2px $inputFocusBorder
			}
		}
	}


	.agent-group-non-trigger {
		width: 100%;
		display: inline-block;
	}

	.agent-group-trigger, .agent-group-non-trigger {
		.group-agent-result {
			width: calc(100% - 16px);
			@include flexbox;
		}
		.group-ellipsis {
			max-width: 40%;
			display: inline-block;
		}
		.agent-ellipsis {
			max-width: 50%;
			display: inline-block;
		}
	}

	.loader-container {
		align-items: center;
		display: flex;
		position: relative;
		top: 100px;

		svg {
			fill: $app-primary;
			height: 28px;
			width: 28px;
		}
	}

	.selected {
		color: $app-primary;
	}

	.nested-selected {
		font-weight: 600;
		background-color: $dropdown-option-selected !important;

		svg {
			fill: $app-primary;
		}
	}

	.not-available {
		cursor: auto;
		color: $disabled-text-color;
		padding: 9px 10px;
	}

	.agent-group-item {
		padding: 8px 10px;
		line-height: 16px;
		height: 32px;
		border-radius: 4px;
		margin-bottom: 5px;
		font-family: inherit;
		font-weight: $text-weight-normal;

		&:hover {
			background-color: $background-color;
		}

		&.basic-dropdown-item {
			&.btn-list-item {
				@include flexbox-simple(flex-start, center);
				span {
					flex-grow: 1;
				}

				svg {
					flex-shrink: 0;
				}
			}
		}
	}

	.trim-right-border-radius {
		border-radius: 4px 0 0 4px !important;
		border-right: 1px solid $dropdown-hover !important;
	}
	.trim-left-border-radius {
		border-radius: 0 4px 4px 0 !important;
		border-left: 1px solid $dropdown-hover !important;
	}
	.clip-right-box-shadow {
		clip-path: inset(-18px 0px -18px -18px);
	}
	.clip-left-box-shadow {
		clip-path: inset(-18px -18px -18px 0px);
	}

	.agent-group-item, .selected {
		span {
			width: 190px;
			display: inline-block;
		}
	}

	.agent-group-content {
		width: 250px;
		height: 450px;
		padding: 0 !important;
		cursor: pointer;
		z-index: 1001 !important;
		margin-top: 5px !important;

		&.assign-to-btn-position {
			margin-top: 12px;
		}

		.group-trigger {
			width: 222px;
		}

		.search_group_agent {
			position: relative;
			display: flex;
			align-items: center;
			border-bottom: 1px solid #eee;
			padding: 0 4px 0 16px;
			height: 41px;

			svg.dropdown-search {
				fill: $secondary-text-color-1;
			}

			&:focus-within {
				border-bottom: 1px solid $inputHoverBorder;
			}

			.agent-group-search-input {
				width: 210px;
				height: 40px;
				border: none;
				border-radius: 0;
				box-shadow: none;
				font-family: inherit;
				&:focus {
					box-shadow: none;
					border: none;
				}
				&::placeholder {
					color: $secondary-text-color-1 !important;
				}
			}
		}

		.content-with-selected-option {
			height: calc(331px - 32px) !important;
		}

		.agent-group-tab {
			padding: 0;
			margin: 0;
			border: none;

			.tabs__items {
				display: table-row;
				.tabs__item__nav {
					margin: 0;
					padding: 0 6px;
					&.agent-tab {
						float: right;
						margin-left: 5px;
					}
				}
				.group-tab {
					margin-left: 10px;
				}
				.tab-title .title {
					display: inline-block;
					width: 55px;
					vertical-align: middle;
					font-size: inherit;
				}

				.has-content-count::after {
					background: $white;
					border: 1px solid $border-light-color;
					box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
					padding: 1.5px 8px;
				}
				.tabs__item.active {
					.has-content-count::after {
						background: $inputFocusBorder;
						color: $white;
						border: none;
						padding: 1.5px 8px;
					}
				}
				.ember-tooltip {
					margin-bottom: 0px !important;
				}
			}
		}

		.agent-group-content-body {
			width: 250px;
			height: 331px;
			overflow-x: hidden;
			overflow-y: auto;

			.tabs__content__pane {
				padding: 0;
			}
		}

		.heading-section,
		.agent-group-section {
			padding: 10px;

			.dropdown-arrow-right {
				float: right;
				transform: rotate(270deg);
				fill: $app-primary-black;
				width: 12px;
				height: 17px;
			}

			.agent-group-item:focus {
				background-color: $background-color;
			}
		}

		.heading-section {
			@include flexbox;
			@include flex-direction(column);
			padding: 8px 10px;
			font-size: 12px;
			background-color: $background-color-2;

			.dropdown-arrow-right {
				fill: $inputFocusBorder;
				width: 10px;
				height: 20px;
			}

			.selected-group-trigger {
				width: 222px;
			}

			button {
				padding: 0 10px;
				margin-bottom: 0;
				background-color: inherit;
				font-weight: 600 !important;
				border-radius: 4px;
				span {
					vertical-align: middle;
				}
				&:hover {
					color: $app-primary;
				}
				&:last-of-type {
					margin-top: 10px;
				}
				&.none-option, &.assign-to-me {
					margin-top: 0 !important;
				}
			}
		}
	}

	.nested-agent-group-content {
		width: 250px;
		height: 450px;
		padding: 10px !important;
		overflow-y: auto;
		border: none !important;
		z-index: 1000 !important;

		.nested-agent-search {
			margin-bottom: 6px;
		}

		.basic-dropdown-item.active, .nested-selected {
			color: $app-primary;
		}
	}

	.agent-group-content, .nested-agent-group-content {
		.assign-to-tooltip{
			max-width: 200px;
		}
	}

	@keyframes drop-fade-right {
		0% {
			transform: translateX(-30px);
		}
		100% {
			transform: translateX(0px);
		}
	}
	.slide-right-fade {
		&.ember-basic-dropdown--transitioning-in {
			animation: drop-fade-right 0.5s;
		}
	}

	@keyframes drop-fade-left {
		0% {
			transform: translateX(30px);
		}
		100% {
			transform: translateX(0px);
		}
	}
	.slide-left-fade {
		&.ember-basic-dropdown--transitioning-in {
			animation: drop-fade-left 0.5s;
		}
	}

	//Avatar related CSS
	&.assign-to-avatar-trigger{
		.avatar__icon, .avatar__initial{
			height: 24px;
			width: 24px;
			font-size: $baseFontSize;
			padding: 0 0 1px 0;
		}
		.agent-group-trigger, .agent-group-non-trigger{
			padding: 0;
			width: 24px;
			.group-agent-result {
				width: 100%;
			}
			&:hover, &[aria-expanded]{
				box-shadow: none;
				.assign-to-svg.add-user{
					background: $background-color-2;
					border: 1px dashed $svgFocusedBorder;
				}
			}
			&:focus {
				box-shadow: none;
				.assign-to-svg.add-user, .assign-to-svg.group, .avatar__icon--circle img, .avatar__icon--circle  .avatar__initial{
					// box-shadow: 0 0 0 2px $inputFocusBorder;
					border: 2px solid$inputFocusBorder;
				}
			}
			.assign-to-svg{
				height: 24px;
				width: 24px;
				border-radius: $svgRadius;
				@extend .flex-center;
				svg{
					padding: 4px 4px 6px 4px;
				}
				&.add-user{
					border: 1px dashed $unassignedBorder;
					path{
						color: $app-grey;
					}
					&:hover{
						background: $background-color-2;
						border: 1px dashed $svgFocusedBorder;
					}
				}
				&.group{
					background: $assignedGroupAvatarBg;
					svg path{
						fill: $white;
					}
				}
			}
		}
	}
}

.ticket-summary-modal-top-actions .agent-group-wrapper {
	.group-agent-result.hint--top::after {
		white-space: nowrap;
		height: 14px;
	}
}
