.time-entry-content-container {
	@include border-style($background-color, 4px);
	margin: 8px 0 0 0;
	padding: 16px 16px 0 16px;
	.top-flex {
		@include flexbox;
		@include justify-content(space-between);
		.time-spent {
			width: 70%;
			font-weight: $text-weight-semi-bold;
			font-size: $title-large;
			margin-bottom: 10px;
		}
		.billable {
			@include border-style($border-dark-color, 4px);
			background: $white;
			padding: 2px 8px;
			font-size: $text-normal;
			height: 24px;
		}
	}
	.action-btns {
		@include flexbox;
		@include align-items(center);
		height: 20px;
		margin-bottom: 14px;

		.btn-link.time-entry-action {
			font-size: $text-normal;
			line-height: 20px;
			padding: 0;
			margin-right: 12px;
			font-weight: $text-weight-medium;
			&::after {
				content: "|";
				font-size: $text-normal;
				position: relative;
				top: -1px;
				margin-left: 12px;
				color: $border-dark-color;
			}
			&:last-child {
				&::after {
					display: none;
				}
			}
		}
	}
	.agent-details {
		@include flexbox;
		height: 40px;
		.avatar-container {
			height: 40px;
			margin-right: 8px;
			.avatar__icon {
				margin-top: 4px;
				.avatar__initial {
					width: 24px;
					height: 24px;
					font-size: $baseFontSize;
				}
			}
		}

		.details-container {
			@include flexbox;
			@include flex-direction(column);
			width: calc(100% - 32px);
			.agent-name {
				font-weight: $text-weight-medium;
				font-size: $title-small;
				color: $primary-text-color-1;
				@include line-clamp;
			}
			.executed-on {
				font-weight: $text-weight-normal;
				font-size: $text-normal;
				color: $secondary-text-color-1;
			}
		}
	}
	.time-entry-note {
		margin-top: 10px;
		.view-more-toggle {
			margin-top: 0px;
		}
	}
}
