.page-error.empty-state-container {
	display: flex;
	background-color: $white;
	align-items: center;
	justify-content: center;
	height: calc(100vh - 62px);
	flex-direction: column;

	.info {
		margin-top: 58px;
		display: flex;
		flex-direction: column;
		align-items: center;

		h3 {
			color: $primary-text-color-1;
			font-weight: $text-weight-semi-bold;
			font-size: $text-large;
		}

		p {
			color: $secondary-text-color-1;
			margin-top: 20px;
			font-size: $text-large;
		}
	}
	&.assumed-identity {
		.info {
			width: 400px;
			text-align: center;
			margin-top: 42px;
		}
		a {
			margin-top: 20px;
			font-size: $text-large;
			font-weight: $text-weight-medium;
		}
	}
}
