.jira-create-form {
	.form-field {
		margin-bottom: 5px;
		label {
			word-break: normal !important;
		}
	}
	.jira-summary  {
		width: 100%;
	}
	.form-field--control {
		width: 100%;
	}
}
.jira-widget{
	.link-jira-button {
		background: $white;
		svg{
			float: left;
		}
	}

	.jira-issue-container{
		.jira-title {
			padding-left: 8px;
		}
	}

	color: $primary-text-color-1;
	padding: 8px 16px;
	.title {
		font-size: 14px;
		font-weight: $text-weight-semi-bold;
		margin-bottom: 4px;
		font-style: normal;
		line-height: 20px;
		letter-spacing: 0px;
		text-align: left;

	}
	.sub-title {
		color: $inputHoverBorder;
		font-size: $text-normal;
		padding-bottom: 10px;
	}
	.btn-primary {
		margin-top: 8px;
	}
	.label, .value {
		font-size: $text-normal;
		display: inline-block;
	}
	.value {
		font-weight: $text-weight-semi-bold;
	}
	.jira-container {
		margin: 0 auto 0 0;
		// padding-top: 10px;
		.linked-issue {
			.jira-buttons {
				padding-top: 10px;
			}
			.jira-subject {
				font-size: $baseFontSize;
				color: $primary-text-color-1;
			}
		}
		.link-plus {
			margin-right: 4px;
			margin-top: 4px;
		}
		.unlink-icon {
			width: 14px;
			height: 14px;
			margin-right: 8px;
			float: left;
		}
		.delete-icon {
			fill: $app-red;
			width: 14px;
			margin-right: 8px;
			float: left;
		}
		.strike-through {
			text-decoration: line-through;
		}
		.jira-no-issue-linked{
			// padding-top: 12px;
		}
	}
	.jira-table {
		display: table;
		padding-top: 10px;
		margin-bottom: 0px !important;
	}
	.jira-table-row {
		display: table-row;
	}
	.jira-table-cell {
		// display: table-cell;
		padding-bottom: 8px;
		// width: 85px;
	}
	.created-on {
		padding-bottom: 18px;
	}
	.jira-buttons {
		display: flex;
		.btn-without-border{
			border: none;
			background: transparent;
			color: $linkColor;
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
			text-align: left;
			cursor: pointer;
			padding: 0px;
			display: flex;
			align-items: center;
		}
		.delete-issue {
			padding-left: 64px;
		}
	}
	.label-jira, .label-value, .line-clamp-summary {
		color: $app-form-label-color;
		font-size: 12px;
		font-style: normal;
		font-weight: 400;
		line-height: 20px;
		letter-spacing: 0px;
		text-align: left;
		display: inline;
	}

	.label-value{
		font-weight: 600;
		color: $primary-text-color-1;
	}
	.line-clamp-summary{
		font-size: 14px;
		font-weight: 600;
	}
}
.select-jira-issue-type {
	display: flex;
	margin-bottom: 28px;
	margin-top: 8px;
	.radio-field {
		label {
			display: inline-block;
		}
	}
	.existing-issue-radio {
		padding-left: 70px;
	}
}
.jira-link-issue {
	.jira-link-issue-field {
		width: 100%;
	}
	label {
		color:$secondary-text-color-1;
		font-size: $text-normal;
		font-weight: $text-weight-semi-bold;
	}
}
.ui-accordion-panel-wrapper{
	padding-left : 3px;
}

//Date Time Field
.date-picker-wrapper {
	.basic-dropdown-toggle{
		float: right;
	}
}
.date-time-split-field-wrapper{
	display: flex;
	.date-control{
		width: 175px;
	}
	.time-control{
		width: 100px;
	}
}

//Time Tracking Field
.jira-time-tracking {
	div {
		margin-top: 0px !important;

	}
}


.multiline-truncate{
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2;
	overflow: hidden;
}