.tcpr-widgets {
	height: 100%;
	padding: 16px 16px 24px;
	position: absolute;
	overflow-y: scroll;
	overflow-x: hidden;
	scroll-behavior: smooth;

	// Every widget on the right page is a '.tcpr-widget'
	// Generic style setup of the widget accordions
	.tcpr-widget {
		width: 334px;
		@include border-style(transparent, 8px);
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.01);
		padding: 0;
		margin-bottom: 8px;
		box-sizing: border-box;
		background: $app-white;

		&:hover {
			@include transition(0.1s linear);
			@include border-style($border-light-blue, 8px)
		}

		> .ui-accordion-list {
			.ui-accordion-item {
				&-expanded {
					padding-bottom: 16px;
					.ui-accordion-panel-wrapper {
						padding: 0 16px 8px 16px;
					}
				}
				.ui-accordion-header {
					// Generic setting for all accordion headers' chevron icon
					.ui-accordion-header-trigger {
						height: 18px;
						@include flexbox-simple(space-between, center);
						padding: 19px 16px;

						// All headers
						.accordion-title-wrap,
						.marketplace-header {
							@include flexbox-simple(normal, center);
							font-weight: $text-weight-semi-bold;
							color: $primary-text-color-1;
							font-size: $title-small;
							width: 100%;
							max-width: 334px;
							background: $white;
							border: none;
						}
					}
				}
				.ui-accordion-panel-wrapper {
					padding: 0 16px 0 16px;
					.ui-accordion-panel-content {
						.shimmer-loader {
							margin-left: -16px;
							.shimmer-loader-item:last-of-type { margin-bottom: 0; }
						}
					}
				}
			}
		}
	}

	.selected-widget-border {
		border: 1px solid $inputFocusBorder !important;
	}
}

.ticket-details-sidebar {
	.shimmer-loader-container {
		width: 334px;
		.group--body {
			margin: 0;
			border-top: none;
		}
	}
}

.custom-scroll {
	padding: 16px 0 24px 16px;
}

.tcpr-widgets-icon {
	&.loading {
		padding: 0px !important;
	}
	right: 0;
	width: 48px;
	height: 100%;
	padding: 8px;
	display: flex;
	position: fixed;
	background: $white;
	flex-direction: column;
	border-left: 1px solid $border-light-color;

	.default-app-navigation, .custom-app-navigation {
		padding-top: 8px;
	}

	.custom-app-navigation {
		.pagerduty {
			svg { width: 14px; height: 14px; }
		}
		.freshdesk {
			svg {
				width: 18px;
				height: 18px;
				margin-left: -2px;
			}
		}
	}

	.default-app-navigation {
		.selected-widget-icon-border {
			svg { path { fill: $app-primary; } }
		}
	}

	.selected-widget-icon-border {
		box-sizing: border-box !important;
		border: 1px solid $app-primary !important;
		box-shadow: $widget-icon-border-color;
	}

	.default-app-border {
		border-bottom: 1px solid $border-light-color;
	}

	.ticket-quick-icon {
		width: 32px;
		height: 32px;
		border: none;
		border-radius: 4px;
		margin-bottom: 8px;
		background: $white;
		display: flex;
		align-items: center;
		justify-content: center;
		svg {
			width: 16px; height: 16px;
		}
		img {
			width: 18px; height: 18px;
			margin-left: -2px;
		}
		&:hover {
			cursor: pointer;
			background: $border-light-color;
			transition: 0.3s linear;
		}
	}

	.ticket-default-icon {
		svg {
			path {
				fill: $app-grey;
			}
		}
	}

	.quick-action-tooltip {
		width: max-content;
		margin: 0px 10px !important
	}
}
