.harvest {
	background: $white;
	// border: 1px solid $app-border-primary;
	// border-radius: 4px;
	padding-top: 8px;
	padding-left: 17px;
	.harvest-box {
		padding: 0 140px 0px 7px;
	}
	.harvest-header-line {
		display: flex;
		.harvest-logo {
			padding-right: 16px;
			margin-left: auto;
		}
	}

	.login-form {
		padding-top: 24px;
		.login-form-label, .form-field--label {
			padding-bottom: 2px;
		}
		.form-field--password {
			padding-top: 16px;
		}
		.login-buttons {
			padding-top: 20px;
			display: flex;
			justify-content: flex-end;
			.button-padding {
				padding-left: 10px;
			}
			.button{
				height: 32px;
			}
			.cancel-button {
				.cancel-btn {
					background: $btnBackground;
					border: 1px solid #CFD7DF;
					box-sizing: border-box;
					border-radius: 4px;
					height: 32px;
				}
			}
		}
	}
	.harvest-text, .form-field--label {
		font-style: normal;
		font-size: $text-normal;
		line-height: 20px;
		font-weight: $text-weight-normal;
	}
	.harvest-header {
		font-size: $title-small;
		font-weight: $text-weight-semi-bold;
	}
	.login-form-show {
		padding-left: 0px;
		border: none;
		background: none;
		font-weight: $text-weight-medium;
		color: $linkColor;
		padding-bottom: 5px;
	}
	.ember-basic-dropdown {
		padding-right: 10px;
	}
	.form-field--label {
		font-weight: $text-weight-semi-bold;
		color: $app-form-label-color;
	}

}

.error-field{
	input{
		border-color: $app-red;
	}
}

.harvest-unchecked {
	height: 40px;
	background: $app-sidebar-bg;
	border: 1px solid $border-dark-color;
	padding-bottom: 10px;
}