@import './harvest';

.hide {
	display: none;
}

.native-integration-app-container {
	.time-sheet-native-apps {
		.app-item {
			border:$border-dark-color 1px solid;
			border-radius: 8px;
			background-color: $background-color-2;
			padding: 8px 16px;
			cursor: pointer;
			margin-bottom: 16px;
			.native-app-item-header {
				@include flexbox-simple(space-between, center);
			}
			&:last-child{
				margin-bottom: 0;
			}
			span {
				font-weight: 600;
				span.star {
					display: none;
				}
			}
			&.selected {
				background-color: $white;
			}
			.hide-app-sec {
				display: none;
			}
			.app-item-icon {
				display: inline-block;
				float: right;
				height: 22px;
			}
			.container {
				width: 80%;
				margin: 30px 0 0 30px;
				.fieldset-wrapper {
					.select-field {
						margin-bottom: 16px;
					}
					.textarea-field {
						margin-bottom: 16px;
						textarea {
							width: 100%;
							height: 75px;
						}
					}
				}
			}
		}
	}
}
