.ui-list-item {
	height: 72px;
	box-sizing: border-box;
	padding: 16px;
	margin-bottom: 8px;
	border-radius: 4px;
	border: 1px solid $border-light-color;
	box-shadow: 0 1px 2px 0 $list-item-shadow;

	/* all buttons are hidden by default, only shown on hover */
	.btn {
		visibility: hidden;
	}

	.link-text {
		font-weight: $text-weight-medium;
	}

	&:hover {
		background-color: $background-color-2;
		cursor: pointer;

		& .btn {
			visibility: visible;
		}

		/* change to link color if any anchor present as direct child */
		.link-text {
			color: $linkColor !important;
		}
	}
}
