.entity-table-wrapper {
	.column-customizer {
		width: 32px;
		height: 39px;
		position: absolute;
		right: 0;
		z-index: 8;
		top: 4px;
		@include flex-center;
		box-shadow: -4px 0px 5px $box-shadow-color-1;

		background-color: $white;
		border-right: 1px solid $border-light-color;
		cursor: pointer;

		.is-dragging {
			box-shadow: 0 0 0 2px $linkColor;
			.remove-column { opacity: 0; }
		}

		.ember-basic-dropdown-content {
			margin-top: 24px;
			width: 500px;
			max-height: 500px;
			padding: 20px 24px 0;
			cursor: default;
			overflow: hidden;
			border: 0;
			right: 0;
		}

		.ember-basic-dropdown-trigger {
			height: 100%;
			width: 100%;
			@include flex-center;
		}

		.column-customizer-table-preference-wrapper {
			h5 { font-weight: $text-weight-bold; }
			.table-view-preference {
				margin: 12px 0 20px 0;

				.table-view-type {
					display: inline-flex;
					align-items: center;
					margin-right: 16px;
					padding: 5px 10px;
					border: 1px solid transparent;

					label {
						padding-bottom: 0;
						cursor: pointer;
						font-weight: $text-weight-semi-bold;
					}

					input {
						margin-right: 8px !important;
					}

					&.active {
						background: $background-color-2;
						border-color: $border-dark-color;
						border-radius: 36px;
					}
				}
			}
		}

		.column-customizer-title {
			font-weight: $text-weight-bold;
			margin-bottom: 12px;
		}

		.column-customizer-wrapper {
			@include flexbox;

			h5 { font-weight: $text-weight-bold; }

			& > section {
				/* calculated fixed height */
				height: 319px;
			}

			section.module-table-headers {
				width: 210px;
				.module-table-selection-headers {
					/* calculated fixed height & wdith */
					height: 235px;
					width: 210px;
					overflow: auto;
					padding: 0 16px 50px 0;
				}
			}

			section.module-table-selected-headers {
				overflow: auto;
				/* calculated fixed width */
				width: 240px;
			}

			.header {
				& > div {
					background: $app-white;
					border-radius: 4px;
					@include flexbox-simple(flex-start, center);
					width: 176px;
					min-height: 28px;

					&:hover {
						box-shadow: 0px 3px 3px $box-shadow-color-2;
					}
				}

				.handle {
					@include flexbox-simple(flex-start, center);
					width: 18px;
					background: none;
					border: none;
					padding: 0;
					cursor: grab;
					margin-left: 5px;

					svg {
						fill: $app-primary-black;
					}
				}

				span.ellipsis {
					max-width: 125px;
					display: inline-block;
					font-weight: $text-weight-medium;
				}

				button {
					min-width: 20px;
					height: 20px;
					margin-left: 0px;
					margin-right: 0px;
				}
			}

			.module-table-headers,
			.module-table-selected-headers {
				width: 50%;
			}

			.module-table-headers {
				padding-left: 0;
				padding-top: 4px;
				position: relative;

				#no-results {
					@include flexbox;
					cursor: default;
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;
					margin-top: 12px;
				}

				.search-headers {
					position: relative;
					width: calc(100% - 20px);

					.form-field {
						@include flexbox-simple(flex-start, center);

						button {
							position: absolute;
							right: 0;
							min-width: auto;
							height: auto;
							top: 7px;
						}

						span {
							position: absolute;
							top: 8px;
							left: 12px;

							svg {
								fill: $disabled-text-color;
							}
						}

						input {
							padding: 0 8px 0 36px !important;
							::-ms-clear {
								display: none; width : 0; height: 0;
							}
						}
					}
				}

				#selection-headers-title {
					font-weight: $text-weight-bold;
					font-size: $text-normal;
					margin: 12px 0 8px;
					cursor: default;
					display: inline-block;
					color: $primary-text-color-1;
				}
			}

			.module-table-selected-headers {
				/* new color */
				background: #eceff3;
				border-radius: 4px;
				padding: 0 4px 50px 24px;

				#selected-headers-title {
					font-weight: $text-weight-bold;
					margin: 12px 0 20px;
					font-size: $text-normal;
					cursor: default;
					display: inline-block;
					color: $primary-text-color-1;
				}
			}

			.module-table-header,
			.module-table-selected-header {
				@include flexbox-simple(flex-start, center);
			}

			.module-table-header {
				margin-top: 8px;
				margin-bottom: 16px;
				display: flex;
				align-items: center;

				& > div {
					display: flex;
					align-items: center;
				}

				input {
					padding: 6px;
					border-radius: 2px;
					min-width: 14px;
					margin-left: 2px;

					& + label {
						padding: 0 0 0 8px;
						word-break: break-all; /* IE fix */
						word-break: break-word;
						cursor: pointer;
					}
				}

				input[disabled] {
					background-color: $border-light-shade-blue !important;
				}

				input[type='checkbox'] {
					&:checked:after {
						left: 4px;
					}

					&[disabled]:checked:after {
						border-color: $app-white !important;
					}
				}

				&.nested {
					padding-left: 16px;
				}

				&.has-nested {
					display: block;
					& > div {
						display: flex;
						align-items: center;
					}
				}

				ul {
					margin-top: 16px;
				}
			}

			.module-table-selected-header {
				margin-bottom: 8px;
				width: 176px;

				.freeze-column,
				.remove-column {
					opacity: 0;
				}

				&:hover {
					.freeze-column,
					.remove-column {
						opacity: 1;
					}
					.remove-column svg { fill: $app-primary-black; }
				}

				.module-table-normal-selected-header {
					& > div {
						border: 2px solid transparent;
					}
					.ember-tooltip {
						margin-right: 45px !important;
					}
				}

				&:focus {
					.module-table-normal-selected-header {
						& > div {
							border-color: $linkColor;
						}
					}

					&.nested {
						& > .module-table-nested-selected-header {
							& > div {
								border-color: $linkColor;
							}
						}

						.module-table-nested-selected-header-nested-headers {
							border-color: $linkColor;
						}
					}

					.module-table-nested-children-selected-header {
						& > div {
							border-color: $linkColor;
						}
					}
				}

				&:focus-within {
					.freeze-column,
					.remove-column {
						opacity: 1;
					}
				}

				.module-table-normal-selected-header,
				.module-table-nested-children-selected-header {
					@include flexbox-simple(flex-start, center);
					@extend .header;
				}

				.module-table-nested-children-selected-header {
					.nested-child {
						@include flexbox-simple(flex-start, center);
						padding: 4px 0;
						margin-left: 6px;
					}

					& > div {
						border: 2px solid transparent;
					}

					div > div:not(.ember-tooltip) {
						max-width: 125px;
					}
				}

				.module-table-normal-selected-header,
				.module-table-nested-selected-header,
				.module-table-nested-children-selected-header {
					.freeze-column,
					.frozen-column {
						margin-left: auto !important;
						margin-right: 4px !important;
					}
				}

				&.nested {
					@include flex-direction(column);
					@include align-items(flex-start);
					position: relative;

					&:hover:before {
						content: "";
						box-shadow: 0px 3px 3px $box-shadow-color-2;
						height: 1px;
						position: absolute;
						bottom: 0;
						width: 181px;
					}

					.module-table-nested-selected-header {
						@include flexbox-simple(flex-start, center);

						& > div {
							border: 2px solid transparent;
							border-bottom: none;
							border-bottom-left-radius: 0;
							border-bottom-right-radius: 0;

							&:hover {
								box-shadow: none;
							}
						}

						.ember-tooltip {
							margin-left: -56px !important;
						}

						@extend .header;
					}

					.module-table-nested-selected-header-nested-headers {
						width: 176px;
						background: $app-white;
						border-bottom-left-radius: 4px;
						border-bottom-right-radius: 4px;
						border: 2px solid transparent;
						border-top: none;

						span.ellipsis {
							font-weight: initial !important;
						}

						.module-table-nested-selected-header {
							& > div {
								padding-left: 40px;
								border-radius: 0;
							}
						}

						.module-table-nested-selected-header:last-child {
							& > div {
								border-bottom-left-radius: 4px;
								border-bottom-right-radius: 4px;
							}
						}
					}
				}
			}
		}

		.column-customizer-footer-buttons {
			position: sticky;
			bottom: 0;
			background: $background-color-2;
			@include flexbox-simple(space-between, center);
			height: 48px;
			width: calc(100% + 48px);
			margin-left: -24px;
			border-bottom-left-radius: 4px;
			border-bottom-right-radius: 4px;
			padding-right: 16px;
			border: 1px solid $border-light-color;

			.btn-cancel {
				margin-left: auto;
				min-width: 80px;
			}
			.btn-primary {
				margin-right: 8px;
				min-width: 80px;
			}
		}
	}

	#info-banner + .column-customizer {
		top: 57px !important;
	}
}
