.import-modal{
	.step-header{
		.step-number{
			display: inline-block;
			background-color: $app-grey;
			border-radius: 16px;
			color: $white;
			padding: 2px 8px;
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
		}
		.step-name{
			display: inline-block;
			padding: 8px;
			font-weight: $text-weight-medium;
			font-size: $title-small;
		}
	}
	#step-upload{
		.import_info{
			margin: 8px 0;
			border: 1px solid $border-dark-color;
			border-radius: 2px;
			background-color: $background-color-2;
			padding: 16px;
		}

		.import-info-action-footer {
			margin-top: 8px;
			display: flex;
			justify-content: space-between;
		}

		.import_upload{
			margin: 16px 0;

			.upload-container{
				height: 190px;
				border: 1px dashed $app-border-primary;
				border-radius: 2px;
				position: relative;
				#upload-label{
					cursor: pointer;
					#upload_file{
						position: absolute;
						left: calc(50% - 50px);
						top: 20px;
						width: 100px;
						height: 100px;
					}
					#upload_label{
						position: absolute;
						left: calc(50%);
						transform: translateX(-50%);
						top: 125px;
						color: $app-primary;
						font-weight: $text-weight-semi-bold;
					}
					.secondary_label {
						color: $secondary-text-color-2;
						position: absolute;
						left: calc(50% - 113px);
						top: 145px;
					}
				}

				&.dropZone-over {
					border-color: $inputFocusBorder;
					background-color: $background-color-2;
				}
			}
			.file-container{
				height: 180px;
				border: 1px dashed $app-border-primary;
				border-radius: 2px;
				background-color: $background-color-2;
				position: relative;
				.file_details{
					height: 68px;
					width: 360px;
					border: 1px solid $app-primary;
					border-radius: 4px;
					background-color: $app-white;
					box-shadow: 0 2px 6px 0 rgba(0,0,0,0.16);
					position: absolute;
					top: 40px;
					left: 50%;
					padding: 8px;
					transform: translateX(-50%);
					display: flex;
					align-items: center;

					#file_img{
						height: 36px;
						width: 36px;
					}
					#file-name{
						padding: 10px;
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
						flex: 1;
					}
					.close-icon {
						cursor: pointer;
						margin-right: 8px;

						#remove_attachment{
							height: 16px;
							width: 16px;
						}
					}
				}
			}
		}
	}
	#step-field-mapping{
		height: 100%;

		.mapping-loader {
			height: 100%;
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;

			.ember-loader {
				width: 30px;
				height: 30px;
			}
		}

		.step-body{
			#field-mapping-header{
				border-radius: 2px;
				padding: 4px 8px;
				background-color: $background-color-2;
				margin: 8px 0;
				span{
					display: inline-block;
					width: 40%;
					font-weight: $text-weight-semi-bold;
				}
			}
			#field-mapping-body{
				margin: 8px 0;
				.field{
					margin: 8px 4px 8px;
					display: flex;
					position: relative;
					label{
						width: 40%;
						display: inline-block;
						margin-top: 6px;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						.req-star{
							color:$app-red;
							margin-left: -2px;
						}
					}
					.import-field{
						width: 35%;
						display: inline-block;
					}
					.selected-data{
						width: 20%;
						display: inline-block;
						margin-left: 8px;
						margin-top: 9px;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
						font-size: $text-normal;
					}
					&.invalid-true{
						margin-bottom: 30px;
						.field-validation-error{
							position: absolute;
							top: 35px;
							left: 40%;
							color: $app-red;
						}
					}
				}
			}
		}
	}
	#step-import-status{
		.status-details{
			height: 71px;
			width: 537px;
			border: 1px solid $border-dark-color;
			border-radius: 4px;
			background-color: $app-white;
			box-shadow: 0 2px 6px 0 rgba(0,0,0,0.04);
			display: flex;
			.status-icon{
				display: flex;
				align-items: center;
				width: 60px;
				padding: 16px;
				.status-loader{
					width: 32px;
					height: 32px;
					fill: $app-primary;
					overflow: visible;
				}
				.status-sym{
					fill: $app-white;
					border-radius: 50%;
					width: 28px;
					height: 28px;
					path{
						fill: $app-white;
					}
					&.status-failure{
						background-color: $app-red-1;
					}
					&.status-success{
						background-color: $app-green;
						padding: 4px;
						width: 20px;
						height: 20px;
					}
					&.status-partial{
						background-color: $app-warning-state;
						padding: 4px;
						width: 20px;
						height: 20px;
					}
				}
			}
			.status-content{
				flex-grow: 1;
				display: flex;
				flex-direction: column;
				justify-content: center;
				margin-bottom: 5px;
				max-width: calc(100% - 180px);
				overflow: hidden;

				h4{
					font-weight: $text-weight-medium;
				}
			}

			.status-content-started-by {
				display: block;
				font-size: $text-normal;
				color: $secondary-text-color-1;
				margin-top: 3px;
				text-overflow: ellipsis;
				white-space: nowrap;
				overflow: hidden;
			}
			.status-pill{
				padding: 20px;
				display: flex;
				align-items: center;
				min-width: 120px;
				justify-content: center;
				.state{
					border-radius: 4px;
					padding: 4px 8px;
					margin-left: 8px;
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;

					&.loading{
						border: 1px solid $border-light-blue;
						background-color: $section-background-blue;
						color: $app-primary;
					}
					&.failure{
						border: 1px solid $alertWarningBorder;
						background-color: $alertWarningBackground;
						color: $app-red-1;
					}
					&.success{
						border: 1px solid #B4E5D9;
						background-color: #E0F5F0;
						color: #007958;
					}
					&.partial{
						border: 1px solid #FDDBB5;
						background-color: $app-casablanca-light;
						color: #C13E1B;
					}
				}
			}
		}
		#import_result_header{
			font-weight: $text-weight-medium;
			color: $inputHoverBorder;
			margin-top: 16px;
			margin-bottom: 8px;
		}
		.status-loading{
			border: 1px solid #FDDBB5;
			border-radius: 4px;
			background-color: $app-casablanca-light;
			margin-top: 16px;
			.import-banner{
				display: flex;
				.status-icon{
					padding: 8px 4px;
					.status-info-icon, .status-info-icon path{
						height: 16px;
						width: 16px;
						fill: $app-red-1;
					}
				}
				.status-text{
					border-left: 1px solid #FDDBB5;
					padding: 4px;
					margin: 4px;
				}
			}
		}
		.status-count{
			display: flex;
			justify-content: space-around;

			.card{
				width: 120px;
				height: 80px;
				border: 1px solid $border-dark-color;
				border-radius: 4px;
				background-color: $app-white;
				box-shadow: 0 2px 6px 0 rgba(0,0,0,0.04);
				margin: 0 8px;
				padding: 8px;

				&:first-child,
				&:last-child {
					margin: 0;
				}

				.card-title{
					vertical-align: top;
					padding: 0 8px;
					font-size: $text-normal;
					color: $app-grey;
				}
				.count{
					font-size: $title-extra-large;
					font-weight: $text-weight-semi-bold;
					text-align: right;
					margin: 8px 4px;
				}
			}
		}
	}
}