.status-info {
	.first-resp-due, .due-by {
		display: flex;
		padding-top: 8px;
		position: relative;
		align-items: center;
		font-size: $text-normal;
		justify-content: space-between;
		.first-resp-due-left, .due-by-left {
			display: flex;
			flex-direction: column;
			.fr-due-label, .due-by-label {
				color: $app-form-label-color;
				font-weight: $text-weight-medium;
			}
			.fr-due-value, .due-by-value {
				color: $primary-text-color-1;
				font-weight: $text-weight-semi-bold;
			}
			.due-by-left-cont {
				display: flex;
			}
			.change-due-by-form {
				.date-menu-picker-trigger {
					cursor: pointer;
					padding: 1px 0 0 3px;
				}
				.dropdown-datepicker {
					position: fixed !important;
				}
				.ember-basic-dropdown-content {
					z-index: 1;
					padding: 0px;
					margin-top: 25px;
					margin-right: -63px;
				}
				.pull-picker-left-cal {
					margin-right: 0;
					margin-left: -343px;
				}
				.pull-picker-left {
					margin-right: 0;
					margin-left: -88px;
				}
				.dropdown-datepicker__inner {
					display: flex;
					min-height: 335px;
					max-width: 450px;
					.calendar__right {
						width: 255px;
						background: $app-white;
						.date-custom-container {
							height: 87%;
							.form-field--label {
								display: none;
							}
							.edit-date-time-container {
								.date-time-picker-header {
									padding:  4%;
									display: flex;
									background: $background-color-2;
									border: 1px solid $border-light-color;
									.date-custom-picker {
										width: 52%;
										.date-text-label {
											color: $secondary-text-color-1;
											font-weight: $text-weight-normal;
										}
										.date-text-field {
											float: left;
										}
										input {
											font-size: $title-small;
											font-weight: $text-weight-medium;
											background-color: $white !important;
											color: $primary-text-color-1 !important;
										}
									}
									.time-custom-picker {
										width: 48%;
										.time-text-label {
											color: $secondary-text-color-1;
											margin-left: 10%;
											font-weight: $text-weight-normal;
										}
										.shift-time--select {
											margin-left: 10%;
											.ember-power-select-selected-item {
												font-size: $title-small;
												font-weight: $text-weight-medium;
												color: $primary-text-color-1 !important;
											}
											.ember-basic-dropdown-content {
												margin-top: 5px;
												width: 150px !important;
											}
										}
									}
								}
								.ember-power-calendar {
									padding: 10px;
									.ember-basic-dropdown-content {
										margin-top: 5px;
										width: 150px !important;
									}
									.ember-power-calendar-days {
										padding-top: 5px;
									}
									.ember-power-calendar-nav-buttons {
										margin-left: 70%;
										position: absolute;
										button {
											background-color: $app-white;
											&:hover {
												background-color: $dropdown-hover;
											}
											&:focus {
												outline: auto;
												outline-color: $linkColor;
											}
										}
									}
								}
								.time-picker-wrapper {
									margin-left: 0;
									.time-picker-icon {
										right: -5px
									}
								}
							}
						}
						.picker-footer {
							height: 13%;
							.btn-primary.disabled {
								opacity: 1;
								border-radius: 4px;
								mix-blend-mode: normal;
								background: $disabled-button-bg;
								color: $disabled-button-color !important;
								border: 1px solid $disabled-button-border;
							}
							.picker-buttons {
								/*padding-top: 8px;*/
							}
							.picker-error-banner {
								margin: 6px 0 1px 12px;
								font-size: 12px;
							}
						}
						.valid {
							padding: 3% 0;
							color: $primary-text-color-1;
							background: $background-color-2;
						}
						.error {
							color: $primary-text-color-1;
							border-bottom-left-radius: 4px;
							box-sizing: border-box;
							border: 1px solid $alertPartialBorder;
							background: $app-casablanca-light;
						}
					}
					.cal-top-bottom-left-border {
						border-top-left-radius: 4px;
						border-bottom-left-radius: 4px;
					}
					.cal-top-left-border {
						border-top-left-radius: 4px;
					}
					.cal-bottom-left-border {
						border-bottom-left-radius: 4px;
					}
					.cal-top-bottom-right-border {
						border-top-right-radius: 4px;
						border-bottom-right-radius: 4px;
					}
					.cal-all-border {
						border-radius: 4px;
					}
					.calendar__left {
						width: 175px;
						padding: 10px;
						background: $btnPrimaryActiveBackground;
						.calendar__left--link {
							padding: 8px;
						}
						.calendar__left--link:hover, .calendar__left--active {
							border-radius: 4px;
							background-color: $background-color-3;
							color: $secondary-text-color-4;
						}
						button {
							width: 100%;
							border: none;
							text-align: left;
							color: $app-white;
							background: $btnPrimaryActiveBackground;
							&:focus-visible {
								outline: none;
								box-shadow:  none;
							}
							&:focus {
								border-radius: 4px;
								border: 1px solid $app-white;
							}
						}
						.time-details {
							font-size: $text-normal;
							color: $secondary-text-color-3;
						}
						&:after, &:before {
							bottom: 100%;
							border: solid transparent;
							left: 50%;
							content: " ";
							height: 0;
							width: 2px;
							position: absolute;
							pointer-events: none;
						}
						&:after {
							border-color: transparent;
							border-bottom-color: $btnPrimaryActiveBackground;
							border-width: 0px;
							margin-left: 5px;
						}
						&:before {
							border-color: transparent;
							border-bottom-color: $btnPrimaryActiveBackground;
							border-width: 5px;
							margin-left: -4%;
						}
						&.pointer-pos {
							&:after, &:before {
								left: 81.5%;
							}
						}
					}
				}
			}
		}
		.first-resp-due-timer, .due-by-timer {
			display: flex;
			padding: 0 8px;
			align-items: center;
			border-radius: 16px;
			justify-content: center;
			.timer-clock {
				float: left;
				padding-top: 4px;
			}
			.clock-value {
				float: left;
				padding-left: 2px;
				font-weight: $text-weight-medium;
			}
		}
		.overdue {
			color: $alertWarningColor;
			background: $alertWarningBackground;
		}
		.safe {
			color: $alertSuccessColor;
			background: $alertSuccessBackground;
		}
	}
}
