.date-time-field-icon {
	position: relative;
	height: 24px;
	min-width: 24px;
	background: $background-color;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 4px;
	&::before {
		content: "";
		position: absolute;
		left: -5px;
		border-left: 1px solid $border-light-color;
		height: 100%;
	}
}

.date-field, .date-range-field, .date-time-split-field {
	// common wrapper
	.date-picker-wrapper {
		display: flex;
		align-items: center;
		max-width: 350px;

		.btn-no-bg {
			padding: 4px;
			margin: 0 0 0 4px;
			display: flex;
			align-items: center;
			width: 24px;
			height: 24px;
			&.hide { display: none !important; }
			svg { fill: $linkColor; }
		}

		.ember-svg-trigger:not([aria-disabled="true"]) {
			&:focus span, &:active span{
				height: 24px;
				min-width: 24px;
				border: 1px solid $linkColor;
				box-shadow: 0 0 0 1px $linkColor;
			}
		}

		.ember-svg-trigger span {
			@extend .date-time-field-icon;
		}
	}

	// type specific wrapper
	.date-field-wrapper, .date-range-field-wrapper, .date-time-split-field-wrapper {
		width: 100%;
		position: relative;
		.date-text-field, .date-range-text-field {
			padding: 4px 40px 4px 8px;
			width: 100%;
		}
		.ember-basic-dropdown {
			position: unset;
		}
	}

	// date picket inputs
	.date-text-field, .date-range-text-field {
		background-color: $white !important;
		cursor: default !important;

		&[disabled] {
			cursor: not-allowed !important;
			background-color: $inputDisabledBackground !important;
		}
	}

	.date-control, .date-time-control {
		.ember-basic-dropdown-content {
			top: 36px;
		}
	}

	// basic bropdown + power calendar
	.date-control, .date-range-control, .date-time-control {
		float: right;
		margin-top: -28px;
		margin-right: 4px;

		.ember-basic-dropdown-trigger {
			display: flex;
			align-items: center;
			width: 24px;
			height: 24px;
			cursor: pointer;
		}

		.ember-basic-dropdown-content {
			padding: 16px;
			left: 0;
			right: unset;
		}

		.ember-power-select-clear-btn {
			right: 4px;
			top: 8px;
			background: none;
			border: none;
		}
	}

	.ember-power-calendar-navigation {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.ember-power-calendar-days {
		// weekdays
		.ember-power-calendar-weekday {
			font-size: $title-small;
			color: $primary-text-color-1;
			margin: 8px 8px 8px 0;
			min-width: 16px;
			font-weight: $text-weight-normal;
		}

		// days
		.ember-power-calendar-week {
			.ember-power-calendar-day {
				font-size: $title-small;
				font-family: inherit;
				width: 24px;
				height: 24px;
				margin-bottom: 8px;
				box-sizing: border-box;
				border-radius: 4px;
				border: 1px solid transparent;
				color: $primary-text-color-1;
				margin-right: 8px;
				display: flex;
				align-items: center;

				&:not([disabled]) {
					cursor: pointer;
				}

				&[disabled] {
					cursor: not-allowed;
					pointer-events: none;
				}

				&:hover {
					background-color: $background-color;
				}

				&--other-month {
					color: $disabled-text-color;
				}

				&--today {
					border: 1px solid $border-dark-color;
				}

				&--selected:not(:hover) {
					border: 1px solid $linkColor;
					background: $linkColor;
					color: $white;
					font-weight: $text-weight-normal;
				}

				&--selected:not([disabled]):hover {
					font-weight: $text-weight-normal;
				}

				&--focused:not(:hover) {
					border: 1px solid $linkColor !important;
					box-shadow: 0 0 0 1px $linkColor;
					color: $linkColor;
					background: none;
					border-radius: 4px;
				}
			}
		}
	}

	// month/year dropdowns
	.ember-power-calendar-month-year {
		display: inline-flex;
		.ember-basic-dropdown-content {
			width: 120px !important;
			padding: 0;
			left: unset;
		}

		.ember-power-select-trigger {
			background: $btnBackground;
			display: inline-flex;
			margin: 0;
			&:hover {
				border-color: $border-dark-color;
			}
			&[aria-expanded="true"], &:focus {
				z-index: 1;
			}
		}

		.calendar-dropdown-month .ember-power-select-trigger {
			border-top-right-radius: 0 !important;
			border-bottom-right-radius: 0 !important;
		}

		.calendar-dropdown-year .ember-power-select-trigger {
			border-left-color: transparent;
			border-top-left-radius: 0 !important;
			border-bottom-left-radius: 0 !important;
		}
	}

	// left/right chevron buttons
	.ember-power-calendar-nav-buttons {
		display: inline-flex;
		button {
			outline: none;
			border: 1px solid transparent;
			padding: 0;
			width: 32px;
			height: 32px;
			cursor: pointer;
			border-radius: 4px;
			background: none;
			display: flex;
			align-items: center;
			justify-content: center;

			&:hover {
				background: $white;
				box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.13);
			}

			&:focus {
				border: 1px solid $linkColor;
				box-shadow: 0 0 0 1px $linkColor;
			}

			svg {
				width: 20px;
				height: 20px;
				fill: $primary-text-color-1;
			}

			&:disabled, &[disabled] {
				& svg {
					fill: $disabled-text-color;
				}
			}
		}
	}

	// individual picker overrides

	// date range picker
	.date-picker-container-footer, .date-range-picker-container-footer {
		border-top: 1px solid $border-light-color;
		background: $background-color-2;
		width: calc(100% + 48px);
		padding: 8px;
		margin: 8px 0 0 -24px;
		display: flex;
		justify-content: flex-end;
		.btn-reset {
			margin-right: auto;
			font-weight: $text-weight-semi-bold;
		}
		.btn-primary {
			margin-right: 0;
			background: $btnPrimaryActiveBackground;
		}
	}

	.date-picker-container-footer {
		width: calc(100% + 32px);
		margin: 8px 0 0 -16px;
	}

	.date-range-field-wrapper .date-range-picker-container {
		display: flex;

		.ember-power-calendar-navigation {
			justify-content: center;
			margin-bottom: 8px;

			.ember-power-calendar-month-year-label {
				display: inline-block;
				margin-bottom: 4px;
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
				padding-left: 4px;
			}

			&.ember-power-calendar-left-navigation {
				.ember-power-calendar-nav-buttons {
					position: absolute;
					left: -8px;
					top: 22px;
				}
			}

			&.ember-power-calendar-right-navigation {
				.ember-power-calendar-nav-buttons {
					position: absolute;
					right: -8px;
					top: 22px;
				}
			}
		}

		.date-range-picker-right {
			padding-left: 24px;
		}

		.date-range-picker-left {
			position: relative;
			padding-right: 16px;
			&:after {
				position: absolute;
				content: "";
				height: 70%;
				width: 1px;
				right: 0;
				background: $border-light-color;
				top: 75px;
			}
		}

		.range-highlighter {
			content: "";
			position: absolute;
			top: -1px;
			width: 12px;
			background: $section-background-blue;
			height: 24px;
			z-index: -1;
		}

		.ember-power-calendar-day {
			&--selected, &--hovered {
				border-radius: 0;
				border: 1px solid $section-background-blue !important;
				background: $section-background-blue !important;
				color: $primary-text-color-1 !important;
				position: relative;
				&::after {
					@extend .range-highlighter;
					right: -12px;
				}
			}

			&--hover:hover {
				position: relative;
				&::after {
					@extend .range-highlighter;
					right: -12px;
				}
			}

			&[data-is-start-of-week="true"] {
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px;
			}

			&[data-is-end-of-week="true"] {
				border-top-right-radius: 4px;
				border-bottom-right-radius: 4px;
				&::after {
					visibility: hidden;
				}
			}

			&--range-start, &--range-end {
				border: 1px solid $linkColor !important;
				background: $linkColor !important;
				color: $white !important;
				border-radius: 4px;
				&::after {
					display: none;
				}
			}
		}

		.ember-power-calendar-row {
			display: flex;
			justify-content: initial;

			$missing-days: 1, 2, 3, 4, 5, 6, 7;
			@each $missing in $missing-days {
				&[data-missing-days="#{$missing}"]:first-child {
					padding-left: #{32 * $missing}px;
					button {
						border-top-left-radius: 4px;
						border-bottom-left-radius: 4px;
					}
				}
				&[data-missing-days="#{$missing}"]:last-child {
					padding-right: #{32 * $missing}px;
					button::after {
						border-top-right-radius: 4px;
						border-bottom-right-radius: 4px;
					}
				}
			}

			td[data-is-range-start="true"] + td[aria-selected="true"] button::before,
			td[data-is-range-start="true"] + td[data-hovered="true"] button::before {
				@extend .range-highlighter;
				left: -12px;
			}
		}
	}

	// date time split
	.date-time-split-field-wrapper {
		display: flex;
		align-items: center;
	}
}

.time-picker-wrapper {
	display: flex;
	align-items: center;
	position: relative;
	margin-left: 8px;

	.time-control {
		width: 112px;
		.ember-power-select-trigger {
			width: 110px;
			padding-right: 0;
			input {
				padding: 9px 0 8px 8px !important;
				font-size: $text-normal;
			}
		}
		.ember-power-select-status-icon {
			display: none;
		}
		.ember-basic-dropdown-content {
			width: 130px !important;
		}
		.ember-power-select-selected-item, .ember-power-select-option {
			font-weight: $text-weight-medium;
			font-size: 14px;
		}
	}
	.time-picker-icon {
		@extend .date-time-field-icon;
		position: absolute;
		right: 6px;
	}
}

.date-range-control {
	.date-range-dropdown-content {
		padding: 16px 24px 0 !important;
	}
	.ember-power-calendar-days {
		min-height: 230px;
	}
}

// new version of date pickers
.date-time-picker-wrapper-utc {
	display: flex;
	align-items: center;
	.time-control {
		.ember-power-select-selected-item {
			margin-top: 4px;
			font-size: $text-normal !important;
			font-family: inherit;
			font-weight: $text-weight-semi-bold !important;
		}
		.ember-power-select-placeholder {
			font-size: $text-normal !important;
			font-weight: $text-weight-semi-bold !important;
			margin-top: 3px;
		}
	}
}

.date-time-picker-wrapper-utc, .date-picker-wrapper-utc {
	.date-control .ember-basic-dropdown-content{
		padding-bottom: 0;
	}
}

.date-time-picker-wrapper-utc, .date-picker-wrapper-utc, .date-range-field-wrapper {
	.date-text-field, .date-range-text-field {
		cursor: pointer !important;
		font-size: $text-normal;
		font-family: inherit;
		font-weight: $text-weight-semi-bold;
		background: none !important;
		&.focus {
			box-shadow: 0 0 0 1px $linkColor;
			border: 1px solid $linkColor !important;
		}
	}
}

.time-picker-wrapper-utc {
	.ember-power-select-clear-btn { display: none; }

	.btn-no-bg {
		padding: 4px;
		margin: 0 0 0 4px;
		svg { fill: $linkColor; }
	}
}

// when date range picket is in sidebar
// to give an indentation and a border
.module-view-query-form {
	.date-range-field {
		margin-left: 16px !important;
		position: relative;
		.form-field--label {
			display: inline-block !important;
		}
		&::before {
			position: absolute;
			content: "";
			border-left: 1px dotted $disabled-text-color;
			left: -10px;
			height: 100%;
		}
	}
}

// positioning changes
.date-range-field .repositionfor-sidebar {
	position: fixed !important;
	right: 24px !important;
	left: unset !important;
}

html[dir="rtl"] {
	.date-range-field .repositionfor-sidebar {
		position: fixed !important;
		left: 24px !important;
		right: unset !important;
	}
}

// when the date picker is inside the page sidebar
.page-sidebar {
	.date-time-split-field .date-time-picker-wrapper { max-width: 150px; }
	.date-field .date-picker-wrapper, .date-range-field .date-picker-wrapper { max-width: 300px; }
}

// when the date picker is inside the modal or page content
.page-content, .ember-modal-dialog {
	.date-range-field .date-picker-wrapper { max-width: 235px; }
	.date-time-split-field .date-time-picker-wrapper, .date-field .date-picker-wrapper { max-width: 200px; }
}
