.feedback-widget-wrap {
	position: absolute;
	-moz-box-shadow: -4px 0 24px 0 rgba(20,28,48,0.24);
	-webkit-box-shadow: -4px 0 24px 0 rgba(20,28,48,0.24);
	box-shadow: -4px 0 24px 0 rgba(20,28,48,0.24);
	z-index: 999;
	.feedback-widget {
		width: 484px;
		background-color: $app-white;
		display: inline-block;
		.feedback-header {
			display: flex;
			align-items: center;
			border-radius: 2px;
			background-color: $app-grey-bg;
			border-bottom: solid 1px $primary-border;
			padding: 10px 20px;
			.avatar__icon--small {
				height: 45px;
				width: 45px;
				margin-right: 5px;
				margin-top: 10px;
			}
			h5 {
				font-weight: 600;
				color: $secondary-text-color-1;
				text-align: center;
			}
			.close-feedback-widget {
				position: absolute;
				top: 8px;
				right: 4px;
				width: 30px;
				cursor: pointer;
				svg {
					fill: $app-muted-color;
				}
			}
		}
		.feedback-msg-box {
			position: relative;
			textarea {
				width: 100%;
				height: 150px;
				border: 0;
				resize: none;
				font-weight: $text-weight-semi-bold;
				padding: 15px;
				box-sizing: border-box;
			}
			button.btn-link {
				position: absolute;
				bottom: 6px;
				right: 5px;
				border: none;
				&.active {
					opacity: 1;
					background-color: $app-white !important;
				}
			}
		}
	}
	.feedback-sent {
		display: flex;
		align-items: center;
		background-color: $app-white;
		width: 400px;
		.feedback-success-image {
			margin: 10px 15px;
		}
		.feedback-sent-msg {
			margin-left: 0 !important;
			padding-left: 10px;
			text-align: center;
			h4 {
				color: $secondary-text-color-1;
				font-size: $title-normal;
				font-weight: $text-weight-semi-bold;
			}
			label {
				color: $secondary-text-color-2;
				font-size: $baseFontSize;
				margin: 5px 0 0;
				padding-bottom: 0;
			}
		}
	}
}