// This will be included directly in app.scss outside ember container
$hover-background-color: #EBEFF3;
$active-background-color: #E5F2FD;
$menu-shadow-color1: #12344D0F;
$menu-shadow-color2: #12344D29;
$primary-text-color-1: #12344D;
$menu-link-color: #2C5CC5;
$primary-text-color-1: #12344D;

#header-page-title-container .burger-menu-container {
	display: flex;
	max-height: 100%;

	button.menu-trigger {
		border: 0;
		background: transparent;
		box-shadow: none;
		box-sizing: border-box;
		display: flex;
		align-items: center;
		padding: 4px;
		cursor: pointer;
		// margin-left: 8px;

		.burger-icon {
			width: 32px;
			height: 32px;
			padding: 8px;
			box-sizing: border-box;
			border-radius: 4px;
			margin-right: 4px;
			path { fill: $primary-text-color-1; }
		}

		&:hover .burger-icon {
			background: $hover-background-color;
		}

		.menu-trigger-text { margin-left: 0; }
	}

	.menu-list-container {
		transition: 0.2s ease-in-out;
		opacity: 0;
		width: 280px;
		position: fixed;
		top: 0;
		// left: 64px;
		// padding: 12px;
		left: 66px;
		height: 100vh;
		padding: 8px;
		z-index: 999;
		overflow-y: auto;
		background: #FFFFFF;
		box-shadow: 0 2px 4px 0 $menu-shadow-color1, 0 2px 18px 0 $menu-shadow-color2;

		&.slide-from-left {
			transform: translateX(-280px);
			visibility: hidden;
		}

		// [role="menu-item"] {
		.menuitem {
			display: block;
			padding: 12px;
			margin: 4px 0;
			box-sizing: border-box;
			color: $primary-text-color-1;
			border-radius: 4px;
			white-space: normal;
			font-size: 14px;
			font-weight: 500;

			&.active {
				background: $active-background-color;
				color: $menu-link-color;
				padding-right: 26px;
				position: relative;

				svg.icon-selected {
					position: absolute;
					right: 8px;
					top: 50%;
					transform: translateY(-50%);
					fill: $menu-link-color;
					width: 16px;
				}
			}

			&:hover {
				background: $hover-background-color;
			}

			&:focus {
				outline: none;
				box-shadow: 0 0 1px 2px $menu-link-color;
			}
		}
	}

	&.menu-open {
		.slide-from-left {
			visibility: visible;
			animation: 0.2s ease-in-out slide-from-left forwards;
		}
	}

	&.menu-closed {
		.slide-from-left {
			animation: 0.2s ease-in-out close-to-left forwards;
		}
	}
}

.expanded #header-page-title-container .burger-menu-container {
	.menu-list-container {
		left: 248px;
	}
}

@keyframes slide-from-left {
	0% {
		transform: translateX(-280px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}

@keyframes close-to-left {
	0% {
		transform: translateX(0);
		opacity: 1;
	}
	99% {
		transform: translateX(-280px);
		opacity: 0;
		visibility: visible;
	}
	100% {
		visibility: hidden;
	}
}
