.view-more-component{
	.view-more-toggle{
		padding-left: 0;
		border: none;
		margin-top: 10px;
	}

	.view-more-body{
		table{
			word-break: normal;
			th, td{
				border: 1px solid $inputBorder;
				border-collapse: collapse;
			}
		}
		img{
			border: none;
			max-width: 100%;
			height: auto;
		}
		ol, ul{
			margin: 8px 5px 10px;
			list-style-position: none;
			padding: 0 0 0 40px;
		}
		ul li{
			list-style-type: disc;
		}
		#alert_incident_info{
			table {
				border-collapse: collapse;
				margin-top: 12px;

				td {
					border: none;
					padding: 6px 6px 6px 0px;
					font-weight: $text-weight-medium;

					.additional-info {
						.alert-incident-info-key {
							max-width: 320px;
						}
					}

					&.alert-incident-info-key {
						min-width: 120px;
						font-size: $text-normal;
						color: $secondary-text-color-1;
						word-wrap: break-word;
					}
					&.alert-incident-info-value {
						font-size: $baseFontSize;
						color: $primary-text-color-1;
						word-wrap: break-word;
					}
				}
			}
		}
	}
}
