// Variables
$regular-border: 1px solid $app-border-primary;
$table-header-row-height: 40px;
$table-body-row-height: 56px;

.table-wrap {
	// position: relative;
	margin-top: 16px;

	// Bottom border radius correction for single page case,
	// since bottom pagination will be hidden.
	&.single-page {
		.ember-light-table {
			border-radius: 4px !important;
		}

		.lt-body-wrap {
			border-radius: 4px !important;
		}
	}

	// Applies when the first column is not a check box column
	// This gets added by the 'noRowSelectable = true'
	&.no-row-selection {
		.ember-light-table {
			.lt-body-wrap {
				thead.lt-head th.lt-column:first-child {
					padding-right: 16px !important;
				}

				thead.lt-head th:first-child {
					border-right: $regular-border !important;
				}

				tbody.lt-body tr.lt-row td.lt-cell:first-child {
					padding-right: 16px !important;
				}
			}
		}
	}

	////////// .ember-light-table TO BE DEPRECATED ///////////
	.ember-light-table {
		border: $regular-border;
		border-radius: 4px 4px 0 0;
		box-shadow: none;
		background: $app-white;
		border-collapse: collapse;
		word-break: break-word;
		overflow: visible;
		width: 100% !important;
		position: relative;

		// Table header row
		@mixin header-row() {
			thead.lt-head {
				background-color: $background-color-2;
				border-top-left-radius: 4px;
				border-top-right-radius: 4px;
				z-index: 999;

				// Each header column
				th.lt-column {
					border-bottom: $regular-border;
					font-size: 14px;
					font-weight: 600;
					line-height: 22px;
					height: 48px;
					padding: 0 16px;
					text-align: left !important;
					text-transform: capitalize;

					&:first-child {
						// checkbox column
						padding-right: 0;
					}

					.lt-sort-icon {
						width: 15px;
					}

					.custom-lt-sort-icon {
						padding-right: 0;
						padding-left: initial;
						margin-right: -8px;
						margin-left: initial;

						svg {
							width: 16px !important;
							height: 16px !important;
							margin-bottom: -3px;
						}
					}
				}

				// Header column separator vertical line
				th {
					&:not(:first-child):not(:last-child) {
						border-right: $regular-border;
						border-left: none;
					}
				}
			} // --> .lt-head
		}

		.lt-head-wrap {
			border-radius: 4px 4px 0 0;
			@include header-row();
		}

		.lt-body-wrap {
			min-height: 96px; // 1 row
			display: block;

			/* removed it because scrolling table in edge has issues */
			/*border-radius: 4px 4px 0 0;*/

			// Fixed header case
			.lt-scrollable {
				overflow: auto;
			}

			// Table header row
			@include header-row();

			// Table body contents
			tbody.lt-body {
				// Each row
				tr.lt-row {
					height: 48px;

					td.lt-cell {
						color: $primary-text-color-1;
						height: 48px;
						font-size: 14px;
						padding: 0 16px;
						border-right: 1px solid transparent;
						vertical-align: middle;

						&:first-child {
							// checkbox column
							padding-right: 0;
							padding-left: 16px;

							input {
								margin-bottom: 2px;
							}
						}

						.table-checkbox-label {
							position: absolute;
							left: -999px;
							visibility: hidden;
						}

						.data-table-link {
							color: $app-grey;
							cursor: pointer;
							font-weight: 600;

							&:hover {
								color: $linkColor;
							}
						}
					}

					&:not(:last-child) {
						border-bottom: 1px solid $border-light-color;
					}

					&.lt-no-data {
						&:hover {
							background-color: transparent !important;
						}

						td {
							padding: 16px;
							text-align: center;
						}
					}

					&.is-selected {
						background-color: transparent;
					}

					&.selected {
						background-color: $dropdown-option-selected !important;
						border-bottom-color: transparent;
					}

					&:not(.is-selected):not(.selected) {
						background-color: $app-white;

						&:hover {
							background-color: $background-color-2;
						}
					}

					&:last-of-type {
						td {
							border-bottom-width: 0;
						}
					}
				}
			} // --> .lt-body

			.lt-infinity {
				min-height: 0;
			}

			// Table footer row
			tfoot {
				.lt-column {
					border-top: $regular-border;
				}

				tr > td {
					border-top: 1px solid $app-border-primary;
					padding: 10px 10px 0 10px;
					font-size: 15px;
				}
			} // --> tfoot
		} // --> .lt-body-wrap
	}
	////////// --> .ember-light-table TO BE DEPRECATED //////////

	.column-customizer {
		// to be moved out into common light-table specific styles
		box-sizing: content-box;
		border-radius: 0;
		position: absolute;
		right: -10px;
		left: auto;
		z-index: 99;
		top: 0; //104px;
		border: none;
		box-shadow: none;

		.ember-basic-dropdown {
			.ember-basic-dropdown-trigger {
				.btn {
					height: 50px;
					width: 48px;
					border-radius: 0;
					padding: 8px 0;
					background: #fff;
					box-shadow: none;
					@include flex-center;
					svg {
						height: 21px;
						width: 21px;
					}
				}
			}

			.ember-basic-dropdown-content {
				max-height: 415px;
				overflow-y: auto;
				width: 270px;
				border: $regular-border;
				padding: 16px !important;

				&--right {
					right: 8px;
					left: auto;
				}

				&:not(.ember-power-select-dropdown) {
					border-radius: 2px 0 2px 2px;
					margin-top: 0 !important;

					ul {
						li {
							@include flexbox;
						}
					}
				}

				ul {
					overflow-y: auto;

					li {
						@include flexbox-simple(flex-start, center);
						padding: 8px;
						cursor: pointer;

						.col-label {
							padding-left: 8px;
						}

						&:hover {
							background-color: $background-color-2;
							border-radius: 0 !important;
						}

						&.no-disable {
							&:hover {
								background-color: transparent;
							}

							.col-label {
								color: $secondary-text-color-2;
							}
						}

						input[type="checkbox"][disabled] {
							& + .col-label {
								cursor: not-allowed;
							}

							&:checked:after {
								border-right: 2px solid $disabled-text-color;
								border-bottom: 2px solid $disabled-text-color;
							}
						}
					}
				}

				.btns-wrapper {
					@include flexbox;
					@include flex-direction(row-reverse);
					margin: 8px -16px 0 -16px;
					padding: 16px 8px 0 8px;
					border-top: $regular-border;

					&:hover {
						background-color: transparent;
					}

					.btn {
						&-primary {
							margin-right: 0;
						}
					}
				}
			}
		}
	} // end of .column-customizer styles
}
