.requester-card-wrapper {
	&:focus {
		outline: none;
		box-shadow: 0 0 0 2px $inputFocusBorder;
		border-radius: 4px;
	}

	.ember-tooltip-show {
		transition: opacity .5s ease-in-out;
		word-break: break-word;
		max-width: 340px;
	}

	.requester-card-info {
		.ember-tooltip-show {
			margin: 10px !important;
		}
	}


	.ember-popover {
		position: fixed !important;
		border: none;
	}

	.requester-cell {
		display: flex;
		align-items: center;

		.avatar__initial {
			width: 24px;
			height: 24px;
			font-size: $title-normal;
		}
		.avatar__icon {
			min-width: 24px;
		}
		.requester-cell-name {
			color: $primary-text-color-1;
			padding: 0 5px 0 8px;
		}
		span {
			width: 18px;
			height: 18px;
		}
	}
	.ellipsis-cell-wrap {
		max-width: 75%;
		display: inline-block;
	}
	.ellipsis-info-wrap {
		width: 200px;
		display: inline-block;
	}

	.requester-card-content{
		width: 340px;
		min-height: 160px;
		max-height: 232px;
		padding: 0 !important;
		box-shadow: 0px 4px 16px rgba(0, 0, 0, 0.08);
		border-radius: 8px;
		z-index: 1001;

		.ember-popover-arrow {
			display: none;
		}

		.requester-card-info {
			padding: 18px 10px 18px 20px;
			background: linear-gradient(96.25deg, $primary-text-color-1 16.4%, $tab-icon-color 117.8%);
			border-top-left-radius: 8px;
			border-top-right-radius: 8px;
			display: flex;
			align-items: center;
			height: 76px;

			.avatar-info {
				.avatar__icon {
					width: 40px;
					height: 40px;
				}
			}

			.name-info {
				margin-left: 7px;

				a {
					font-weight: $text-weight-semi-bold;
					color: $white;
					text-transform: capitalize;

					&:hover {
						text-decoration: underline;
					}
				}

				.job-info {
					font-size: $text-normal;
					color: $white;
				}
			}
			.badge_info {
				margin-left: auto;
				position: relative;
				bottom: 5px;
			}
		}

		.requester-card-details {
			padding: 16px 10px 0 20px;

			.requester-details-align {
				font-size: $text-normal;
				height: 20px;
				margin-bottom: 10px;

				.recent_tickets, .copy-btn{
					&:focus {
						outline: none;
						box-shadow: 0 0 0 2px $inputFocusBorder;
						border-radius: 4px;
					}
				}

				.requester-details-icon {
					display: inline-block;
					vertical-align: middle;
					position: relative;
					top: 3px;
				}

				.requester-details-text {
					padding-left: 3px;
					vertical-align: middle;

					.ellipsis-mail-wrap {
						max-width: 250px;
						display: inline-block;
						vertical-align: middle;
					}
				}

				button {
					border: none;
					box-shadow: none;
					min-width: 12px !important;
					background: $white;
					height: 20px;

					svg {
						fill: $inputFocusBorder;
						width: 12px;
						height: 12px;
					}
				}
				a {
					svg {
						fill: $disabled-text-color;
					}
				}
			}

		}

	}
}
