.salesforce-requester-details {
	width: 100%; 
	height: 100%; 
	padding:16px; 
	border: 1px solid $border-light-color;
	border-radius: 8px;
	overflow: scroll;
	max-height: 392px;

	.truncate {
		max-width: 92%;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	
	&__title {
		font-size: 14px;
		font-weight: 600;
		display: flex;

		&-icon {
			width: 12px;
			fill: $linkColor;
			margin-left: 4px;
		}
	}
	&__subtitle {
		font-size: 12px; 
		color: $secondary-text-color-1;
		padding-top: 4px;
	}

	&__row {
		font-size: 12px; 
		display: flex;
		padding-top: 4px;

		&-key {
			font-weight: 400;
			display: table; // it behave as table column for proper layout
			&::after {
				content: " : ";
			}
		}
		&-value {
			color: $app-grey; 
			font-weight: 600;
			padding-left: 4px;
		}
	}

	&__view-more {
		color: $app-primary;
		font-weight: 500;
		padding: 8px 0 0 0;
		font-size: 12px;
		border: none;
		background: none;
		cursor: pointer;
	}

	&__divider:not(:last-child) {
		border-top: 1px solid $border-light-color;
		margin: 8px 0 12px 0;
	}

	.view-more-toggle {
		margin-top: 8px;
		font-size: 12px;
		text-transform: lowercase;
		&:first-letter{
			text-transform: capitalize
		}
	}	
}