.tickets-subject-container {
	display: flex;
	width: 100%;
	.module-icon {
		@include flex-center;
		@include priority-colors('.priority-low', '.priority-medium', '.priority-high', '.priority-urgent');
		background: $app-green;
		width: 48px;
		height: 48px;
		position: relative;
		border-radius: 4px;
		svg {
			width: 20px;
			height: 20px;
			fill: $white;
			path {
				fill: $white;
			}
		}
		&.disabled {
			background: $secondary-text-color-2;
		}
	}

	.summary-content {
		width: 96%;
		margin-bottom: 8px;
		margin-left: 15px !important;

		.subject-text {
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
			color: $primary-text-color-1;
			display: inline;
			margin-right: 10px;
		}
		.requester-created-at,
		.requested-for {
			padding-top: 8px;
		}
		svg.vip-badge {
			vertical-align: middle;
			width: 16px;
			height: 16px;
		}
		.requester-name,
		.requester-created-on-detail {
			font-size: $baseFontSize;
			font-weight: $text-weight-normal;
			vertical-align: middle;
			display: inline-block;
		}
		.requester-name {
			font-weight: $text-weight-medium;
		}
		.requester-created-on-detail {
			color: $secondary-text-color-1;
		}
		.source-name {
			@extend .ellipsis;
			display: inline-block;
			vertical-align: middle;
			max-width: 140px;
		}
		.source-icon {
			vertical-align: middle;
			margin-left: 3px;
		}

		.tickets-source {
			display: inline-block;
			&.mail-box,
			&.globe,
			&.phone-mobile,
			&.chat,
			&.feedback-widget,
			&.aws-cloudwatch,
			&.chatbot,
			&.workplace,
			&.eob,
			&.alerts,
			&.custom-source {
				path {
					fill: $secondary-text-color-1 !important;
				}
			}
		}

		.requested-for {
			color: $secondary-text-color-1;
			@include flexbox;
			@include align-items(center);
			margin-left: 0 !important;
			width: 100%;
			svg.vip-badge {
				margin-right: 8px;
			}
		}

		.requester-name,
		.requested-for-name {
			&.link {
				&:hover {
					color: $linkColorHover;
				}
			}
		}

		.requested-for-label,
		.requested-for-name {
			vertical-align: middle;
			display: inline-block;
		}

		.requested-for-label {
			max-width: 20%;
			margin-right: 8px;
		}

		.requested-for-name {
			width: 100%;
		}

		.requester-created-at {
			[data-test-id="requester-vip-badge"] { vertical-align: top; }
			.shimmer-loader {
				position: absolute;
				.shimmer-loader-item {
					margin: 5px;
				}
			}
		}

		.deleted-entity {
			color: $alertWarningColor;
			background-color: $alertWarningBackground !important;
			border-color: $alertWarningBorder !important;
		}

		.spam-entity {
			color: $alertCancelColor;
			background: $background-color;
		}

		.restore-btn {
			background: $app-red !important;
			svg {
				height: 18px;
				fill: $white;
			}
			&.disabled {
				background: none !important;
				svg {
					fill: $secondary-text-color-1;
				}
			}
		}

		.unspam-btn {
			background: $app-border-primary !important;
			svg {
				height: 18px;
				fill: $secondary-text-color-1;
			}
			&.disabled {
				background: none !important;
				svg {
					fill: $app-border-primary;
				}
			}
		}

		.restore-tooltip,
		.unspam-tooltip {
			width: max-content;
			z-index: 1000;
		}

		.requester-card-wrapper {
			display: inline-block;
			max-width: 98%;
			margin-left: 0 !important;
			vertical-align: middle;

			.requester-name { white-space: break-spaces; }

			.requester-card-content{
				z-index: 500;

				.requester-card-info {
					.name-info {
						margin-left: 10px !important;
					}
				}

				.requester-card-details {
					.recent_tickets, .copy-btn{
						.ember-svg {
							margin-left: 4px;
							vertical-align: middle;
						}
					}
				}
			}
		}
	}
}

.tcpr-emails-list-wrapper {
	font-size: $baseFontSize;
	font-weight: $text-weight-normal;
	color: $secondary-text-color-1;
	vertical-align: middle;
	display: flex;
	margin: 0px 20px 8px 91px !important;
	width: 70%;
	.tcpr-email-label {
		font-weight: $text-weight-medium;
	}
	.tcpr-email-list {
		padding-left: 5px;
		width: 90%;
	}
}

.tcpr-email-ids-tooltip{
	word-break: break-word;
	max-width: 350px;
	background: $app-light-bg !important;
	color: $primary-text-color-1 !important;
	padding: 12px 16px;
	box-shadow: 0px 2px 4px rgba(18, 52, 77, 0.16), 0px 2px 16px rgba(18, 52, 77, 0.06);
	.tcpr-email-tooltip{
		.email-recipient-type{
			white-space: nowrap;
			padding-right: 6px;
			text-align: right;
		}
		tr:not(:last-child){
			.email-list{
				padding-bottom: 8px;
			}
		}
	}
}

.tickets-details-description-wrapper {
	background: $section-background-blue;
	padding: 20px;
	border: 1px solid $border-light-blue;
	border-radius: 8px;
	color: $app-grey;
	margin-bottom: 12px;
	@include flex-direction(column);
	box-shadow: 0px 2px 0px $border-light-blue;

	.description-label {
		font-weight: $text-weight-semi-bold;
		font-size: $title-normal;
		color: $primary-text-color-1;
	}

	.view-more-toggle {
		font-weight: $text-weight-semi-bold;
		padding-left: 0;
	}

	.no-description {
		font-size: $text-normal;
		color: $secondary-text-color-2;
	}

	.eod-wrapper {
		margin: 40px 0 0;
		.eod-label {
			font-weight: $text-weight-medium;
			font-size: $text-normal;
		}
		.eod-field-list {
			width: 100%;
			.eod-field {
				width: 50%;
				border-left: 1px solid $border-light-blue;
				padding: 8px 16px;
				float: left;
				.eod-field-label {
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;
					color: $secondary-text-color-1;
					@extend .ellipsis;
				}
				.eod-field-value {
					font-weight: $text-weight-medium;
					font-size: $title-small;
					color: $primary-text-color-1;
					@extend .ellipsis;
					max-width: 90%;
				}
			}
		}
	}

	.phone-call-wrapper {
		padding-top: 16px;
		.call-duration {
			padding-top: 16px;
		}
		.audio-wrapper  {
			margin-bottom: 0px
		}
	}

	.attachment-container {
		padding: 0;
		margin: 16px 0 0 0;
		background: none;
		.desc-attachment-list .attach-files{
			padding: 0;
			margin: 0;
			background: none;
			.attachment-title {
				display: none;
			}
		}

		.attachment-list {
			@include flexbox;
			flex-wrap: wrap;
			@include flex-direction(row);
			.list-file-item {
				height: 72px;
				max-height: 72px;
				padding: 16px;
				display: flex;
				width: 40%;
				margin: 8px 8px 0 0;

				div svg.file-icon{
					width: 24px;
					height: 24px;
				}
				.delete-file{
					margin: 0;
					padding: 0 0 0 8px;
				}
				.file-info{
					margin: 0 0 0 10px;
					width: 80%;
					align-self: flex-start;
					.file {
						max-width: 100%;
						top: 0;
						cursor: pointer;
						.file-name{
							font-weight: $text-weight-medium;
						}
						&:not(.local) {
							top: -2px;
						}
					}
					.file-size {
						display: block;
						top: -10px;
					}
					.file-provider-type {
						display: block;
						margin: 0 !important;
					}
				}
				&.cloud-file .file .file-name {
					max-width: 95%;
				}
			}
			.max-size-file-list{
				margin: 0;
			}
		}
	}

	.attachment-title {
		color: $app-grey;
		font-size: $text-normal;
		font-weight: $text-weight-medium;
		@include flexbox;
		@include align-items(center);
		margin-bottom: 4px;
		span{
			@include flexbox;
		}
		.ember-tooltip{
			max-width: 210px;
		}
	}

	.attachment-icon {
		fill: $app-primary;
		width: 12px;
		height: auto;
		margin-left: 5px;
	}

	.ticket-eod + .attachment-container {
		margin-top: 40px;
	}
}

.multiline-truncate {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 4;
	overflow: hidden;
}
