.add-new-requester-modal {
	.ember-modal-header {
		font-weight: $text-weight-semi-bold;
	}
	.ember-modal-body {
		.ember-alert {
			margin-bottom: 10px;
			padding: 7px;
			height: auto;
			svg {
				margin-right: 7px;
			}
			span {
				border-left: 1px solid;
				padding-left: 10px;
			}
			&.ember-alert-warning {
				svg {
					fill: $app-red;
				}
				span {
					border-left-color: $alertPartialBorder;
				}
			}
			&.ember-alert-info {
				svg {
					fill: $secondary-text-color-1;
				}
				span {
					border-left-color: $border-light-blue;
				}
			}
		}
		.fieldset-wrapper {
			.form-field--has-errors input[aria-invalid="true"] {
				border-color: $app-red !important;
			}
			input[aria-invalid="false"] {
				border-color: $primary-border !important;
			}
			.department-field {
				.ember-power-select-trigger[aria-disabled="true"] {
					background-color: $formActionsBackground;
				}
				.ember-power-select-status-icon {
					left: calc(100% - 16px);
				}
				.ember-power-select-selected-item {
					margin-left: 8px;
					& + .ember-power-select-status-icon {
						left: 0 !important;
					}
				}
				.ember-basic-dropdown-content-wormhole-origin {
					.ember-basic-dropdown-content, .ember-power-select-search .ember-power-select-search-input{
						width: 100%;
					}
					.ember-power-select-search .search-container {
						top: 17px;
					}
				}
			}
		}
	}
	.ember-modal-footer {
		.btn-primary {
			background: $btnPrimaryBackground;
			&[disabled] {
				background: $btnPrimaryBackground;
			}
		}
	}
}

.add-requester-btn-wrapper {
	background: $white;
	margin: -6px -8px;
	padding-bottom: 6px;

	.add-req-info {
		margin-left: 8px;
		padding: 8px 0 6px 0;
		color: $app-form-label-color;
	}
	#add-new-requester-btn {
		@include flexbox-simple(flex-start, center);
		background-color: $dropdown-hover !important;
		padding: 8px 16px;
		width: 100%;
		border-radius: 4px;
		border-width: 0;
		font-weight: $text-weight-medium;
		svg {
			margin-right: 8px;
		}
	}
}
