.br-field {
	&[aria-hidden] {
		display: none;
	}

	&[aria-disabled] {
		cursor: not-allowed;
		opacity: 0.6;
		& > * {
			pointer-events: none;
		}
		&.attach-files{
			opacity: 1;
			.attachment-uploader{
				opacity: 0.6;
			}
		}
		.attachment-list-container {
			pointer-events: initial;
			.delete-file{
				display: none;
			}
		}
	}

	&[aria-disabled],
	&:not([aria-required]) {
		.star, .field-required-error {
			display: none;
		}
	}
	&[aria-required] {
		position: relative;

		.field-required-error {
			display: block;
			font-size: $text-normal;
			color: $app-red-1;
			text-align: left;
		}
	}
}

// to retain atleast one asset in listview incase of mandatory rule
.br-mandate-assets li.asset-details:only-child,
.br-disable-assets{
	.remove-asset{
		pointer-events: none;
		cursor: not-allowed;
		opacity: 0.5;
	}
}

.ticket-property-form {
	div.inline-split[hidden] { // to handle default fields
		display: none;
	}
}

// prevent delete action to retain alteast one file in show page
.pane-content{
	.mandate-attachment .list-file-item:only-child .delete-file{ 
		display: none;
	}
}

// Temp fix due to MultitabNav not getting reacted to tabs array
.hideAssetTabforBR #tab_assets{
	display: none;
}
// To hide level 2 fields, when parent level is hidden
.dependent-select-dropdown-field[hidden]{
	+ .ember-view:not(.form-field){
		display: none;
	}
}
