.formfield-multiselect-input {
	@include flexbox();
	float: left;

	.ui-dropdown-grouped .ember-basic-dropdown-content {
		margin-top: 6px;
	}
}

.ui-dropdown-grouped.ember-basic-dropdown {
	min-width: 80px;

	.ember-power-select-dropdown {
		padding: 16px;
		z-index: 6;

		.dd-loading-block {
			@include flexbox-simple(flex-start, center);
			svg {
				fill: $app-primary;
				height: 28px;
				width: 28px;
			}
		}
		.ember-power-select-options {
			&.parent {
				max-height: none;
				overflow: visible;
			}
			.ember-power-select-group {
				&[data-group-index='0'] {
					.dd-group-title {
						margin-top: 4px;
					}
				}

				.dd-group-title {
					@include flexbox-simple(flex-start, center);
					margin: 16px 0px 8px;

					svg {
						width: 14px;
						height: 14px;
					}

					.group-title {
						color: $primary-text-color-1;
						font-weight: bold;
						@include font-size($text-normal);
						margin-left: 6px;
					}
				}

				.group-options {
					padding: 6px 0px;
					border: 1px solid $dropdown-hover;
					box-sizing: border-box;
					border-radius: 4px;

					.ember-power-select-option {
						margin: 0px 8px;
					}
					.no-result {
						padding-left: 12px;
					}
				}

				.group-loadmore {
					margin: 8px 0px 0px 8px;
					font-weight: $text-weight-semi-bold;
					color: $linkColor;

					&:hover {
						color: $linkColorHover;
					}
				}
			}
		}

		.dd-grouped-bottom {
			color: $disabled-text-color;
			margin-top: 8px;
		}
	}
}
