.ws-logo-name-wrapper {
	display: flex;
	align-items: center;
	.ws-icon {
		width: 42px;
		height: 42px;
	}
}
.ws-logo {
	background: $dark-blue;
	border-radius: 8px;
	padding: 12px;
	color: $white;
	font-weight: $text-weight-semi-bold;
	text-align: center;
}
.ws-bg-1 {
	background-color: $dark-purple;
}
.ws-bg-2 {
	background-color: $dark-blue;
}
.ws-bg-3 {
	background-color: $dark-green-shade;
}
.ws-bg-4 {
	background-color: $brown;
}
.ws-bg-5 {
	background-color: $dark-orange-shade;
}
.ws-bg-6 {
	background-color: $dark-red;
}
.ws-bg-7 {
	background-color: $dark-brown;
}
.ws-bg-8 {
	background-color: $dark-grey;
}
.ws-bg-9 {
	background-color: $light-blue;
}
